define('tahi/components/decision-radio-button', ['exports', 'ember', 'tahi/pods/components/radio-button/component'], function (exports, Ember, RadioButtonComponent) {

  'use strict';

  var DecisionRadioButtonComponent;

  DecisionRadioButtonComponent = RadioButtonComponent['default'].extend({
    click: function() {
      var decision;
      decision = this.get('value');
      return Ember['default'].run.later(this, (function() {
        return this.sendAction('action', decision);
      }), 150);
    },
    checked: (function() {
      return this.get('value') === this.get('selection');
    }).property()
  });

  exports['default'] = DecisionRadioButtonComponent;

});