define('tahi/controllers/overlays/paper_editor', ['exports', 'tahi/pods/task/controller', 'tahi/mixins/controllers/select-2-assignees'], function (exports, TaskController, Select2Assignees) {

  'use strict';

  var PaperEditorOverlayController;

  PaperEditorOverlayController = TaskController['default'].extend(Select2Assignees['default'], {
    select2RemoteUrl: Ember.computed('paper.journal.id', function() {
      return "/filtered_users/editors/" + (this.get('model.paper.journal.id')) + "/";
    }),
    actions: {
      assignEditor: function(select2User) {
        return this.store.find('user', select2User.id).then((function(_this) {
          return function(user) {
            _this.set('editor', user);
            return _this.send('saveModel');
          };
        })(this));
      }
    }
  });

  exports['default'] = PaperEditorOverlayController;

});