define('tahi/models/comment-look', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var CommentLook, a;

  a = DS['default'].attr;

  CommentLook = DS['default'].Model.extend({
    comment: DS['default'].belongsTo('comment'),
    user: DS['default'].belongsTo('user'),
    readAt: a('date'),
    taskId: a('string'),
    paperId: a('string')
  });

  exports['default'] = CommentLook;

});