define('tahi/pods/admin/journal/manuscript-manager-template/edit/controller', ['exports', 'ember', 'tahi/services/utils', 'tahi/mixins/validation-errors'], function (exports, Ember, Utils, ValidationErrorsMixin) {

  'use strict';

  var ManuscriptManagerTemplateEditController;

  ManuscriptManagerTemplateEditController = Ember['default'].ObjectController.extend(ValidationErrorsMixin['default'], {
    dirty: false,
    editMode: false,
    journal: Ember['default'].computed.alias('model.journal'),
    positionSort: ["position:asc"],
    sortedPhaseTemplates: Ember['default'].computed.sort('phaseTemplates', 'positionSort'),
    deletedRecords: null,
    showSaveButton: (function() {
      return this.get('dirty') || this.get('editMode');
    }).property('dirty', 'editMode'),
    deleteRecord: function(record) {
      var deleted;
      deleted = this.get('deletedRecords') || [];
      deleted.addObject(record);
      record.deleteRecord();
      return this.setProperties({
        deletedRecords: deleted,
        dirty: true
      });
    },
    successfulSave: function(transition) {
      this.reset();
      if (transition) {
        return transition.retry();
      } else {
        return this.transitionToRoute('admin.journal.manuscript_manager_template.edit', this.get('model'));
      }
    },
    reset: function() {
      return this.setProperties({
        editMode: false,
        dirty: false,
        deletedRecords: []
      });
    },
    actions: {
      toggleEditMode: function() {
        this.clearValidationErrors();
        this.toggleProperty('editMode');
        return null;
      },
      changeTaskPhase: function(taskTemplate, targetPhaseTemplate) {
        var newPosition;
        newPosition = targetPhaseTemplate.get('length');
        taskTemplate.setProperties({
          phaseTemplate: targetPhaseTemplate,
          position: newPosition
        });
        targetPhaseTemplate.get('taskTemplates').pushObject(taskTemplate);
        return this.set('dirty', true);
      },
      addPhase: function(position) {
        this.get('model.phaseTemplates').forEach(function(phaseTemplate) {
          if (phaseTemplate.get('position') >= position) {
            return phaseTemplate.incrementProperty('position');
          }
        });
        this.store.createRecord('phaseTemplate', {
          name: 'New Phase',
          manuscriptManagerTemplate: this.get('model'),
          position: position
        });
        return this.set('dirty', true);
      },
      removePhase: function(phaseTemplate) {
        return this.deleteRecord(phaseTemplate);
      },
      rollbackPhase: function(phase, oldName) {
        return phase.set('name', oldName);
      },
      removeTask: function(taskTemplate) {
        return this.deleteRecord(taskTemplate);
      },
      savePhase: function(phase) {
        this.set('dirty', true);
        return null;
      },
      saveTemplate: function(transition) {
        return this.get('model').save().then((function(_this) {
          return function(mmt) {
            var taskTemplates;
            taskTemplates = [];
            return Ember['default'].RSVP.all(mmt.get('phaseTemplates').invoke('save')).then(function(phaseTemplates) {
              var promises;
              promises = phaseTemplates.map(function(phaseTemplate) {
                return phaseTemplate.get('taskTemplates').invoke('save');
              });
              return Ember['default'].RSVP.all(promises.compact()).then(function() {
                var deletedRecords;
                if (deletedRecords = _this.get('deletedRecords')) {
                  return Ember['default'].RSVP.all(deletedRecords.invoke('save')).then(function() {
                    return _this.successfulSave(transition);
                  });
                } else {
                  return _this.successfulSave(transition);
                }
              });
            });
          };
        })(this))["catch"]((function(_this) {
          return function(response) {
            return _this.displayValidationErrorsFromResponse(response);
          };
        })(this));
      },
      rollback: function() {
        if (this.get('model.isNew')) {
          this.get('model').deleteRecord();
          this.reset();
          return this.send('didRollBack');
        } else {
          this.store.unloadAll('taskTemplate');
          this.store.unloadAll('phaseTemplate');
          this.get('model').rollback();
          return this.get('model').reload().then((function(_this) {
            return function() {
              _this.reset();
              return _this.send('didRollBack');
            };
          })(this));
        }
      }
    }
  });

  exports['default'] = ManuscriptManagerTemplateEditController;

});