define('tahi/helpers/formatted-time', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var FormattedTime;

  FormattedTime = Ember['default'].Handlebars.makeBoundHelper(function(time) {
    time || (time = new Date());
    return new Handlebars.SafeString($.timeago(time));
  });

  exports['default'] = FormattedTime;

});