define('tahi/mixins/routes/animate-element', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var AnimateElement;

  AnimateElement = Ember['default'].Mixin.create({
    out: function(selector, speed) {
      var defer;
      defer = new Ember['default'].RSVP.defer();
      $(selector).removeClass('animation-fade-in').addClass('animation-fade-out');
      Ember['default'].run.later(defer, (function() {
        defer.resolve();
      }), speed);
      return defer.promise;
    },
    "in": function(selector, speed) {
      var defer;
      defer = new Ember['default'].RSVP.defer();
      $(selector).addClass('animation-fade-in');
      Ember['default'].run.later(defer, (function() {
        defer.resolve();
      }), speed);
      return defer.promise;
    },
    animateOverlayIn: function() {
      return this["in"]('.overlay', 330);
    },
    animateOverlayOut: function() {
      return this.out('.overlay', 230);
    }
  });

  exports['default'] = AnimateElement;

});