define('tahi/views/flow-manager', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var FlowManagerView;

  FlowManagerView = Ember['default'].View.extend({
    columnCountDidChange: (function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, Tahi.utils.resizeColumnHeaders);
    }).on('didInsertElement').observes('controller.model.@each')
  });

  exports['default'] = FlowManagerView;

});