define('tahi/models/funder', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Funder, a;

  a = DS['default'].attr;

  Funder = DS['default'].Model.extend({
    task: DS['default'].belongsTo('financialDisclosureTask'),
    authors: DS['default'].hasMany('author'),
    funderHadInfluence: a('boolean'),
    funderInfluenceDescription: a('string'),
    grantNumber: a('string'),
    name: a('string'),
    relationshipsToSerialize: ['authors'],
    website: a('string')
  });

  exports['default'] = Funder;

});