define('tahi/pods/admin/journal/flow-manager/controller', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var JournalFlowManagerController;

  JournalFlowManagerController = Ember['default'].ObjectController.extend({
    flowSort: ['position:asc'],
    sortedFlows: Ember['default'].computed.sort('model.flows', 'flowSort'),
    newFlowPosition: function() {
      return this.get('sortedFlows.lastObject.position') + 1;
    },
    actions: {
      saveFlow: function(flow) {
        return flow.save().then(function() {
          return Ember['default'].run.schedule('afterRender', Utils['default'].resizeColumnHeaders);
        });
      },
      removeFlow: function(flow) {
        flow.get('role.flows').then(function(flows) {
          return flows.removeObject(flow);
        });
        return flow.destroyRecord();
      },
      addFlow: function() {
        var flow;
        flow = this.store.createRecord('flow', {
          title: 'Up for grabs',
          role: this.get('model'),
          position: this.newFlowPosition(),
          query: {},
          taskRoles: []
        });
        return flow.save().then(function(flow) {
          return flow.get('role.flows').then(function(flows) {
            return flows.addObject(flow);
          });
        });
      }
    }
  });

  exports['default'] = JournalFlowManagerController;

});