define('tahi/models/phase', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Phase, a;

  a = DS['default'].attr;

  Phase = DS['default'].Model.extend({
    paper: DS['default'].belongsTo('paper'),
    tasks: DS['default'].hasMany('task', {
      polymorphic: true
    }),
    name: a('string'),
    position: a('number'),
    noTasks: Ember.computed.empty('tasks.[]')
  });

  exports['default'] = Phase;

});