define('tahi/tests/controllers/manuscript-manager-template-index-test', ['ember', '../helpers/start-app', 'ember-qunit'], function (Ember, startApp, ember_qunit) {

  'use strict';

  ember_qunit.moduleFor('controller:admin/journal/manuscript-manager-template/index', 'ManuscriptManagerTemplateIndexController', {
    setup: function() {
      startApp['default']();
      return Ember['default'].run((function(_this) {
        return function() {
          _this.ctrl = _this.subject();
          _this.store = getStore();
          _this.template = _this.store.createRecord('manuscriptManagerTemplate', {
            name: 'a template'
          });
          return _this.ctrl.setProperties({
            model: [_this.template],
            store: _this.store
          });
        };
      })(this));
    }
  });

  ember_qunit.test('#destroyTemplate does not delete the last template', function() {
    this.ctrl.send('destroyTemplate', this.template);
    return equal(this.ctrl.get('model.length'), 1);
  });

  ember_qunit.test('#destroyTemplate deletes the given template when there are more than one templates', function() {
    var handler;
    handler = function() {};
    return Ember['default'].run((function(_this) {
      return function() {
        _this.nextTemplate = _this.store.createRecord('manuscriptManagerTemplate', {
          name: 'next template'
        });
        _this.ctrl.set('model', [_this.template, _this.nextTemplate]);
        sinon.stub(_this.nextTemplate, 'destroyRecord').returns(new Ember['default'].RSVP.Promise(handler, handler));
        _this.ctrl.send('destroyTemplate', _this.nextTemplate);
        return ok(_this.nextTemplate.destroyRecord.called);
      };
    })(this));
  });

});