define('tahi/pods/admin/journal/manuscript-manager-template/index/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ManuscriptManagerTemplateIndexController;

  ManuscriptManagerTemplateIndexController = Ember['default'].ArrayController.extend({
    canDeleteTemplates: Ember['default'].computed.gt('model.length', 1),
    actions: {
      addTemplate: function() {
        return this.transitionToRoute('admin.journal.manuscript_manager_template.new');
      },
      destroyTemplate: function(template) {
        if (this.get('canDeleteTemplates')) {
          return template.destroyRecord().then((function(_this) {
            return function() {
              return _this.get('model').removeObject(template);
            };
          })(this));
        }
      }
    }
  });

  exports['default'] = ManuscriptManagerTemplateIndexController;

});