define('tahi/pods/components/flow-task-group/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var FlowTaskGroupComponent;

  FlowTaskGroupComponent = Ember['default'].Component.extend({
    tagName: 'li',
    actions: {
      viewCard: function(task) {
        return this.sendAction('viewCard', task);
      }
    }
  });

  exports['default'] = FlowTaskGroupComponent;

});