define('tahi/models/comment', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Comment, a;

  a = DS['default'].attr;

  Comment = DS['default'].Model.extend({
    commenter: DS['default'].belongsTo('user'),
    task: DS['default'].belongsTo('task', {
      polymorphic: true
    }),
    commentLook: DS['default'].belongsTo('comment-look'),
    body: a('string'),
    createdAt: a('date'),
    entities: a(),
    isUnreadBy: function(user) {
      var commentLook;
      if (commentLook = this.get('commentLook')) {
        return Em.isEmpty(commentLook.get('readAt'));
      }
    },
    markReadBy: function(user) {
      var cl;
      cl = this.get('commentLook');
      cl.set('readAt', new Date());
      return cl.save();
    }
  });

  exports['default'] = Comment;

});