define('tahi/models/admin-journal', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var AdminJournal, a;

  a = DS['default'].attr;

  AdminJournal = DS['default'].Model.extend({
    manuscriptManagerTemplates: DS['default'].hasMany('manuscriptManagerTemplate'),
    roles: DS['default'].hasMany('role'),
    journalTaskTypes: DS['default'].hasMany('journalTaskType'),
    logoUrl: a('string'),
    name: a('string'),
    paperTypes: a(),
    epubCoverUrl: a('string'),
    epubCoverFileName: a('string'),
    epubCss: a('string'),
    pdfCss: a('string'),
    manuscriptCss: a('string'),
    description: a('string'),
    paperCount: a('number'),
    createdAt: a('date'),
    doiJournalPrefix: a('string'),
    doiPublisherPrefix: a('string'),
    lastDoiIssued: a('number')
  });

  exports['default'] = AdminJournal;

});