define('tahi/models/figure', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Figure, a;

  a = DS['default'].attr;

  Figure = DS['default'].Model.extend({
    paper: DS['default'].belongsTo('paper'),
    alt: a('string'),
    filename: a('string'),
    src: a('string'),
    status: a('string'),
    title: a('string'),
    caption: a('string'),
    previewSrc: a('string'),
    detailSrc: a('string'),
    createdAt: a('date'),
    updatePaperFigures: (function() {
      var paperFigures;
      paperFigures = this.get('paper.figures');
      return paperFigures.addObject(this);
    }).on('didLoad'),
    isStrikingImage: false,
    strikingImageDidChange: (function() {
      return this.set('isStrikingImage', this.get('paper.strikingImageId') === this.get('id'));
    }).observes('paper.strikingImageId').on('didLoad')
  });

  exports['default'] = Figure;

});