define('tahi/tests/integration/edit-paper-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/setups', '../helpers/mock-server', '../helpers/factory'], function (Ember, ember_qunit, startApp, setups, setupMockServer, Factory) {

  'use strict';

  var app, currentPaper, fakeUser, server;

  app = null;

  server = null;

  fakeUser = null;

  currentPaper = null;

  module('Integration: EditPaper', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var collaborators, figureTask, figureTaskId, figureTaskResponse, journal, litePaper, paperPayload, paperResponse, phase, records, taskPayload;
      app = startApp['default']();
      server = setupMockServer['default']();
      fakeUser = window.currentUser.user;
      figureTaskId = 94139;
      records = setups.paperWithTask('FigureTask', {
        id: figureTaskId,
        role: "author"
      });
      currentPaper = records[0], figureTask = records[1], journal = records[2], litePaper = records[3], phase = records[4];
      paperPayload = Factory['default'].createPayload('paper');
      paperPayload.addRecords(records.concat([fakeUser]));
      paperResponse = paperPayload.toJSON();
      taskPayload = Factory['default'].createPayload('task');
      taskPayload.addRecords([figureTask, litePaper, fakeUser]);
      figureTaskResponse = taskPayload.toJSON();
      collaborators = [
        {
          id: "35",
          full_name: "Aaron Baker",
          info: "testroles2, collaborator"
        }
      ];
      server.respondWith('GET', "/papers/" + currentPaper.id, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(paperResponse)
      ]);
      server.respondWith('GET', "/tasks/" + figureTaskId, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(figureTaskResponse)
      ]);
      server.respondWith('PUT', /\/tasks\/\d+/, [
        204, {
          "Content-Type": "application/json"
        }, JSON.stringify({})
      ]);
      return server.respondWith('GET', /\/filtered_users\/users\/\d+/, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify([])
      ]);
    }
  });

  ember_qunit.test('visiting /edit-paper: Author completes all metadata cards', function() {
    expect(2);
    visit("/papers/" + currentPaper.id + "/edit").then(function() {
      var submitButton;
      submitButton = find('a:contains("Submit")');
      return ok(submitButton.hasClass('button--disabled'), "Submit is disabled");
    }).then(function() {
      var card, _i, _len, _ref, _results;
      _ref = find('#paper-metadata-tasks .card-content');
      _results = [];
      for (_i = 0, _len = _ref.length; _i < _len; _i++) {
        card = _ref[_i];
        click(card);
        click('#task_completed');
        _results.push(click('.overlay-close-button:first'));
      }
      return _results;
    });
    return andThen(function() {
      var submitButton;
      submitButton = find('a:contains("Submit")');
      return ok(!submitButton.hasClass('button--disabled'), "Submit is enabled");
    });
  });

  ember_qunit.test('on paper.edit when paper.editable changes, user transitions to paper.index', function() {
    visit("/papers/" + currentPaper.id + "/edit").then(function() {
      return Ember['default'].run(function() {
        return getStore().getById('paper', currentPaper.id).set('editable', false);
      });
    });
    return andThen(function() {
      ok(!find('.button-primary:contains("Submit")').length);
      return equal(currentRouteName(), "paper.index");
    });
  });

  ember_qunit.test('on paper.edit when there are no metadata tasks', function() {
    var paperPayload, paperResponse, records;
    expect(2);
    records = setups.paperWithTask('Task', {
      id: 2,
      role: "admin"
    });
    currentPaper = records[0];
    paperPayload = Factory['default'].createPayload('paper');
    paperPayload.addRecords(records.concat([fakeUser]));
    paperResponse = paperPayload.toJSON();
    server.respondWith('GET', "/papers/" + currentPaper.id, [
      200, {
        "Content-Type": "application/json"
      }, JSON.stringify(paperResponse)
    ]);
    return visit("/papers/" + currentPaper.id + "/edit").then(function() {
      return ok(find('main.sidebar-empty').length, "The sidebar should be hidden");
    }).then(function() {
      var msg;
      msg = "There is a submit manuscript button in the main area";
      return ok(find('.edit-paper .no-sidebar-submit-manuscript.button--green:contains("Submit Manuscript")').length, msg);
    });
  });

  ember_qunit.test('on paper.index when there are no metadata tasks', function() {
    var paperPayload, paperResponse, records;
    expect(2);
    records = setups.paperWithTask('Task', {
      id: 2,
      role: "admin"
    });
    currentPaper = records[0];
    paperPayload = Factory['default'].createPayload('paper');
    paperPayload.addRecords(records.concat([fakeUser]));
    paperResponse = paperPayload.toJSON();
    server.respondWith('GET', "/papers/" + currentPaper.id, [
      200, {
        "Content-Type": "application/json"
      }, JSON.stringify(paperResponse)
    ]);
    visit("/papers/" + currentPaper.id + "/edit").then(function() {
      return Ember['default'].run(function() {
        return getStore().getById('paper', currentPaper.id).set('editable', false);
      });
    });
    return andThen(function() {
      var msg;
      ok(find('main.sidebar-empty').length, "The sidebar should be hidden");
      msg = "There is no submit manuscript button in the main area";
      return ok(!find('.edit-paper .no-sidebar-submit-manuscript.button--green:contains("Submit Manuscript")').length, msg);
    });
  });

});