define('tahi/pods/components/flash-message/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    classNames: ["flash-message"],
    classNameBindings: ["type"],
    layoutName: "flash-message",

    type: (function () {
      return "flash-message--" + this.get("message.type");
    }).property("message.type"),

    fadeIn: (function () {
      this.$().hide().fadeIn(250);
    }).on("didInsertElement"),

    actions: {
      remove: function remove() {
        var _this = this;
        this.$().fadeOut(function () {
          _this.sendAction("remove", _this.get("message"));
        });
      }
    }
  });

});