define('tahi/pods/components/question-radio/component', ['exports', 'ember', 'tahi/pods/components/question/component'], function (exports, Ember, QuestionComponent) {

  'use strict';

  exports['default'] = QuestionComponent['default'].extend({
    displayContent: Ember['default'].computed.oneWay("selectedYes"),
    selectedYes: Ember['default'].computed.equal("model.answer", "Yes"),
    selectedNo: Ember['default'].computed.equal("model.answer", "No")
  });

});