define('tahi/pods/admin/admin-journal-user/route', ['exports', 'ember', 'tahi/routes/authorized'], function (exports, Ember, AuthorizedRoute) {

  'use strict';

  var AdminJournalUserRoute;

  AdminJournalUserRoute = AuthorizedRoute['default'].extend({
    model: function() {
      return this.store.find('adminJournalUser');
    }
  });

  exports['default'] = AdminJournalUserRoute;

});