define('tahi/services/visual-editor', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var VisualEditorService;

  VisualEditorService = Ember['default'].Object.extend({
    init: function() {
      return ve.init.platform.setModulesUrl('/visual-editor/modules');
    },
    isEnabled: false,
    isFocused: false,
    isCurrentlyEditing: Ember['default'].computed.and('isEnabled', 'isFocused'),
    update: function($parent, content) {
      var container, self, target;
      container = $('<div>');
      $parent.html('').append(container);
      ve.debug = false;
      target = new ve.init.sa.Target(container, ve.createDocumentFromHtml(content || ''));
      self = this;
      target.on('surfaceReady', function() {
        var surfaceView;
        target.toolbar.disableFloatable();
        surfaceView = target.surface.getView();
        surfaceView.on('focus', function() {
          return self.set('isFocused', true);
        });
        return surfaceView.on('blur', function() {
          return self.set('isFocused', false);
        });
      });
      return this.set('target', target);
    },
    bodyHtml: (function() {
      var documentNode, surf;
      surf = this.get('target').surface;
      documentNode = ve.dm.converter.getDomFromModel(surf.getModel().getDocument());
      return $(documentNode).find('body').html();
    }).property().volatile(),
    isEmpty: (function() {
      return Ember['default'].isBlank(Ember['default'].$(this.get('bodyHtml')).text());
    }).property().volatile(),
    enable: function() {
      var target;
      if (target = this.get('target')) {
        target.surface.enable();
        return this.setProperties({
          isEnabled: true,
          isFocused: false
        });
      }
    },
    disable: function() {
      var target;
      if (target = this.get('target')) {
        this.get("target").surface.disable();
        return this.set('isEnabled', false);
      }
    },
    startEditing: function() {
      if (this.get('isEnabled')) {
        this.get('target').surface.getView().focus();
        return this.set('isFocused', true);
      }
    }
  });

  exports['default'] = VisualEditorService;

});