define('tahi/services/heartbeat', ['exports', 'ember', 'tahi/services/rest-less'], function (exports, Ember, RESTless) {

  'use strict';

  var HeartbeatService;

  HeartbeatService = Ember['default'].Object.extend({
    interval: 90 * 1000,
    intervalId: null,
    resource: null,
    init: function() {
      var resource;
      resource = this.get('resource');
      if (!resource) {
        throw new Error("need to specify resource");
      }
    },
    start: function() {
      var heartbeatWrapper;
      this.heartbeat();
      heartbeatWrapper = (function(_this) {
        return function() {
          return _this.heartbeat();
        };
      })(this);
      return this.set('intervalId', setInterval(heartbeatWrapper, this.get('interval')));
    },
    stop: function() {
      var intervalId;
      intervalId = this.get('intervalId');
      if (intervalId) {
        clearInterval(intervalId);
        return this.set('intervalId', null);
      }
    },
    heartbeat: function() {
      return RESTless['default'].putModel(this.get('resource'), "/heartbeat");
    }
  });

  exports['default'] = HeartbeatService;

});