define('tahi/pods/application/controller', ['exports', 'ember', 'tahi/config/environment'], function (exports, Ember, ENV) {

  'use strict';

  var ApplicationController;

  ApplicationController = Ember['default'].Controller.extend({
    delayedSave: false,
    isLoading: false,
    isLoggedIn: (function() {
      return !Ember['default'].isBlank(this.currentUser);
    }).property('currentUser'),
    isAdmin: Ember['default'].computed.alias('currentUser.siteAdmin'),
    canViewAdminLinks: false,
    canViewFlowManagerLink: false,
    clearError: (function() {
      return this.set('error', null);
    }).observes('currentPath'),
    resetScrollPosition: (function() {
      return window.scrollTo(0, 0);
    }).observes('currentPath'),
    overlayBackground: Ember['default'].computed.oneWay('defaultBackground'),
    overlayRedirect: [],
    defaultBackground: 'overlay-background',
    testing: (function() {
      return Ember['default'].testing || ENV['default'].environment === 'test';
    }).property(),
    showSaveStatusDiv: Ember['default'].computed.and('testing', 'delayedSave'),
    navigationVisible: false,
    toggleNavigation: (function() {
      if (this.get('navigationVisible')) {
        return $('html').addClass('navigation-visible');
      } else {
        return $('html').removeClass('navigation-visible');
      }
    }).observes('navigationVisible'),
    actions: {
      showNavigation: function() {
        return this.set('navigationVisible', true);
      },
      hideNavigation: function() {
        return this.set('navigationVisible', false);
      }
    }
  });

  exports['default'] = ApplicationController;

});