define('tahi/tests/integration/admin-journal-test', ['ember', '../helpers/start-app', 'ember-qunit', '../helpers/setups', '../helpers/mock-server', '../helpers/factory'], function (Ember, startApp, ember_qunit, setups, setupMockServer, Factory) {

  'use strict';

  var app, journalId, server;

  app = null;

  server = null;

  journalId = null;

  module('Integration: Admin Journal Test', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var adminJournalPayload, adminRole, journal, stubbedAdminJournalUserResponse;
      app = startApp['default']();
      server = setupMockServer['default']();
      journal = Factory['default'].createRecord('AdminJournal');
      journalId = journal.id;
      adminRole = Factory['default'].createJournalRole(journal, {
        name: "Admin",
        kind: "admin",
        can_administer_journal: true,
        can_view_assigned_manuscript_managers: false,
        can_view_all_manuscript_managers: true,
        can_view_flow_manager: true
      });
      adminJournalPayload = Factory['default'].createPayload('adminJournal');
      adminJournalPayload.addRecords([journal, adminRole]);
      stubbedAdminJournalUserResponse = {
        user_roles: [],
        admin_journal_users: []
      };
      server.respondWith('GET', '/admin/journals/authorization', [
        204, {
          'Content-Type': 'application/html'
        }, ""
      ]);
      server.respondWith('PUT', "/admin/journals/" + journalId, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(adminJournalPayload.toJSON())
      ]);
      server.respondWith('GET', "/admin/journals/" + journalId, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(adminJournalPayload)
      ]);
      return server.respondWith('GET', "/admin/journal_users?journal_id=" + journalId, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(stubbedAdminJournalUserResponse)
      ]);
    }
  });

  ember_qunit.test('admin sees the complete DOI form', function() {
    return visit("/admin/journals/" + journalId).then(function() {
      ok(find('.admin-doi-setting-section'));
      ok(find('.admin-doi-setting-section .publisher-prefix'));
      ok(find('.admin-doi-setting-section .journal-prefix'));
      return ok(find('.admin-doi-setting-section .article'));
    });
  });

  ember_qunit.test('saving doi info will send a put request to the admin journal controller', function() {
    var adminPage;
    adminPage = "/admin/journals/" + journalId;
    visit(adminPage).then(function() {
      click('.admin-doi-settings-edit-button');
      fillIn('.admin-doi-setting-section .doi_publisher_prefix', 'PPREFIX');
      fillIn('.admin-doi-setting-section .doi_journal_prefix', 'JPREFIX');
      fillIn('.admin-doi-setting-section .last_doi_issued', '10000');
      return click('.admin-doi-setting-section button');
    });
    return andThen(function() {
      return ok(_.findWhere(server.requests, {
        method: 'PUT',
        url: adminPage
      }));
    });
  });

});