define('tahi/store', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var ApplicationStore;

  ApplicationStore = DS['default'].Store.extend({
    adapter: '-active-model',
    push: function(type, data, _partial) {
      var dataType, modelType, oldRecord, oldType;
      oldType = type;
      dataType = data.type;
      modelType = oldType;
      if (dataType && (this.modelFor(oldType) !== this.modelFor(dataType))) {
        modelType = dataType;
        if (oldRecord = this.getById(oldType, data.id)) {
          this.dematerializeRecord(oldRecord);
        }
      }
      return this._super(this.modelFor(modelType), data, _partial);
    },
    findTask: function(id) {
      var matchingTask;
      matchingTask = this.allTaskClasses().find(function(tm) {
        return tm.idToRecord[id];
      });
      if (matchingTask) {
        return matchingTask.idToRecord[id];
      }
    },
    didSaveRecord: function(record, data) {
      var es;
      this._super(record, data);
      es = this.container.lookup('eventstream:main');
      return es.play();
    },
    allTaskClasses: function() {
      return Ember.keys(this.typeMaps).reduce((function(_this) {
        return function(memo, key) {
          var typeMap;
          typeMap = _this.typeMaps[key];
          if (typeMap.type.toString().match(/:.*task:/)) {
            memo.addObject(typeMap);
          }
          return memo;
        };
      })(this), []);
    }
  });

  exports['default'] = ApplicationStore;

});