define('tahi/models/lite-paper', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var LitePaper, a;

  a = DS['default'].attr;

  LitePaper = DS['default'].Model.extend({
    title: a('string'),
    shortTitle: a('string'),
    doi: a('string'),
    submitted: a('boolean'),
    relatedAtDate: a('date'),
    roles: a(),
    displayTitle: (function() {
      return this.get('title') || this.get('shortTitle');
    }).property('title', 'shortTitle'),
    roleList: (function() {
      return this.get('roles').sort().join(', ');
    }).property('roles.@each', 'roles.[]')
  });

  exports['default'] = LitePaper;

});