define('tahi/pods/components/upload-preview/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var UploadPreviewComponent;

  UploadPreviewComponent = Ember['default'].Component.extend({
    classNameBindings: [':upload-preview', ':_uploading', 'error:alert'],
    file: Ember['default'].computed.alias('upload.file'),
    filename: Ember['default'].computed.alias('file.name'),
    preview: (function() {
      var preview;
      preview = this.get('file.preview');
      return preview != null ? preview.toDataURL() : void 0;
    }).property('file.preview'),
    progress: (function() {
      return Math.round(this.get('upload.dataLoaded') * 100 / this.get('upload.dataTotal'));
    }).property('upload.dataLoaded', 'upload.dataTotal'),
    error: null,
    progressBarStyle: (function() {
      return "width: " + (this.get('progress')) + "%;";
    }).property('progress')
  });

  exports['default'] = UploadPreviewComponent;

});