define('tahi/pods/admin/journal/manuscript-manager-template/thumbnail/view', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ManuscriptManagerTemplateThumbnailView;

  ManuscriptManagerTemplateThumbnailView = Ember['default'].View.extend({
    templateName: 'admin/journal/manuscript-manager-template/thumbnail',
    classNames: ['mmt-thumbnail', 'blue-box'],
    classNameBindings: ['destroyState:mmt-thumbnail-destroy'],
    canDeleteTemplates: Ember['default'].computed.alias('controller.canDeleteTemplates'),
    phaseCount: Ember['default'].computed.alias('content.phaseTemplates.length'),
    destroyState: false,
    actions: {
      toggleWillDestroyTemplate: function() {
        return this.toggleProperty('destroyState');
      }
    }
  });

  exports['default'] = ManuscriptManagerTemplateThumbnailView;

});