define('tahi/mixins/routes/alert-unsaved-changes', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var AlertUnsavedChanges;

  AlertUnsavedChanges = Ember['default'].Mixin.create({
    actions: {
      willTransition: function(transition) {
        if (this.controller.get('dirty')) {
          this.set('attemptingTransition', transition);
          transition.abort();
          return this.render('overlays/unsaved-data', {
            into: 'application',
            outlet: 'overlay',
            controller: 'overlays/unsaved-data'
          });
        } else {
          return true;
        }
      },
      discardChanges: function() {
        this.controller.send('rollback');
        return this.get('attemptingTransition').retry();
      },
      cancelTransition: function() {
        return this.send('closeOverlay');
      }
    }
  });

  exports['default'] = AlertUnsavedChanges;

});