define('tahi/mixins/models/common-flow-attrs', ['exports', 'ember', 'ember-data'], function (exports, Ember, DS) {

  'use strict';

  var CommonFlowAttrs;

  CommonFlowAttrs = Ember['default'].Mixin.create({
    litePapers: DS['default'].hasMany('lite-paper'),
    tasks: DS['default'].hasMany('card-thumbnail'),
    title: DS['default'].attr('string'),
    flowId: DS['default'].attr('number'),
    taskRoles: DS['default'].attr()
  });

  exports['default'] = CommonFlowAttrs;

});