define('tahi/mixins/components/adhoc-inline-edit-item', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var AdhocInlineEditItem;

  AdhocInlineEditItem = Ember['default'].Mixin.create({
    editing: Ember['default'].computed.alias('parentView.editing'),
    isNew: false,
    bodyPart: null,
    bodyPartType: Ember['default'].computed.alias('bodyPart.type'),
    actions: {
      deleteItem: function() {
        return this.sendAction('delete', this.get('bodyPart'));
      }
    }
  });

  exports['default'] = AdhocInlineEditItem;

});