define('tahi/pods/admin/route', ['exports', 'ember', 'tahi/routes/authorized'], function (exports, Ember, AuthorizedRoute) {

  'use strict';

  var AdminRoute;

  AdminRoute = AuthorizedRoute['default'].extend({
    beforeModel: function() {
      return Ember['default'].$.ajax('/admin/journals/authorization');
    },
    actions: {
      viewUserDetails: function(user) {
        this.controllerFor('overlays/userDetail').set('model', user);
        return this.render('overlays/userDetail', {
          into: 'application',
          outlet: 'overlay',
          controller: 'overlays/userDetail'
        });
      },
      didTransition: function() {
        $('html').attr('screen', 'admin');
        return true;
      },
      willTransition: function() {
        $('html').attr('screen', '');
        return true;
      }
    }
  });

  exports['default'] = AdminRoute;

});