define('tahi/models/attachment', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Attachment, a;

  a = DS['default'].attr;

  Attachment = DS['default'].Model.extend({
    attachable: DS['default'].belongsTo('attachable', {
      polymorphic: true
    }),
    caption: a('string'),
    previewSrc: a('string'),
    src: a('string'),
    status: a('string'),
    title: a('string')
  });

  exports['default'] = Attachment;

});