define('tahi/tests/integration/submit-paper-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/mock-server', '../helpers/setups', '../helpers/factory'], function (Ember, ember_qunit, startApp, setupMockServer, setups, Factory) {

  'use strict';

  var app, currentPaper, fakeUser, server;

  app = null;

  server = null;

  fakeUser = null;

  currentPaper = null;

  module('Integration: Submitting Paper', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var dashboardResponse, paperPayload, paperResponse, records;
      app = startApp['default']();
      server = setupMockServer['default']();
      fakeUser = window.currentUser.user;
      records = setups.paperWithTask('Task', {
        id: 1,
        title: "Metadata",
        isMetadataTask: true,
        completed: true
      });
      currentPaper = records[0];
      paperPayload = Factory['default'].createPayload('paper');
      paperPayload.addRecords(records.concat([fakeUser]));
      paperResponse = paperPayload.toJSON();
      server.respondWith('GET', "/papers/" + currentPaper.id, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(paperResponse)
      ]);
      dashboardResponse = {
        dashboards: [
          {
            id: 1,
            total_paper_count: 0,
            total_page_count: 0
          }
        ]
      };
      server.respondWith('PUT', "/papers/" + currentPaper.id, [
        204, {
          "Content-Type": "application/html"
        }, ""
      ]);
      server.respondWith('PUT', "/papers/" + currentPaper.id + "/submit", [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify({
          papers: []
        })
      ]);
      return server.respondWith('GET', '/dashboards', [
        200, {
          'Content-Type': 'application/json'
        }, JSON.stringify(dashboardResponse)
      ]);
    }
  });

  ember_qunit.test("User can submit a paper", function() {
    expect(1);
    visit("/papers/" + currentPaper.id + "/edit");
    click(".edit-paper a:contains('Submit Manuscript')");
    click("button.button-primary");
    return andThen(function() {
      return ok(_.findWhere(server.requests, {
        method: "PUT",
        url: "/papers/" + currentPaper.id + "/submit"
      }), "It posts to the server");
    });
  });

});