define('tahi/tests/serializers/application-test', ['ember', 'ember-data', '../helpers/start-app', 'ember-qunit'], function (Ember, DS, startApp, ember_qunit) {

  'use strict';

  var container, subject;

  subject = null;

  container = null;

  module('integration/serializer', {
    setup: function() {
      var app;
      app = startApp['default']();
      container = app.__container__;
      return subject = container.lookup('serializer:application');
    }
  });

  ember_qunit.test('normalizeType denamespaces task types', function() {
    var result;
    result = subject.normalizeType({
      type: 'Foo::BarTask'
    });
    equal(result.qualified_type, 'Foo::BarTask', 'saves the original type as qualified_type');
    return equal(result.type, 'BarTask', 'strips the namespace off the type');
  });

  ember_qunit.test('serializing a model that was originally namespaced will correctly re-namespace it', function() {
    return Ember['default'].run((function(_this) {
      return function() {
        var json, task;
        task = getStore().createRecord('task', {
          qualifiedType: "Foo::BarTask"
        });
        json = subject.serialize(task);
        equal(json.type, "Foo::BarTask");
        return equal(void 0, json.qualified_type, 'deletes qualified_type from the payload');
      };
    })(this));
  });

  ember_qunit.test('has a custom extractTypeName function to make things easier', function() {
    var hashType, propType;
    hashType = subject.extractTypeName('foo', {
      type: 'bar'
    });
    equal(hashType, 'bar', 'extracts the type from the hash if it exists');
    propType = subject.extractTypeName('cow', {
      otherStuff: 'whoah'
    });
    return equal(propType, 'cow', 'uses the prop for the typeName otherwise');
  });

  ember_qunit.test("extractSingle puts sideloaded things into the store via their 'type' attribute", function() {
    var PlosAuthorsTask, TechCheckTask, jsonHash, store;
    TechCheckTask = DS['default'].Model.extend({
      title: DS['default'].attr('string'),
      type: DS['default'].attr('string')
    });
    PlosAuthorsTask = DS['default'].Model.extend({
      title: DS['default'].attr('string'),
      type: DS['default'].attr('string')
    });
    store = getStore();
    container.register("model:tech-check-task", TechCheckTask);
    container.register("model:plos-authors-task", PlosAuthorsTask);
    jsonHash = {
      tasks: [
        {
          id: '1',
          type: 'TechCheckTask',
          title: 'Tech Check'
        }, {
          id: '2',
          type: 'Foo::PlosAuthorsTask',
          title: 'Check Authors'
        }
      ],
      phase: {
        id: '1',
        tasks: [
          {
            id: '1',
            type: 'TechCheckTask'
          }, {
            id: '2',
            type: 'PlosAuthorsTask'
          }
        ]
      }
    };
    return Ember['default'].run(function() {
      var result;
      result = subject.extractSingle(store, store.modelFor('phase'), jsonHash);
      equal(store.getById('task', 1), null, 'no Task gets pushed into the store');
      store.find('techCheckTask', 1).then(function(task) {
        return equal(task.get('title'), 'Tech Check', 'the message task is in the store');
      });
      return store.find('plosAuthorsTask', 2).then(function(task) {
        return equal(task.get('title'), 'Check Authors', 'the namespaced authors task is in the store');
      });
    });
  });

  ember_qunit.test("extractMany puts normalizes things via their 'type' attribute", function() {
    var PaperEditorTask, jsonHash, store;
    PaperEditorTask = DS['default'].Model.extend({
      title: DS['default'].attr('string'),
      type: DS['default'].attr('string'),
      uniqueProperty: DS['default'].attr('string')
    });
    store = getStore();
    container.register("model:paper-editor-task", PaperEditorTask);
    jsonHash = {
      users: [
        {
          id: '1',
          username: 'editorGuy'
        }
      ],
      tasks: [
        {
          id: '1',
          type: 'PaperEditorTask',
          title: 'Edit Stuff',
          unique_property: "foo"
        }
      ]
    };
    return Ember['default'].run(function() {
      var result;
      result = subject.extractArray(store, store.modelFor('task'), jsonHash);
      return ok(result[0].unique_property);
    });
  });

});