define('tahi/pods/admin/journal/thumbnail/controller', ['exports', 'ember', 'tahi/mixins/file-upload', 'tahi/mixins/validation-errors'], function (exports, Ember, FileUploadMixin, ValidationErrorsMixin) {

  'use strict';

  var JournalThumbnailController;

  JournalThumbnailController = Ember['default'].ObjectController.extend(FileUploadMixin['default'], ValidationErrorsMixin['default'], {
    needs: ['application'],
    currentUser: Ember['default'].computed.alias('controllers.application.currentUser'),
    isEditing: (function() {
      return this.get('model.isDirty');
    }).property(),
    thumbnailId: (function() {
      return "journal-logo-" + (this.get('model.id'));
    }).property(),
    logoUploadUrl: (function() {
      return "/admin/journals/" + (this.get('model.id')) + "/upload_logo";
    }).property('model.id'),
    logoPreview: null,
    journal: null,
    uploadLogoFunction: null,
    stopEditing: function() {
      return this.setProperties({
        isEditing: false,
        uploadLogoFunction: null,
        logoPreview: null
      });
    },
    saveJournal: function() {
      return this.get('model').save().then((function(_this) {
        return function() {
          return _this.stopEditing();
        };
      })(this))["catch"]((function(_this) {
        return function(response) {
          return _this.displayValidationErrorsFromResponse(response);
        };
      })(this));
    },
    actions: {
      editJournalDetails: function() {
        return this.set('isEditing', true);
      },
      uploadFinished: function(data, filename) {
        this.uploadFinished(data, filename);
        this.set('model.logoUrl', data.admin_journal.logo_url);
        return this.saveJournal();
      },
      saveJournalDetails: function() {
        var updateLogo;
        updateLogo = this.get('uploadLogoFunction');
        if (this.get('model.isNew')) {
          return this.get('model').save().then((function(_this) {
            return function(journal) {
              return (updateLogo || _this.stopEditing).call(_this);
            };
          })(this))["catch"]((function(_this) {
            return function(response) {
              return _this.displayValidationErrorsFromResponse(response);
            };
          })(this));
        } else {
          return (updateLogo || this.saveJournal).call(this);
        }
      },
      resetJournalDetails: function() {
        this.get('model').rollback();
        this.set('isEditing', false);
        return this.clearValidationErrors();
      },
      showPreview: function(file) {
        return this.set('logoPreview', file.preview);
      },
      uploadReady: function(uploadLogoFunction) {
        return this.set('uploadLogoFunction', uploadLogoFunction);
      }
    }
  });

  exports['default'] = JournalThumbnailController;

});