define('tahi/controllers/overlays/card-delete', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var CardDeleteOverlayController;

  CardDeleteOverlayController = Ember['default'].Controller.extend({
    overlayClass: 'overlay--fullscreen paper-submit-overlay',
    actions: {
      closeAction: function() {
        this.set('model', null);
        return this.send('closeOverlay');
      },
      removeTask: function() {
        return this.get('model').destroyRecord().then((function(_this) {
          return function(task) {
            return _this.send('closeOverlay');
          };
        })(this));
      }
    }
  });

  exports['default'] = CardDeleteOverlayController;

});