define('tahi/pods/profile/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ProfileRoute;

  ProfileRoute = Ember['default'].Route.extend({
    model: function() {
      return this.currentUser;
    },
    afterModel: function(model) {
      return Ember['default'].$.getJSON('/affiliations', function(data) {
        return model.set('institutions', data.institutions);
      });
    }
  });

  exports['default'] = ProfileRoute;

});