define('tahi/views/manuscript-manager-column', ['exports', 'ember', 'tahi/services/drag-n-drop'], function (exports, Ember, DragNDrop) {

  'use strict';

  var ManuscriptManagerColumnView;

  ManuscriptManagerColumnView = Ember['default'].View.extend(DragNDrop['default'].DroppableMixin, {
    classNames: ['column'],
    nextPosition: (function() {
      return this.get('content.position') + 1;
    }).property('content.position'),
    removeDragStyles: function() {
      return this.$().removeClass('current-drop-target');
    },
    dragOver: function(e) {
      return this.$().addClass('current-drop-target');
    },
    dragLeave: function() {
      return this.removeDragStyles();
    },
    dragEnd: function() {
      return this.removeDragStyles();
    },
    drop: function(e) {
      this.removeDragStyles();
      this.get('controller').send('changeTaskPhase', DragNDrop['default'].dragItem, this.get('content'));
      DragNDrop['default'].dragItem = null;
      return DragNDrop['default'].cancel(e);
    }
  });

  exports['default'] = ManuscriptManagerColumnView;

});