define('tahi/models/card-thumbnail', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var CardThumbnail, a;

  a = DS['default'].attr;

  CardThumbnail = DS['default'].Model.extend({
    litePaper: DS['default'].belongsTo('lite-paper'),
    task: DS['default'].belongsTo('task', {
      polymorphic: true
    }),
    completed: a('boolean'),
    createdAt: a('string'),
    position: a('number'),
    taskType: a('string'),
    title: a('string')
  });

  exports['default'] = CardThumbnail;

});