define('tahi/controllers/overlays/supporting-information', ['exports', 'ember', 'tahi/pods/task/controller', 'tahi/mixins/file-upload'], function (exports, Ember, TaskController, FileUploadMixin) {

  'use strict';

  exports['default'] = TaskController['default'].extend(FileUploadMixin['default'], {
    uploadUrl: (function () {
      return "/supporting_information_files?paper_id=" + this.get("litePaper.id");
    }).property("litePaper.id"),

    actions: {
      uploadFinished: function (data, filename) {
        this.uploadFinished(data, filename);
        this.store.pushPayload("supportingInformationFile", data);
        // TODO: Do we need these anymore? Ember-Data should handle relationships now
        file = this.store.getById("supportingInformationFile", data.supporting_information_file.id);
        this.get("paper.supportingInformationFiles").pushObject(file);
      },

      destroyAttachment: function (attachment) {
        attachment.destroyRecord();
      }
    }
  });

});