define('tahi/controllers/overlays/ad-hoc', ['exports', 'ember', 'tahi/pods/task/controller', 'tahi/mixins/controllers/builds-task-template', 'tahi/mixins/file-upload', 'tahi/services/rest-less'], function (exports, Ember, TaskController, BuildsTaskTemplate, FileUploadMixin, RESTless) {

  'use strict';

  var AdHocOverlayController;

  AdHocOverlayController = TaskController['default'].extend(BuildsTaskTemplate['default'], FileUploadMixin['default'], {
    needs: ['task'],
    blocks: Ember['default'].computed.alias('body'),
    imageUploadUrl: (function() {
      return "/tasks/" + (this.get('model.id')) + "/attachments";
    }).property(),
    isNewTask: Ember['default'].computed.alias('controllers.task.isNewTask'),
    actions: {
      setTitle: function(title) {
        this._super(title);
        return this.send('saveModel');
      },
      saveBlock: function(block) {
        this._super(block);
        return this.send('saveModel');
      },
      deleteBlock: function(block) {
        this._super(block);
        if (!this.isNew(block)) {
          return this.send('saveModel');
        }
      },
      deleteItem: function(item, block) {
        this._super(item, block);
        if (!this.isNew(block)) {
          return this.send('saveModel');
        }
      },
      sendEmail: function(data) {
        return RESTless['default'].putModel(this.get('model'), "/send_message", {
          task: data
        });
      },
      destroyAttachment: function(attachment) {
        return attachment.destroyRecord();
      },
      uploadFinished: function(data, filename) {
        var attachment;
        this.uploadFinished(data, filename);
        this.store.pushPayload('attachment', data);
        attachment = this.store.getById('attachment', data.attachment.id);
        return this.get('model.attachments').pushObject(attachment);
      }
    }
  });

  exports['default'] = AdHocOverlayController;

});