define('tahi/pods/components/add-column/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var AddColumnComponent;

  AddColumnComponent = Ember['default'].Component.extend({
    tagName: 'span',
    classNameBindings: [':add-column', 'bonusClass'],
    attributeBindings: ['toggle:data-toggle', 'placement:data-placement', 'title'],
    toggle: 'tooltip',
    placement: 'auto right',
    title: 'Add Phase',
    click: function() {
      return this.sendAction('action', this.get('position'));
    },
    didInsertElement: function() {
      return this.$().tooltip();
    }
  });

  exports['default'] = AddColumnComponent;

});