define('tahi/pods/components/user-role-selector/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var UserRoleSelectorComponent;

  UserRoleSelectorComponent = Ember['default'].Component.extend({
    classNames: ['user-role-selector', 'select2-multiple'],
    actions: {
      assignRole: function(data) {
        return this.sendAction("selected", data);
      },
      removeRole: function(data) {
        return this.sendAction("removed", data);
      },
      dropdownClosed: function() {
        this.$('.select2-search-field input').removeClass('active');
        return this.$('.assign-role-button').removeClass('searching');
      },
      activateDropdown: function() {
        this.$('.select2-search-field input').addClass('active').trigger('click');
        return this.$('.assign-role-button').addClass('searching');
      }
    }
  });

  exports['default'] = UserRoleSelectorComponent;

});