define('tahi/pods/components/inline-edit-checkbox/component', ['exports', 'ember', 'tahi/mixins/components/adhoc-inline-edit-item'], function (exports, Ember, AdhocInlineEditItem) {

  'use strict';

  var InlineEditCheckboxComponent;

  InlineEditCheckboxComponent = Ember['default'].Component.extend(AdhocInlineEditItem['default'], {
    checked: (function(key, value, oldValue) {
      var answer;
      if (arguments.length > 1) {
        return this.set('bodyPart.answer', value);
      } else {
        answer = this.get('bodyPart.answer');
        return answer === 'true' || answer === true;
      }
    }).property('bodyPart.answer'),
    actions: {
      saveModel: function() {
        return this.sendAction('saveModel');
      },
      deleteItem: function() {
        return this.sendAction('delete', this.get('bodyPart'), this.get('parentView.block'));
      }
    }
  });

  exports['default'] = InlineEditCheckboxComponent;

});