define('tahi/serializers/task', ['exports', 'tahi/serializers/application', 'tahi/mixins/serializers/serializes-has-many'], function (exports, ApplicationSerializer, SerializesHasMany) {

  'use strict';

  var TaskSerializer;

  TaskSerializer = ApplicationSerializer['default'].extend(SerializesHasMany['default'], {
    serializeIntoHash: function(data, type, record, options) {
      var root;
      root = 'task';
      return data[root] = this.serialize(record, options);
    },
    primaryTypeName: function(primaryType) {
      return 'task';
    }
  });

  exports['default'] = TaskSerializer;

});