define('tahi/pods/admin/journal/flow-manager/view', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var JournalFlowManagerView;

  JournalFlowManagerView = Ember['default'].View.extend({
    columnCountDidChange: (function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, Utils['default'].resizeColumnHeaders);
    }).on('didInsertElement').observes('controller.model.flows.@each')
  });

  exports['default'] = JournalFlowManagerView;

});