define('tahi/models/user-flow', ['exports', 'ember-data', 'tahi/mixins/models/common-flow-attrs'], function (exports, DS, CommonFlowAttrs) {

  'use strict';

  var UserFlow;

  UserFlow = DS['default'].Model.extend(CommonFlowAttrs['default'], {
    journalName: DS['default'].attr('string'),
    journalLogo: DS['default'].attr('string')
  });

  exports['default'] = UserFlow;

});