define('tahi/initializers/event-stream', ['exports', 'ember', 'tahi/services/event-stream'], function (exports, Ember, EventStream) {

  'use strict';

  var EventStreamInitializer;

  EventStreamInitializer = {
    name: 'eventStream',
    after: 'currentUser',
    initialize: function(container, application) {
      var es, store;
      es = !container.lookup('user:current') || Ember['default'].testing ? Ember['default'].Object.extend({
        play: function() {
          return null;
        },
        pause: function() {
          return null;
        }
      }) : EventStream['default'];
      store = container.lookup('store:main');
      container.register('eventstream:main', es.extend({
        store: store
      }), {
        singleton: true
      });
      return application.inject('route', 'eventStream', 'eventstream:main');
    }
  };

  exports['default'] = EventStreamInitializer;

});