define('tahi/tests/integration/add-new-card-test', ['ember', '../helpers/start-app', 'ember-qunit', '../helpers/setups', '../helpers/mock-server'], function (Ember, startApp, ember_qunit, setups, setupMockServer) {

  'use strict';

  var app, server;

  app = null;

  server = null;

  module('Integration: adding a new card', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var adminJournalsResponse, taskPayload;
      app = startApp['default']();
      server = setupMockServer['default']();
      adminJournalsResponse = {
        admin_journal: {
          id: 1,
          name: "Test Journal of America",
          journal_task_type_ids: [1]
        },
        journal_task_types: [
          {
            id: 1,
            title: "Ad Hoc",
            kind: "Task",
            journal_id: 1
          }
        ]
      };
      taskPayload = {
        task: {
          id: 2,
          title: "Ad Hoc Task",
          type: "Task",
          phase_id: 1,
          paper_id: 1,
          lite_paper_id: 1
        }
      };
      server.respondWith('GET', /\/papers\/\d+\/manuscript_manager/, [204, {}, ""]);
      server.respondWith('GET', "/papers/1", [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(setups.paperWithParticipant().toJSON())
      ]);
      server.respondWith('POST', "/tasks", [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(taskPayload)
      ]);
      server.respondWith('GET', '/flows/authorization', [
        204, {
          'content-type': 'application/html',
          'tahi-authorization-check': true
        }, ""
      ]);
      return server.respondWith('GET', '/admin/journals/1', [
        200, {
          'Content-Type': 'application/json'
        }, JSON.stringify(adminJournalsResponse)
      ]);
    }
  });

  ember_qunit.test('user sees task overlay when the task is added', function() {
    visit('/papers/1/manage');
    click("a:contains('Add New Card')");
    pickFromChosenSingle('.task-type-select', 'Ad Hoc');
    click('.button--green:contains("Add")');
    return andThen(function() {
      return ok(find('div.overlay-container').length);
    });
  });

});