define('tahi/initializers/error-handler', ['exports', 'tahi/config/environment'], function (exports, ENV) {

  'use strict';

  var ErrorHandler;

  ErrorHandler = {
    name: 'errorHandler',
    after: 'flashMessages',
    initialize: function(container, application) {
      var flash, logError;
      flash = container.lookup('flashMessages:main');
      logError = function(msg) {
        var e;
        e = new Error(msg);
        return console.log(e.stack || e.message);
      };
      container.register('logError:main', logError, {
        instantiate: false
      });
      application.inject('route', 'logError', 'logError:main');
      Ember.onerror = function(error) {
        if (ENV['default'].environment === 'production') {
          if (Bugsnag && Bugsnag.notifyException) {
            return Bugsnag.notifyException(error, "Uncaught Ember Error");
          }
        } else {
          flash.displayMessage('error', error);
          logError('\n' + error.message + '\n' + error.stack + '\n');
          throw error;
        }
      };
      return $(document).ajaxError(function(event, jqXHR, ajaxSettings, thrownError) {
        var msg, status, statusText, type, url;
        type = ajaxSettings.type, url = ajaxSettings.url;
        status = jqXHR.status, statusText = jqXHR.statusText;
        if (status === 403) {
          return;
        }
        if (status === 422) {
          return;
        }
        if (status === 401) {
          return document.location.href = '/users/sign_in';
        }
        msg = "Error with " + type + " request to " + url + ". Server returned " + status + ": " + statusText + ".  " + thrownError;
        logError(msg);
        if (!Ember.testing) {
          return flash.displayMessage('error', msg);
        }
      });
    }
  };

  exports['default'] = ErrorHandler;

});