define('tahi/views/inline-edit', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var InlineEditView;

  InlineEditView = Ember['default'].View.extend({
    templateName: 'inline-edit',
    isTextItem: Ember['default'].computed.equal('item.type', 'text'),
    isCheckboxItem: Ember['default'].computed.equal('item.type', 'checkbox'),
    isEmailItem: Ember['default'].computed.equal('item.type', 'email'),
    editing: Ember['default'].computed.alias('parentView.editing'),
    isSendable: true
  });

  exports['default'] = InlineEditView;

});