define('tahi/pods/components/question/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var QuestionComponent;

  QuestionComponent = Ember['default'].Component.extend({
    tagName: 'div',
    helpText: null,
    displayContent: false,
    model: (function() {
      var ident, question, task;
      ident = this.get('ident');
      if (!ident) {
        throw new Error("You must specify an ident, set to name attr");
      }
      question = this.get('task.questions').findProperty('ident', ident);
      if (!question) {
        task = this.get('task');
        question = task.get('store').createRecord('question', {
          question: this.get('question'),
          ident: ident,
          task: task,
          additionalData: [{}]
        });
        task.get('questions').pushObject(question);
      }
      return question;
    }).property('task', 'ident'),
    additionalData: Em.computed.alias('model.additionalData'),
    change: function() {
      Ember['default'].run.debounce(this, this._saveModel, this.get('model'), 200);
      return false;
    },
    _saveModel: function(model) {
      return model.save();
    }
  });

  exports['default'] = QuestionComponent;

});