define('tahi/tests/components/flow-column-test', ['ember', 'ember-qunit', '../helpers/start-app'], function (Ember, ember_qunit, startApp) {

  'use strict';

  var appendBasicComponent;

  ember_qunit.moduleForComponent('flow-column', 'Unit: components/flow-column', {
    needs: ['helper:display-value', 'component:segmented-button', 'component:segmented-buttons', 'component:progress-spinner', 'component:flow-task-group', 'component:select-2-single'],
    setup: function() {
      return startApp['default']();
    }
  });

  appendBasicComponent = function(context, attrs) {
    Ember['default'].run((function(_this) {
      return function() {
        context.component = context.subject();
        return context.component.setProperties(attrs);
      };
    })(this));
    return context.append();
  };

  ember_qunit.test("clicking the X should send 'removeFlow'", function() {
    var componentAttrs, targetObject;
    targetObject = {
      externalAction: function(flow) {
        return ok(true);
      }
    };
    componentAttrs = {
      removeFlow: 'externalAction',
      targetObject: targetObject,
      flow: Ember['default'].Object.create({
        title: "Fake Title"
      })
    };
    appendBasicComponent(this, componentAttrs);
    return click('.remove-column');
  });

  ember_qunit.test('it forwards viewCard', function() {
    var component, componentAttrs, targetObject;
    targetObject = {
      externalAction: function(card) {
        return equal(card, "test");
      }
    };
    componentAttrs = {
      viewCard: 'externalAction',
      targetObject: targetObject
    };
    component = this.subject(componentAttrs);
    return component.send('viewCard', 'test');
  });

});