define('tahi/controllers/base-paper', ['exports', 'ember', 'tahi/services/document-download'], function (exports, Ember, DocumentDownload) {

  'use strict';

  var BasePaperController;

  BasePaperController = Ember['default'].ObjectController.extend({
    needs: ['application'],
    currentUser: Ember['default'].computed.alias('controllers.application.currentUser'),
    isAdmin: Ember['default'].computed.alias('controllers.application.isAdmin'),
    downloadLink: (function() {
      return "/papers/" + (this.get('id')) + "/download";
    }).property('id'),
    paper: Ember['default'].computed.alias('model'),
    logoUrl: (function() {
      var logoUrl;
      logoUrl = this.get('model.journal.logoUrl');
      if (/default-journal-logo/.test(logoUrl)) {
        return false;
      } else {
        return logoUrl;
      }
    }).property(),
    authorTasks: Ember['default'].computed.filterBy('tasks', 'role', 'author'),
    canViewManuscriptManager: false,
    assignedTasks: (function() {
      var assignedTasks, authorTasks;
      assignedTasks = this.get('tasks').filter((function(_this) {
        return function(task) {
          return task.get('participations').mapBy('user').contains(_this.currentUser);
        };
      })(this));
      authorTasks = this.get('authorTasks');
      return assignedTasks.filter(function(t) {
        return !authorTasks.contains(t);
      });
    }).property('tasks.@each'),
    editorTasks: (function() {
      if (this.get('model.editors').contains(this.get('currentUser'))) {
        return this.get('tasks').filterBy('role', 'reviewer');
      }
    }).property('tasks.@each.role'),
    hasNoMetaDataTasks: (function() {
      return Ember['default'].isEmpty(this.get('assignedTasks')) && Ember['default'].isEmpty(this.get('editorTasks')) && Ember['default'].isEmpty(this.get('authorTasks'));
    }).property('assignedTasks.@each', 'editorTasks.@each', 'authorTasks.@each'),
    actions: {
      "export": function(downloadType) {
        return DocumentDownload['default'].initiate(this.get('id'), downloadType.format);
      }
    }
  });

  exports['default'] = BasePaperController;

});