define('tahi/models/plos-author', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var a = DS['default'].attr;

  exports['default'] = DS['default'].Model.extend({
    paper: DS['default'].belongsTo("paper"),
    plosAuthorsTask: DS['default'].belongsTo("plosAuthorsTask"),

    qualifiedType: "PlosAuthors::PlosAuthor",

    firstName: a("string"),
    middleInitial: a("string"),
    lastName: a("string"),
    email: a("string"),
    title: a("string"),
    department: a("string"),
    affiliation: a("string"),
    secondaryAffiliation: a("string"),
    corresponding: a("boolean"),
    deceased: a("boolean"),
    position: a("number"),

    fullName: (function () {
      return [this.get("firstName"), this.get("middleInitial"), this.get("lastName")].compact().join(" ");
    }).property("firstName", "middleInitial", "lastName")
  });

});