define('tahi/tests/integration/paper-manage-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/mock-server', '../helpers/setups', '../helpers/factory'], function (Ember, ember_qunit, startApp, setupMockServer, setups, Factory) {

  'use strict';

  var app, server;

  app = null;

  server = null;

  module('Integration: Paper Manage page', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var taskPayload;
      Factory['default'].resetFactoryIds();
      app = startApp['default']();
      server = setupMockServer['default']();
      taskPayload = {
        task: {
          id: 1,
          title: "New Ad-Hoc Task",
          type: "Task",
          phase_id: 1,
          paper_id: 1,
          lite_paper_id: 1
        }
      };
      server.respondWith('GET', /\/papers\/\d+\/manuscript_manager/, [204, {}, ""]);
      server.respondWith('GET', "/papers/1", [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(setups.paperWithParticipant().toJSON())
      ]);
      server.respondWith('POST', "/tasks", [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(taskPayload)
      ]);
      return server.respondWith('DELETE', "/tasks/1", [
        200, {
          "Content-Type": "application/json"
        }, '{}'
      ]);
    }
  });

  ember_qunit.test('show delete confirmation overlay on deletion of a Task', function() {
    visit('/papers/1/manage');
    andThen(function() {
      $("div.card .card-remove").show();
      return click("div.card .card-remove");
    });
    return andThen(function() {
      equal(find('h1:contains("You\'re about to delete this card forever")').length, 1);
      equal(find('h1:contains("Are you sure?")').length, 1);
      equal(find('.overlay button:contains("cancel")').length, 1);
      return equal(find('.overlay button:contains("Yes, Delete this Card")').length, 1);
    });
  });

  ember_qunit.test('click delete confirmation overlay cancel button', function() {
    visit('/papers/1/manage');
    return andThen(function() {
      equal(find(".card-content").length, 1);
      $("div.card .card-remove").show();
      click("div.card .card-remove");
      click('.overlay button:contains("cancel")');
      return equal(find(".card-content").length, 1);
    });
  });

  ember_qunit.test('click delete confirmation overlay submit button', function() {
    visit('/papers/1/manage');
    andThen(function() {
      equal(find(".card-content").length, 1, "card exists");
      $("div.card .card-remove").show();
      click("div.card .card-remove");
      return click('.overlay button:contains("Yes, Delete this Card")');
    });
    return andThen(function() {
      var req;
      equal(find(".card-content").length, 0, "card deleted");
      req = _.findWhere(server.requests, {
        method: "DELETE",
        url: "/tasks/1"
      });
      return equal(req.status, 200, "It sends DELETE request to the server");
    });
  });

});