define('tahi/models/journal', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Journal, a;

  a = DS['default'].attr;

  Journal = DS['default'].Model.extend({
    reviewers: DS['default'].hasMany('user'),
    logoUrl: a('string'),
    name: a('string'),
    paperTypes: a(),
    manuscriptCss: a('string')
  });

  exports['default'] = Journal;

});