define('tahi/tests/integration/event-stream-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/factory', 'tahi/services/event-stream'], function (Ember, ember_qunit, startApp, Factory, EventStream) {

  'use strict';

  var app, phaseHasTask, setupEventStream, taskBelongsToPhase;

  app = null;

  setupEventStream = function() {
    var es, store;
    store = getStore();
    es = EventStream['default'].create({
      store: store,
      init: function() {}
    });
    return [es, store];
  };

  phaseHasTask = function(store, _arg) {
    var phase, phaseId, task, taskId;
    phaseId = _arg.phaseId, taskId = _arg.taskId;
    phase = store.getById('phase', phaseId);
    task = store.getById('task', taskId);
    return phase.get('tasks').contains(task);
  };

  taskBelongsToPhase = function(store, _arg) {
    var phase, phaseId, task, taskId;
    phaseId = _arg.phaseId, taskId = _arg.taskId;
    phase = store.getById('phase', phaseId);
    task = store.getById('task', taskId);
    return task.get('phase') === phase;
  };

  module('Integration: EventStream', {
    teardown: function() {
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      app = startApp['default']();
      return 1 + 1;
    }
  });

  ember_qunit.test('action:created without a task will put the payload in the store', function() {
    var data, es, store, _ref;
    expect(1);
    _ref = setupEventStream(), es = _ref[0], store = _ref[1];
    data = {
      action: 'created',
      meta: null,
      comment: {
        id: 1,
        body: "HEY"
      }
    };
    return Ember['default'].run((function(_this) {
      return function() {
        var comment;
        es.msgResponse(data);
        comment = store.getById('comment', 1);
        return equal(comment.get('body'), "HEY", "it puts the correct payload in the store");
      };
    })(this));
  });

  ember_qunit.test('action:created will still overwrite existing models', function() {
    var data, es, store, _ref;
    expect(1);
    _ref = setupEventStream(), es = _ref[0], store = _ref[1];
    data = {
      action: 'created',
      meta: null,
      comment: {
        id: 1,
        body: "NEW"
      }
    };
    return Ember['default'].run((function(_this) {
      return function() {
        var comment;
        store.push('comment', {
          id: 1,
          body: "OLD"
        });
        es.msgResponse(data);
        comment = store.getById('comment', 1);
        return equal(comment.get('body'), "NEW", "it overrides the current state");
      };
    })(this));
  });

  ember_qunit.test('action:destroy will delete the task from the store', function() {
    var data, es, store, _ref;
    expect(2);
    _ref = setupEventStream(), es = _ref[0], store = _ref[1];
    data = {
      action: 'destroyed',
      type: "tasks",
      ids: [1]
    };
    return Ember['default'].run((function(_this) {
      return function() {
        store.push('task', {
          id: 1
        });
        store.push('task', {
          id: 2
        });
        es.msgResponse(data);
        ok(store.getById('task', 1) === null);
        return ok(store.getById('task', 2) !== null);
      };
    })(this));
  });

  ember_qunit.test("action:created with a task updates the phase's tasks", function() {
    var data, es, store, _ref;
    expect(2);
    _ref = setupEventStream(), es = _ref[0], store = _ref[1];
    data = {
      action: 'created',
      task: {
        id: 10,
        phase_id: 1
      }
    };
    return Ember['default'].run((function(_this) {
      return function() {
        store.push('phase', {
          id: 1,
          title: 'A Phase'
        });
        es.msgResponse(data);
        ok(phaseHasTask(store, {
          phaseId: 1,
          taskId: 10
        }));
        return ok(taskBelongsToPhase(store, {
          phaseId: 1,
          taskId: 10
        }));
      };
    })(this));
  });

  ember_qunit.test("action:updated with a task updates the phase's tasks", function() {
    var data, es, originalTaskPayload, store, _ref;
    expect(6);
    _ref = setupEventStream(), es = _ref[0], store = _ref[1];
    originalTaskPayload = {
      task: {
        id: 10,
        phase_id: 1
      },
      phases: [
        {
          id: 1,
          title: "Phase 1",
          tasks: [
            {
              id: 10,
              type: "task"
            }
          ]
        }
      ]
    };
    data = {
      action: 'updated',
      task: {
        id: 10,
        phase_id: 2
      }
    };
    return Ember['default'].run((function(_this) {
      return function() {
        store.push('phase', {
          id: 2,
          title: 'Phase 2'
        });
        store.pushPayload('task', originalTaskPayload);
        ok(phaseHasTask(store, {
          phaseId: 1,
          taskId: 10
        }), "phase should have task");
        ok(taskBelongsToPhase(store, {
          phaseId: 1,
          taskId: 10
        }), "task should belong to phase");
        es.msgResponse(data);
        ok(phaseHasTask(store, {
          phaseId: 2,
          taskId: 10
        }), "new phase should have task");
        ok(taskBelongsToPhase(store, {
          phaseId: 2,
          taskId: 10
        }), "task should belong to new phase");
        ok(!phaseHasTask(store, {
          phaseId: 1,
          taskId: 10
        }), "old phase should not have task");
        return ok(!taskBelongsToPhase(store, {
          phaseId: 1,
          taskId: 10
        }), "task should not belong to old phase");
      };
    })(this));
  });

});