define('tahi/controllers/overlays/user-detail', ['exports', 'ember', 'tahi/mixins/validation-errors'], function (exports, Ember, ValidationErrorsMixin) {

  'use strict';

  var UserDetailOverlayController;

  UserDetailOverlayController = Ember['default'].ObjectController.extend(ValidationErrorsMixin['default'], {
    overlayClass: 'overlay--fullscreen user-detail-overlay',
    resetPasswordSuccess: false,
    resetPasswordFailure: false,
    actions: {
      saveUser: function() {
        return this.get('model').save().then((function(_this) {
          return function() {
            _this.clearValidationErrors();
            return _this.send('closeOverlay');
          };
        })(this))["catch"]((function(_this) {
          return function(response) {
            return _this.displayValidationErrorsFromResponse(response);
          };
        })(this));
      },
      rollbackUser: function() {
        this.get('model').rollback();
        this.clearValidationErrors();
        return this.send('closeOverlay');
      },
      resetPassword: function(user) {
        return $.get("/admin/journal_users/" + (user.get('id')) + "/reset").done((function(_this) {
          return function() {
            return _this.set('resetPasswordSuccess', true);
          };
        })(this)).fail((function(_this) {
          return function() {
            return _this.set('resetPasswordFailure', true);
          };
        })(this));
      }
    }
  });

  exports['default'] = UserDetailOverlayController;

});