define('tahi/mixins/models/card-thumbnail-observer', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var CardThumbnailObserver;

  CardThumbnailObserver = Ember['default'].Mixin.create({
    createThumbnail: (function() {
      var thumbnailParams;
      thumbnailParams = this.getProperties('id', 'completed', 'title', 'litePaper');
      thumbnailParams.taskType = this.get('type');
      this.store.push('cardThumbnail', thumbnailParams);
      return this.setThumbnailRelationship();
    }).on('didCreate'),
    updateThumbnail: (function() {
      var thumbnail;
      thumbnail = this.store.getById('cardThumbnail', this.get('id'));
      if (thumbnail) {
        return thumbnail.set('completed', this.get('completed'));
      }
    }).on('didUpdate'),
    deleteThumbnail: (function() {
      var thumbnail;
      thumbnail = this.store.getById('cardThumbnail', this.get('id'));
      if (thumbnail) {
        return thumbnail.deleteRecord();
      }
    }).on('didDelete'),
    upsertThumbnail: (function() {
      if (this.store.hasRecordForId('cardThumbnail', this.get('id'))) {
        this.updateThumbnail();
      } else {
        this.createThumbnail();
      }
      return this.setThumbnailRelationship();
    }).on('didLoad'),
    setThumbnailRelationship: function() {
      var thumbnail;
      thumbnail = this.store.getById('cardThumbnail', this.get('id'));
      return this.set('cardThumbnail', thumbnail);
    }
  });

  exports['default'] = CardThumbnailObserver;

});