define('tahi/pods/components/card-preview/component', ['exports', 'ember', 'tahi/services/drag-n-drop'], function (exports, Ember, DragNDrop) {

  'use strict';

  var CardPreviewComponent;

  CardPreviewComponent = Ember['default'].Component.extend(DragNDrop['default'].DraggableMixin, {
    classNameBindings: [':card', 'task.completed:card--completed', 'classes'],
    paper: null,
    commentLooks: Ember['default'].computed.oneWay('defaultCommentLooks'),
    task: null,
    canRemoveCard: false,
    canDragCard: false,
    classes: '',
    defaultCommentLooks: [],
    unreadCommentsCount: (function() {
      var taskId;
      taskId = this.get('task.id');
      return this.get('commentLooks').filter(function(look) {
        return look.get('taskId') === taskId && Em.isEmpty(look.get('readAt'));
      }).get('length');
    }).property('commentLooks.[]', 'commentLooks.@each.taskId', 'task.id', 'commentLooks.@each.readAt'),
    setupTooltip: (function() {
      return this.$().find('.card-remove').tooltip();
    }).on('didInsertElement'),
    dragStart: function(e) {
      if (this.get('canDragCard')) {
        return DragNDrop['default'].dragItem = this.get('task');
      }
    },
    actions: {
      viewCard: function(task) {
        return this.sendAction('action', task);
      },
      promptDelete: function(task) {
        return this.sendAction('showDeleteConfirm', task);
      }
    }
  });

  exports['default'] = CardPreviewComponent;

});