define('tahi/pods/components/toggle-paper-editable/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var TogglePaperEditableComponent;

  TogglePaperEditableComponent = Ember['default'].Component.extend({
    classNames: ['edit-paper-button button-primary'],
    classNameBindings: ['buttonColor'],
    click: function() {
      if (this.get('buttonState') !== 'disabled') {
        return this.sendAction();
      }
    },
    buttonState: (function() {
      if (!this.get('canEdit')) {
        return 'disabled';
      } else {
        if (this.get('isEditing')) {
          return 'isEditing';
        } else {
          return 'canEdit';
        }
      }
    }).property('canEdit', 'isEditing'),
    buttonStates: {
      isEditing: {
        buttonColor: 'button--green',
        prompt: 'stop writing',
        iconClass: ''
      },
      canEdit: {
        buttonColor: 'button--green',
        prompt: 'start writing',
        iconClass: 'glyphicon-pencil'
      },
      disabled: {
        buttonColor: 'button--disabled',
        prompt: 'start writing',
        iconClass: 'glyphicon-pencil'
      }
    },
    buttonColor: (function() {
      var state;
      state = this.get('buttonState');
      return this.get('buttonStates')[state].buttonColor;
    }).property('buttonState'),
    prompt: (function() {
      var state;
      state = this.get('buttonState');
      return this.get('buttonStates')[state].prompt;
    }).property('buttonState'),
    iconClass: (function() {
      var state;
      state = this.get('buttonState');
      return this.get('buttonStates')[state].iconClass;
    }).property('buttonState')
  });

  exports['default'] = TogglePaperEditableComponent;

});