define('tahi/pods/admin/journal/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var JournalRoute;

  JournalRoute = Ember['default'].Route.extend({
    model: function(params) {
      return this.store.find('adminJournal', params.journal_id);
    }
  });

  exports['default'] = JournalRoute;

});