define('tahi/views/overlays/supporting-information', ['exports', 'tahi/views/overlay'], function (exports, OverlayView) {

  'use strict';

  exports['default'] = OverlayView['default'].extend({
    templateName: "overlays/supporting-information",
    layoutName: "layouts/overlay",

    setupTooltip: (function () {
      this.$().find(".file-original-download-link").tooltip();
    }).on("didInsertElement")
  });

});