define('tahi/pods/admin/journal/index/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var JournalIndexRoute;

  JournalIndexRoute = Ember['default'].Route.extend({
    fetchAdminJournalUsers: function(journalId) {
      return this.store.find('AdminJournalUser', {
        journal_id: journalId
      }).then((function(_this) {
        return function(users) {
          return _this.set('controller.adminJournalUsers', users);
        };
      })(this));
    },
    setupController: function(controller, model) {
      this._super(controller, model);
      controller.set('doiStartNumberEditable', Ember['default'].isEmpty(model.get('lastDoiIssued')));
      return this.fetchAdminJournalUsers(model.get('id'));
    },
    deactivate: function() {
      this.set('controller.adminJournalUsers', null);
      return this.set('controller.doiEditState', false);
    },
    actions: {
      openEditOverlay: function(key) {
        this.controllerFor('overlays/adminJournal').setProperties({
          model: this.modelFor('admin/journal/index'),
          propertyName: key
        });
        return this.render("overlays/admin-journal-" + (key.dasherize()), {
          into: 'application',
          outlet: 'overlay',
          controller: 'overlays/adminJournal'
        });
      },
      editEPubCSS: function() {
        return this.send('openEditOverlay', 'epubCss');
      },
      editPDFCSS: function() {
        return this.send('openEditOverlay', 'pdfCss');
      },
      editManuscriptCSS: function() {
        return this.send('openEditOverlay', 'manuscriptCss');
      },
      editTaskTypes: function() {
        return this.render('overlays/editTaskTypes', {
          into: 'application',
          outlet: 'overlay',
          controller: 'overlays/editTaskTypes'
        });
      }
    }
  });

  exports['default'] = JournalIndexRoute;

});