define('tahi/tests/integration/flow-manager-admin-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/setups', '../helpers/mock-server', '../helpers/factory'], function (Ember, ember_qunit, startApp, setups, setupMockServer, Factory) {

  'use strict';

  var app, fakeUser, journal, server;

  app = null;

  server = null;

  fakeUser = null;

  journal = null;

  module('Integration: Flow Manager Administration', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var adminJournalPayload, adminRole;
      app = startApp['default']();
      server = setupMockServer['default']();
      journal = Factory['default'].createRecord('AdminJournal');
      adminRole = Factory['default'].createJournalRole(journal, {
        name: "Admin",
        kind: "admin",
        can_administer_journal: true,
        can_view_assigned_manuscript_managers: false,
        can_view_all_manuscript_managers: true,
        can_view_flow_manager: false
      });
      adminJournalPayload = Factory['default'].createPayload('adminJournal');
      adminJournalPayload.addRecords([journal, adminRole]);
      server.respondWith('GET', "/admin/journals/" + journal.id, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(adminJournalPayload.toJSON())
      ]);
      server.respondWith('GET', "/admin/journals/authorization", [
        204, {
          "Content-Type": "application/html"
        }, ""
      ]);
      server.respondWith('GET', '/user_flows/authorization', [
        204, {
          'content-type': 'application/html',
          'tahi-authorization-check': true
        }, ""
      ]);
      return server.respondWith('GET', "/admin/journal_users?journal_id=" + journal.id, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify({
          admin_journal_users: []
        })
      ]);
    }
  });

  ember_qunit.test('Flow manager edit link should show up on a role with permission in edit mode', function() {
    visit("/admin/journals/" + journal.id);
    click('.admin-role-action-button');
    andThen(function() {
      return ok(!find('a:contains("Edit Flows")').length, "No flow manager link should show up without permission");
    });
    click('input[name="role[canViewFlowManager]"]');
    return andThen(function() {
      return ok(find('a:contains("Edit Flows")').length);
    });
  });

});