define('tahi/models/author', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Author, a;

  a = DS['default'].attr;

  Author = DS['default'].Model.extend({
    paper: DS['default'].belongsTo('paper'),
    firstName: a('string'),
    lastName: a('string'),
    position: a('number'),
    fullName: (function() {
      return [this.get('firstName'), this.get('middleInitial'), this.get('lastName')].compact().join(' ');
    }).property('firstName', 'middleInitial', 'lastName')
  });

  exports['default'] = Author;

});