define('tahi/pods/dashboard/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var IndexController;

  IndexController = Ember['default'].ObjectController.extend({
    needs: ['application'],
    papers: null,
    unreadComments: [],
    currentUser: Ember['default'].computed.alias('controllers.application.currentUser'),
    hasPapers: Ember['default'].computed.notEmpty('papers'),
    relatedAtSort: ['relatedAtDate:desc'],
    sortedPapers: Ember['default'].computed.sort('papers', 'relatedAtSort'),
    pageNumber: 1,
    paginate: (function() {
      return this.get('pageNumber') !== this.get('totalPageCount');
    }).property('totalPageCount', 'pageNumber'),
    actions: {
      loadMorePapers: function() {
        return this.store.find('lite_paper', {
          page_number: this.get('pageNumber') + 1
        }).then((function(_this) {
          return function(litePapers) {
            return _this.incrementProperty('pageNumber');
          };
        })(this));
      }
    }
  });

  exports['default'] = IndexController;

});