define('tahi/tests/integration/feedback-form-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/setups', '../helpers/mock-server'], function (Ember, ember_qunit, startApp, setups, setupMockServer) {

  'use strict';

  var app, fakeUser, server;

  app = null;

  server = null;

  fakeUser = null;

  module('Integration: Feedback Form', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var dashboardResponse;
      app = startApp['default']();
      server = setupMockServer['default']();
      fakeUser = window.currentUser.user;
      dashboardResponse = {
        users: [fakeUser],
        dashboards: [
          {
            id: 1,
            user_id: 1,
            paper_ids: [],
            total_paper_count: 1,
            total_page_count: 1
          }
        ]
      };
      server.respondWith('GET', '/dashboards', [
        200, {
          'Content-Type': 'application/json'
        }, JSON.stringify(dashboardResponse)
      ]);
      server.respondWith('GET', "/admin/journals/authorization", [
        204, {
          "Content-Type": "application/html"
        }, ""
      ]);
      return server.respondWith('POST', "/feedback", [
        201, {
          "Content-Type": "application/json"
        }, "{}"
      ]);
    }
  });

  ember_qunit.test('clicking the feedback button sends feedback to the backend', function() {
    return visit('/').then(function() {
      return click('.navigation-toggle');
    }).andThen(function() {
      click('.navigation-item-feedback');
      return ok(find('.overlay').length);
    }).andThen(function() {
      fillIn('.overlay textarea', "My feedback");
      return click('.overlay-footer .button-primary');
    }).andThen(function() {
      return ok(find('.overlay .thanks'));
    });
  });

});