define('tahi/tests/helpers/custom-assertions', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var customAssertions;

  customAssertions = (function() {
    Ember['default'].Test.registerHelper('assertText', function(app, selector, text) {
      return ok(Em.$.trim(find(selector).text()).indexOf(text) !== -1, "it should have text: " + text + " within " + selector);
    });
    return Ember['default'].Test.registerHelper('assertNoText', function(app, selector, text) {
      return ok(Em.$.trim(find(selector).text()).indexOf(text) === -1, "it should not have text: " + text + " within " + selector);
    });
  })();

  exports['default'] = customAssertions;

});