define('tahi/tests/controllers/journal-role-flow-manager-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleFor('controller:admin/journal/flow-manager', 'Unit: controller/journalFlowManager');

  ember_qunit.test('newFlowPosition puts the new flow at the highest position', function() {
    var controller, eo, flows, role;
    eo = Ember['default'].Object;
    flows = [
      eo.create({
        position: 1
      }), eo.create({
        position: 2
      })
    ];
    role = eo.create({
      flows: flows
    });
    controller = this.subject();
    controller.set('model', role);
    return equal(controller.newFlowPosition(), 3);
  });

});