define('tahi/pods/paper/edit/route', ['exports', 'ember', 'tahi/routes/authorized', 'tahi/mixins/routes/lazy-loader', 'tahi/services/rest-less', 'tahi/services/heartbeat'], function (exports, Ember, AuthorizedRoute, LazyLoader, RESTless, Heartbeat) {

  'use strict';

  var PaperEditRoute;

  PaperEditRoute = AuthorizedRoute['default'].extend({
    heartbeatService: null,
    beforeModel: function() {
      var visualEditorScript;
      visualEditorScript = '/visualEditor.min.js';
      if (!LazyLoader['default'].loaded[visualEditorScript]) {
        return $.getScript(visualEditorScript).then(function() {
          return LazyLoader['default'].loaded[visualEditorScript] = true;
        });
      }
    },
    model: function() {
      var paper;
      paper = this.modelFor('paper');
      return new Ember['default'].RSVP.Promise(function(resolve, reject) {
        return paper.get('tasks').then(function(tasks) {
          return resolve(paper);
        });
      });
    },
    afterModel: function(model) {
      if (model.get('editable')) {
        this.set('heartbeatService', Heartbeat['default'].create({
          resource: model
        }));
        return this.startHeartbeat();
      } else {
        return this.replaceWith('paper.index', model);
      }
    },
    setupController: function(controller, model) {
      controller.set('model', model);
      controller.set('commentLooks', this.store.all('commentLook'));
      if (this.currentUser) {
        return RESTless['default'].authorize(controller, "/papers/" + (model.get('id')) + "/manuscript_manager", 'canViewManuscriptManager');
      }
    },
    deactivate: function() {
      return this.endHeartbeat();
    },
    startHeartbeat: function() {
      if (this.isLockedByCurrentUser()) {
        return this.get('heartbeatService').start();
      }
    },
    endHeartbeat: function() {
      return this.get('heartbeatService').stop();
    },
    isLockedByCurrentUser: function() {
      var lockedBy;
      lockedBy = this.modelFor('paper').get('lockedBy');
      return lockedBy && lockedBy === this.currentUser;
    },
    actions: {
      viewCard: function(task) {
        var paper, redirectParams;
        paper = this.modelFor('paper');
        redirectParams = ['paper.edit', this.modelFor('paper')];
        this.controllerFor('application').get('overlayRedirect').pushObject(redirectParams);
        this.controllerFor('application').set('overlayBackground', 'paper/edit');
        return this.transitionTo('task', paper.id, task.id);
      },
      startEditing: function() {
        return this.startHeartbeat();
      },
      stopEditing: function() {
        return this.endHeartbeat();
      },
      addContributors: function() {
        var collaborations, controller, paper;
        paper = this.modelFor('paper');
        collaborations = paper.get('collaborations') || [];
        controller = this.controllerFor('overlays/showCollaborators');
        controller.setProperties({
          paper: paper,
          collaborations: collaborations,
          initialCollaborations: collaborations.slice(),
          allUsers: this.store.find('user')
        });
        return this.render('overlays/showCollaborators', {
          into: 'application',
          outlet: 'overlay',
          controller: controller
        });
      },
      showConfirmSubmitOverlay: function() {
        return this.render('overlays/paperSubmit', {
          into: 'application',
          outlet: 'overlay',
          controller: 'overlays/paperSubmit'
        });
      },
      editableDidChange: function() {
        return this.replaceWith('paper.index', this.modelFor('paper'));
      }
    }
  });

  exports['default'] = PaperEditRoute;

});