define('tahi/pods/components/content-editable/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ContentEditableComponent;

  ContentEditableComponent = Ember['default'].Component.extend({
    attributeBindings: ['contenteditable', 'placeholder'],
    editable: true,
    placeholder: "",
    plaintext: false,
    preventEnterKey: false,
    _userIsTyping: false,
    setup: (function() {
      this.setHTMLFromValue();
      if (this.elementIsEmpty() && this.get('placeholder')) {
        return this.setPlaceholder();
      }
    }).on('didInsertElement'),
    contenteditable: (function() {
      if (this.get('editable')) {
        return 'true';
      } else {
        return undefined;
      }
    }).property('editable'),
    valueDidChange: (function() {
      if (this.get('value') && !this.get('_userIsTyping')) {
        return this.setHTMLFromValue();
      }
    }).observes('value'),
    keyDown: function(event) {
      this.set('_userIsTyping', true);
      if (this.get('preventEnterKey')) {
        this.supressEnterKeyEvent(event);
      }
      if (this.elementHasPlaceholder()) {
        return this.removePlaceholder();
      }
    },
    keyUp: function(event) {
      if (this.elementIsEmpty() || this.elementHasPlaceholder()) {
        this.set('value', '');
        this.setPlaceholder();
        return;
      }
      return this.setValueFromHTML();
    },
    focusOut: function() {
      this.set('_userIsTyping', false);
      if (this.elementIsEmpty()) {
        return this.setPlaceholder();
      }
    },
    elementIsEmpty: function() {
      return Em.isEmpty(this.$().text());
    },
    elementHasPlaceholder: function() {
      return this.$().text() === this.get('placeholder');
    },
    setPlaceholder: function() {
      this.$().text(this.get('placeholder'));
      return this.mute();
    },
    removePlaceholder: function() {
      this.$().text('');
      return this.unmute();
    },
    setHTMLFromValue: function() {
      this.$().html(this.get('value'));
      return this.unmute();
    },
    mute: function() {
      return this.$().addClass('content-editable-muted');
    },
    unmute: function() {
      return this.$().removeClass('content-editable-muted');
    },
    setValueFromHTML: function() {
      if (this.get('plaintext')) {
        return this.set('value', this.$().text());
      } else {
        return this.set('value', this.$().html());
      }
    },
    supressEnterKeyEvent: function(e) {
      if (e.keyCode === 13 || e.which === 13) {
        return e.preventDefault();
      }
    }
  });

  exports['default'] = ContentEditableComponent;

});