define('tahi/mixins/controllers/select-2-assignees', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var Select2Assignees;

  Select2Assignees = Ember['default'].Mixin.create({
    select2RemoteSource: (function() {
      return {
        url: this.get('select2RemoteUrl'),
        dataType: "json",
        quietMillis: 500,
        data: function(term) {
          return {
            query: term
          };
        },
        results: function(data) {
          return {
            results: data.filtered_users
          };
        }
      };
    }).property('select2RemoteUrl'),
    resultsTemplate: function(user) {
      return user.full_name;
    },
    selectedTemplate: function(user) {
      return user.full_name || user.get('fullName');
    }
  });

  exports['default'] = Select2Assignees;

});