define('tahi/pods/components/select-2-single/component', ['exports', 'tahi/pods/components/select-2/component'], function (exports, Select2Component) {

  'use strict';

  var Select2SingleComponent;

  Select2SingleComponent = Select2Component['default'].extend({
    setSelectedData: (function() {
      return this.$().select2('val', this.get('selectedData'));
    }).observes('selectedData'),
    initSelection: function(el, callback) {
      return callback(this.get('selectedData'));
    }
  });

  exports['default'] = Select2SingleComponent;

});