define('tahi/views/control-bar', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ControlBarView;

  ControlBarView = Ember['default'].View.extend({
    classNames: ['control-bar'],
    controlBarSelector: '.control-bar',
    setupScroll: (function() {
      var controlBar;
      controlBar = $(this.get('controlBarSelector'));
      return $(window).on('scroll.controlBar', function() {
        var scrolledAmount;
        scrolledAmount = $(this).scrollTop() - 30;
        return controlBar.css('backgroundColor', 'rgba(255,255,255, ' + (scrolledAmount >= 90 ? 0.9 : scrolledAmount / 100), +')');
      });
    }).on('didInsertElement'),
    teardownScroll: (function() {
      return $(window).off('scroll.controlBar');
    }).on('willDestroyElement')
  });

  exports['default'] = ControlBarView;

});