define('tahi/views/decision-radio-button', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DecisionRadioButtonComponent;

  DecisionRadioButtonComponent = Ember['default'].Component.extend({
    tagName: 'input',
    type: 'radio',
    attributeBindings: ['name', 'type', 'value', 'checked:checked'],
    checked: (function() {
      return this.get('selection') === this.get('value');
    }).property('selection', 'value'),
    click: function() {
      return this.sendAction('action', this.get('value'));
    }
  });

  exports['default'] = DecisionRadioButtonComponent;

});