define('tahi/models/feedback', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Feedback;

  Feedback = DS['default'].Model.extend({
    remarks: DS['default'].attr('string'),
    referrer: DS['default'].attr('string'),
    screenshots: DS['default'].attr()
  });

  exports['default'] = Feedback;

});