define('tahi/controllers/overlays/paper-new', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var PaperNewOverlayController;

  PaperNewOverlayController = Ember['default'].ObjectController.extend({
    overlayClass: 'overlay--fullscreen paper-new-overlay',
    noJournalSelected: Ember['default'].computed.not('model.journal'),
    journalDidChange: (function() {
      return this.set('model.paperType', this.get('model.journal.paperTypes.firstObject'));
    }).observes('model.journal'),
    actions: {
      createNewPaper: function() {
        return this.get('model').save().then((function(_this) {
          return function(paper) {
            _this.send('addPaperToEventStream', paper);
            return _this.transitionToRoute('paper.edit', paper);
          };
        })(this), (function(_this) {
          return function(response) {
            return _this.flash.displayErrorMessagesFromResponse(response);
          };
        })(this));
      }
    }
  });

  exports['default'] = PaperNewOverlayController;

});