define('tahi/pods/paper/manage/route', ['exports', 'ember', 'tahi/routes/authorized', 'tahi/services/utils'], function (exports, Ember, AuthorizedRoute, Utils) {

  'use strict';

  var PaperManageRoute;

  PaperManageRoute = AuthorizedRoute['default'].extend({
    afterModel: function(paper, transition) {
      var promise;
      return promise = new Ember['default'].RSVP.Promise(function(resolve, reject) {
        return Ember['default'].$.ajax({
          method: 'GET',
          url: "/papers/" + (paper.get('id')) + "/manuscript_manager",
          success: function(json) {
            return Ember['default'].run(null, resolve, json);
          },
          error: function(xhr, status, error) {
            return Ember['default'].run(null, reject, xhr);
          }
        });
      });
    },
    setupController: function(controller, model) {
      controller.set('model', model);
      controller.set('commentLooks', this.store.all('commentLook'));
      return controller.set('canRemoveCard', true);
    },
    actions: {
      chooseNewCardTypeOverlay: function(phase) {
        var chooseNewCardTypeOverlay;
        chooseNewCardTypeOverlay = this.controllerFor('overlays/chooseNewCardType');
        chooseNewCardTypeOverlay.set('phase', phase);
        this.store.find('adminJournal', phase.get('paper.journal.id')).then((function(_this) {
          return function(adminJournal) {
            return chooseNewCardTypeOverlay.set('journalTaskTypes', adminJournal.get('journalTaskTypes'));
          };
        })(this));
        return this.render('overlays/chooseNewCardType', {
          into: 'application',
          outlet: 'overlay',
          controller: chooseNewCardTypeOverlay
        });
      },
      viewCard: function(task, queryParams) {
        var paper, redirectParams;
        queryParams || (queryParams = {
          queryParams: {}
        });
        paper = this.modelFor('paper');
        redirectParams = ['paper.manage', this.modelFor('paper')];
        this.controllerFor('application').get('overlayRedirect').pushObject(redirectParams);
        this.controllerFor('application').set('overlayBackground', 'paper/manage');
        return this.transitionTo('task', paper.id, task.id, queryParams);
      },
      addTaskType: function(phase, taskType) {
        var unNamespacedKind;
        if (!taskType) {
          return;
        }
        unNamespacedKind = Utils['default'].deNamespaceTaskType(taskType.get('kind'));
        return this.store.createRecord(unNamespacedKind, {
          phase: phase,
          role: taskType.get('role'),
          type: taskType.get('kind'),
          paper: this.modelFor('paper'),
          title: taskType.get('title')
        }).save().then((function(_this) {
          return function(newTask) {
            return _this.send('viewCard', newTask, {
              queryParams: {
                isNewTask: true
              }
            });
          };
        })(this));
      },
      showDeleteConfirm: function(task) {
        return this.render('overlays/card-delete', {
          into: 'application',
          outlet: 'overlay',
          controller: 'overlays/card-delete',
          model: task
        });
      }
    }
  });

  exports['default'] = PaperManageRoute;

});