define('tahi/models/participation', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Participation;

  Participation = DS['default'].Model.extend({
    user: DS['default'].belongsTo('user'),
    task: DS['default'].belongsTo('task', {
      polymorphic: true
    })
  });

  exports['default'] = Participation;

});