define('tahi/tests/controllers/journal-index-test', ['ember', 'ember-qunit', '../helpers/start-app'], function (Ember, ember_qunit, startApp) {

  'use strict';

  ember_qunit.moduleFor('controller:admin/journal/index', 'JournalIndexController', {
    setup: function() {
      startApp['default']();
      this.journal = Ember['default'].Object.create({
        title: 'test journal'
      });
      this.journalWithLogo = Ember['default'].Object.create({
        title: 'test journal with logo',
        logoUrl: 'http://example.com/test.png'
      });
      return Ember['default'].run((function(_this) {
        return function() {
          return _this.controller = _this.subject();
        };
      })(this));
    }
  });

  ember_qunit.test('#logo return logoUrl if it exists else return Journal name', function() {
    this.controller.set('model', this.journal);
    equal(this.controller.get("logo"), void 0);
    return equal(this.controller.get("logoUrl"), void 0);
  });

  ember_qunit.test('#logo return logoUrl if it exists else return Journal name', function() {
    this.controller.set('model', this.journalWithLogo);
    equal(this.controller.get("logo"), this.journalWithLogo.logoUrl);
    return equal(this.controller.get("logoUrl"), this.journalWithLogo.logoUrl);
  });

});