define('tahi/pods/admin/journal-task-type/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var JournalTaskTypeController;

  JournalTaskTypeController = Ember['default'].ObjectController.extend({
    needs: ['admin/journal'],
    journal: Ember['default'].computed.alias('controllers.admin/journal'),
    journalRoleSort: ['name: asc'],
    availableTaskRoles: Ember['default'].computed.sort('journal.roles', 'journalRoleSort'),
    actions: {
      save: function() {
        return this.get('model').save().then()["catch"](function() {});
      },
      cancel: function() {
        if (this.get('model.isNew')) {
          return this.get('model').deleteRecord();
        } else {
          return this.get('model').rollback();
        }
      },
      "delete": function() {
        return this.send('deleteRole', this.get('model'));
      }
    }
  });

  exports['default'] = JournalTaskTypeController;

});