define('tahi/helpers/badge-count', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var BadgeCount;

  BadgeCount = Ember['default'].Handlebars.makeBoundHelper(function(count, classString) {
    if (count > 0) {
      return new Handlebars.SafeString("<span class='badge " + classString + "'>" + count + "</span>");
    } else {
      return new Handlebars.SafeString("");
    }
  });

  exports['default'] = BadgeCount;

});