define('tahi/models/task', ['exports', 'ember-data', 'tahi/mixins/models/card-thumbnail-observer'], function (exports, DS, CardThumbnailObserver) {

  'use strict';

  var Task, a;

  a = DS['default'].attr;

  Task = DS['default'].Model.extend(CardThumbnailObserver['default'], {
    attachments: DS['default'].hasMany('attachment'),
    cardThumbnail: DS['default'].belongsTo('card-thumbnail', {
      inverse: 'task'
    }),
    comments: DS['default'].hasMany('comment'),
    litePaper: DS['default'].belongsTo('lite-paper'),
    paper: DS['default'].belongsTo('paper', {
      inverse: 'tasks'
    }),
    participations: DS['default'].hasMany('participation'),
    phase: DS['default'].belongsTo('phase', {
      inverse: 'tasks'
    }),
    questions: DS['default'].hasMany('question', {
      inverse: 'task'
    }),
    body: a(),
    completed: a('boolean'),
    paperTitle: a('string'),
    role: a('string'),
    title: a('string'),
    type: a('string'),
    qualifiedType: a('string'),
    isMetadataTask: false
  });

  exports['default'] = Task;

});