define('tahi/controllers/overlays/edit-task-types', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var EditTaskTypesOverlayController;

  EditTaskTypesOverlayController = Ember['default'].Controller.extend({
    overlayClass: 'overlay--fullscreen edit-task-types-overlay',
    needs: ['admin/journal/index'],
    model: Ember['default'].computed.alias('controllers.admin/journal/index.model'),
    taskTypeSort: ['title:asc'],
    sortedTaskTypes: Ember['default'].computed.sort('model.journalTaskTypes', 'taskTypeSort')
  });

  exports['default'] = EditTaskTypesOverlayController;

});