define('tahi/models/register-decision-task', ['exports', 'ember-data', 'tahi/models/task'], function (exports, DS, Task) {

  'use strict';

  var RegisterDecisionTask, a;

  a = DS['default'].attr;

  RegisterDecisionTask = Task['default'].extend({
    decisionLetters: a('string'),
    paperDecision: a('string'),
    paperDecisionLetter: a('string'),
    acceptedLetterTemplate: (function() {
      return JSON.parse(this.get('decisionLetters')).Accepted;
    }).property('decisionLetters'),
    rejectedLetterTemplate: (function() {
      return JSON.parse(this.get('decisionLetters')).Rejected;
    }).property('decisionLetters'),
    reviseLetterTemplate: (function() {
      return JSON.parse(this.get('decisionLetters')).Revise;
    }).property('decisionLetters')
  });

  exports['default'] = RegisterDecisionTask;

});