define('tahi/pods/components/show-if-parent/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ShowIfParentComponent;

  ShowIfParentComponent = Ember['default'].Component.extend({
    showContent: Em.computed.oneWay('initialShowState'),
    initialShowState: (function() {
      var prop;
      prop = this.get('propName');
      return this.get(prop);
    }).property('parentView'),
    prop: "",
    propName: (function() {
      return "parentView." + (this.get('prop'));
    }).property('prop'),
    showPropDidChange: function(sender, key) {
      return this.set('showContent', sender.get(key));
    },
    setupObserver: (function() {
      return this.addObserver(this.get('propName'), this, this.showPropDidChange);
    }).on('didInsertElement'),
    removeObserver: (function() {
      return Ember['default'].removeObserver(this, this.get('propName'), this, this.showPropDidChange);
    }).on('willDestroyElement')
  });

  exports['default'] = ShowIfParentComponent;

});