define('tahi/tests/integration/navbar-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/mock-server', '../helpers/factory'], function (Ember, ember_qunit, startApp, setupMockServer, Factory) {

  'use strict';

  var app, respondAuthorized, respondUnauthorized, server, setCurrentUserAdmin;

  app = null;

  server = null;

  module('Integration: Navbar', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var dashboardResponse;
      app = startApp['default']();
      server = setupMockServer['default']();
      dashboardResponse = {
        dashboards: [
          {
            id: 1
          }
        ]
      };
      return server.respondWith('GET', '/dashboards', [
        200, {
          'Content-Type': 'application/json'
        }, JSON.stringify(dashboardResponse)
      ]);
    }
  });

  respondAuthorized = function() {
    var adminJournalsResponse;
    adminJournalsResponse = {
      admin_journals: [
        {
          id: 1,
          name: "Test Journal of America"
        }
      ]
    };
    server.respondWith('GET', '/admin/journals', [
      200, {
        'Content-Type': 'application/json'
      }, JSON.stringify(adminJournalsResponse)
    ]);
    server.respondWith('GET', "/admin/journals/authorization", [
      204, {
        "Content-Type": "application/html"
      }, ""
    ]);
    return server.respondWith('GET', '/user_flows/authorization', [
      204, {
        'content-type': 'application/html'
      }, ""
    ]);
  };

  respondUnauthorized = function() {
    server.respondWith('GET', '/admin/journals/authorization', [
      403, {
        'content-type': 'application/html'
      }, ""
    ]);
    return server.respondWith('GET', '/user_flows/authorization', [
      403, {
        'content-type': 'application/html'
      }, ""
    ]);
  };

  setCurrentUserAdmin = function(bool) {
    var store;
    store = getStore();
    return store.find('user', getCurrentUser().get('id')).then(function(currentUser) {
      return currentUser.set('admin', bool);
    });
  };

  ember_qunit.test('all users can see their username', function() {
    respondUnauthorized();
    setCurrentUserAdmin(false);
    visit('/');
    click('.navigation-toggle');
    return andThen(function() {
      return equal(find(".navigation-item--account span:contains('Fake User')").length, 1);
    });
  });

  ember_qunit.test('(200 response) can see the Flow Manager link', function() {
    respondAuthorized();
    visit('/');
    click('.navigation-toggle');
    return andThen(function() {
      return equal(find(".navigation:contains('Flow Manager')").length, 1);
    });
  });

  ember_qunit.test('(403 response) cannot see the Flow Manager link', function() {
    respondUnauthorized();
    visit('/');
    click('.navigation-toggle');
    return andThen(function() {
      return equal(find(".navigation:contains('Flow Manager')").length, 0);
    });
  });

  ember_qunit.test('(200 response) can see the Admin link', function() {
    respondAuthorized();
    visit('/');
    click('.navigation-toggle');
    return andThen(function() {
      return equal(find(".navigation:contains('Admin')").length, 1);
    });
  });

  ember_qunit.test('(200 response) can see the Flow Manager link', function() {
    respondAuthorized();
    visit('/');
    click('.navigation-toggle');
    return andThen(function() {
      return equal(find(".navigation:contains('Flow Manager')").length, 1);
    });
  });

  ember_qunit.test('(403 response) cannot see the Admin link', function() {
    respondUnauthorized();
    visit('/');
    click('.navigation-toggle');
    return andThen(function() {
      return equal(find(".navigation:contains('Admin')").length, 0);
    });
  });

  ember_qunit.test('(403 response) cannot see the Flow Manager link', function() {
    respondUnauthorized();
    visit('/');
    click('.navigation-toggle');
    return andThen(function() {
      return equal(find(".navigation:contains('Flow Manager')").length, 0);
    });
  });

});