define('tahi/tests/components/select-2-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/mock-server'], function (Ember, ember_qunit, startApp, setupMockServer) {

  'use strict';

  var appendBasicComponent, fillInDropdown, selectObjectFromDropdown, server;

  server = null;

  ember_qunit.moduleForComponent('select-2', 'Unit: components/select-2', {
    setup: function() {
      startApp['default']();
      server = setupMockServer['default']();
      return server.respondWith('GET', /filtered_objects.*/, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify([
          {
            id: 1,
            text: 'Aaron'
          }
        ])
      ]);
    },
    teardown: function() {
      return server.restore();
    }
  });

  fillInDropdown = function(object) {
    keyEvent('.select2-container input', 'keydown');
    fillIn('.select2-container input', object);
    return keyEvent('.select2-container input', 'keyup');
  };

  selectObjectFromDropdown = function(object) {
    fillInDropdown(object);
    return click('.select2-result-selectable', 'body');
  };

  appendBasicComponent = function(context) {
    Ember['default'].run((function(_this) {
      return function() {
        context.component = context.subject();
        return context.component.setProperties({
          multiSelect: true,
          source: [
            {
              id: 1,
              text: '1'
            }, {
              id: 2,
              text: '2'
            }, {
              id: 3,
              text: '3'
            }
          ]
        });
      };
    })(this));
    return context.append();
  };

  ember_qunit.test("User can make a selection from the dropdown", function() {
    appendBasicComponent(this);
    selectObjectFromDropdown('1');
    return andThen(function() {
      return ok($('.select2-container').select2('val').contains("1"));
    });
  });

  ember_qunit.test("User can remove a selection from the dropdown", function() {
    appendBasicComponent(this);
    this.component.setProperties({
      selectedData: [
        {
          id: 1,
          text: '1'
        }
      ]
    });
    ok($('.select2-container').select2('val').contains("1"));
    click('.select2-search-choice-close');
    return ok(!$('.select2-container').select2('val').contains("1"));
  });

  ember_qunit.test("Making a selection should trigger a callback to add the object", function() {
    var targetObject;
    appendBasicComponent(this);
    targetObject = {
      externalAction: function(choice) {
        return equal(choice.id, '1');
      }
    };
    this.component.set('selectionSelected', 'externalAction');
    this.component.set('targetObject', targetObject);
    return selectObjectFromDropdown('1');
  });

  ember_qunit.test("Removing a selection should trigger a callback to remove the object", function() {
    var targetObject;
    appendBasicComponent(this);
    this.component.setProperties({
      selectedData: [
        {
          id: 1,
          text: '1'
        }
      ]
    });
    targetObject = {
      externalAction: function(choice) {
        return equal(choice.id, '1');
      }
    };
    this.component.set('selectionRemoved', 'externalAction');
    this.component.set('targetObject', targetObject);
    return click('.select2-search-choice-close');
  });

  ember_qunit.test("Typing more than 3 letters with a remote url should make a call to said remote url", function() {
    Ember['default'].run((function(_this) {
      return function() {
        _this.component = _this.subject();
        return _this.component.setProperties({
          multiSelect: true,
          source: [],
          remoteSource: {
            url: "filtered_objects",
            dataType: "json",
            data: function(term) {
              return {
                query: term
              };
            },
            results: function(data) {
              return {
                results: data
              };
            }
          }
        });
      };
    })(this));
    this.append();
    keyEvent('.select2-container input', 'keydown');
    fillIn('.select2-container input', 'Aaron');
    keyEvent('.select2-container input', 'keyup');
    waitForElement('.select2-result-selectable');
    return andThen(function() {
      return ok(find('.select2-result-selectable', 'body').length);
    });
  });

  ember_qunit.test("Event stream object added should add the object to the selected objects in the dropdown", function() {
    Ember['default'].run((function(_this) {
      return function() {
        _this.component = _this.subject();
        return _this.component.setProperties({
          multiSelect: true,
          source: [
            {
              id: 1,
              text: '1'
            }, {
              id: 2,
              text: '2'
            }, {
              id: 3,
              text: '3'
            }
          ]
        });
      };
    })(this));
    this.append();
    ok(!$('.select2-container').select2('val').contains("4"));
    this.component.setProperties({
      selectedData: [
        {
          id: 4,
          text: '4'
        }
      ]
    });
    return ok($('.select2-container').select2('val').contains("4"));
  });

  ember_qunit.test("Event stream object removed should remove the object from the selected objects in the dropdown", function() {
    appendBasicComponent(this);
    this.component.setProperties({
      selectedData: [
        {
          id: 4,
          text: '4'
        }
      ]
    });
    ok($('.select2-container').select2('val').contains("4"));
    this.component.setProperties({
      selectedData: []
    });
    return ok(!$('.select2-container').select2('val').contains("4"));
  });

});