define('tahi/tests/routes/paper-index-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleFor('route:paper/index', 'Unit: route/PaperIndex', {
    needs: ['model:paper', 'route:paper'],
    setup: function() {
      return sinon.stub(this.subject(), 'replaceWith');
    }
  });

  ember_qunit.test('transition to edit route if paper is editable', function() {
    var editablePaper;
    editablePaper = Ember['default'].Object.create({
      editable: true
    });
    this.subject().afterModel(editablePaper);
    return ok(this.subject().replaceWith.calledWithExactly('paper.edit', editablePaper));
  });

  ember_qunit.test('does not transition to edit route if paper has been submitted', function() {
    var uneditablePaper;
    uneditablePaper = Ember['default'].Object.create({
      editable: false
    });
    this.subject().afterModel(uneditablePaper);
    return ok(!this.subject().replaceWith.called);
  });

});