define('tahi/pods/admin/journal/manuscript-manager-template/new/route', ['exports', 'ember', 'tahi/mixins/routes/alert-unsaved-changes'], function (exports, Ember, AlertUnsavedChanges) {

  'use strict';

  var ManuscriptManagerTemplateNewRoute;

  ManuscriptManagerTemplateNewRoute = Ember['default'].Route.extend(AlertUnsavedChanges['default'], {
    controllerName: 'admin.journal.manuscript_manager_template.edit',
    model: function(params) {
      var journal, newTemplate;
      journal = this.modelFor('admin.journal');
      newTemplate = this.store.createRecord('manuscriptManagerTemplate', {
        journal: journal,
        paperType: "Research"
      });
      newTemplate.get('phaseTemplates').pushObject(this.store.createRecord('phaseTemplate', {
        name: "Phase 1",
        position: 1
      }));
      newTemplate.get('phaseTemplates').pushObject(this.store.createRecord('phaseTemplate', {
        name: "Phase 2",
        position: 2
      }));
      newTemplate.get('phaseTemplates').pushObject(this.store.createRecord('phaseTemplate', {
        name: "Phase 3",
        position: 3
      }));
      this.set('journal', journal);
      this.set('newTemplate', newTemplate);
      return newTemplate;
    },
    setupController: function(controller, model) {
      controller.set('model', model);
      return controller.set('journal', this.modelFor('admin.journal'));
    },
    renderTemplate: function() {
      return this.render('admin/journal/manuscript_manager_template/edit');
    },
    actions: {
      didRollBack: function() {
        this.get('journal.manuscriptManagerTemplates').removeObject(this.get('newTemplate'));
        return this.transitionTo('admin.journal');
      }
    }
  });

  exports['default'] = ManuscriptManagerTemplateNewRoute;

});