define('tahi/pods/components/binary-radio-button/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var BinaryRadioButtonComponent;

  BinaryRadioButtonComponent = Ember['default'].Component.extend({
    selection: null,
    index: null,
    yesValue: true,
    noValue: false,
    yesLabel: 'Yes',
    noLabel: 'No',
    idYes: Ember['default'].computed('name', function() {
      return "" + (this.get('name')) + "-yes";
    }),
    idNo: Ember['default'].computed('name', function() {
      return "" + (this.get('name')) + "-no";
    }),
    yesChecked: (function() {
      return this.get('yesValue') === this.get('selection');
    }).property('selection', 'yesValue'),
    noChecked: (function() {
      return this.get('noValue') === this.get('selection');
    }).property('selection', 'noValue'),
    actions: {
      selectYes: function() {
        this.set('selection', this.get('yesValue'));
        return this.sendAction("yesAction");
      },
      selectNo: function() {
        this.set('selection', this.get('noValue'));
        return this.sendAction('noAction');
      }
    }
  });

  exports['default'] = BinaryRadioButtonComponent;

});