define('tahi/pods/admin/index/route', ['exports', 'tahi/routes/authorized'], function (exports, AuthorizedRoute) {

  'use strict';

  var AdminIndexRoute;

  AdminIndexRoute = AuthorizedRoute['default'].extend({
    setupController: function(controller) {
      return controller.set('promise', this.store.find('adminJournal'));
    }
  });

  exports['default'] = AdminIndexRoute;

});