define('tahi/tests/components/comment-board-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleForComponent('comment-board', 'Unit: components/comment-board', {
    setup: function() {
      this.comment1 = Ember['default'].Object.create({
        body: "comment 1",
        createdAt: 1
      });
      this.comment2 = Ember['default'].Object.create({
        body: "comment 2",
        createdAt: 2
      });
      this.comment3 = Ember['default'].Object.create({
        body: "comment 3",
        createdAt: 3
      });
      this.comment4 = Ember['default'].Object.create({
        body: "comment 4",
        createdAt: 4
      });
      this.comment5 = Ember['default'].Object.create({
        body: "comment 5",
        createdAt: 5
      });
      this.comment6 = Ember['default'].Object.create({
        body: "comment 6",
        createdAt: 6
      });
      this.comment7 = Ember['default'].Object.create({
        body: "comment 7",
        createdAt: 7
      });
      this.comment8 = Ember['default'].Object.create({
        body: "comment 8",
        createdAt: 8
      });
      return Ember['default'].run((function(_this) {
        return function() {
          _this.board = _this.subject();
          return _this.board.set('comments', [_this.comment1, _this.comment2, _this.comment3, _this.comment4, _this.comment5, _this.comment6, _this.comment7, _this.comment8]);
        };
      })(this));
    }
  });

  ember_qunit.test('#firstComments returns the latest 5 comments in reverse order', function() {
    var expectedCommentBodies;
    expectedCommentBodies = this.board.get('firstComments').map(function(comment) {
      return comment.get('body');
    });
    return deepEqual(expectedCommentBodies, ["comment 8", "comment 7", "comment 6", "comment 5", "comment 4"]);
  });

  ember_qunit.test('#showingAllComments returns false if there are more than 5 comments', function() {
    return ok(!this.board.get('showingAllComments'));
  });

  ember_qunit.test('#showAllComments action should set @showingAllComments to true', function() {
    this.board.send('showAllComments');
    return ok(this.board.get('showingAllComments'));
  });

});