define('tahi/tests/helpers/mock-server', ['exports'], function (exports) {

  'use strict';

  var setupMockServer;

  setupMockServer = function() {
    var server;
    server = sinon.fakeServer.create();
    server.autoRespond = true;
    server.xhr.useFilters = true;
    server.xhr.addFilter(function(method, url) {
      return !!url.match(/visualEditor/);
    });
    server.respondWith('GET', /visual-editor/, '{}');
    server.respondWith('GET', '/formats', [
      200, {
        "Content-Type": "application/json"
      }, JSON.stringify({
        "export_formats": [
          {
            "format": "docx"
          }, {
            "format": "latex"
          }
        ],
        "import_formats": [
          {
            "format": "docx"
          }, {
            "format": "odt"
          }
        ]
      })
    ]);
    server.respondWith('GET', '/flows/authorization', [
      204, {
        'content-type': 'application/html',
        'tahi-authorization-check': true
      }, ""
    ]);
    server.respondWith('GET', '/user_flows/authorization', [
      204, {
        'content-type': 'application/html',
        'tahi-authorization-check': true
      }, ""
    ]);
    server.respondWith('GET', '/admin/journals/authorization', [
      204, {
        'content-type': 'application/html',
        'tahi-authorization-check': true
      }, ""
    ]);
    server.respondWith('GET', "/comment_looks", [
      200, {
        'Content-Type': 'application/json'
      }, JSON.stringify({
        comment_looks: []
      })
    ]);
    server.respondWith('GET', /\/papers\/\d+\/manuscript_manager/, [403, {}, JSON.stringify({})]);
    return server;
  };

  exports['default'] = setupMockServer;

});