define('tahi/tests/integration/admin-test', ['ember', '../helpers/start-app', 'ember-qunit', '../helpers/setups', '../helpers/mock-server', '../helpers/factory'], function (Ember, startApp, ember_qunit, setups, setupMockServer, Factory) {

  'use strict';

  var app, server;

  app = null;

  server = null;

  module('Integration: Admin Test', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var adminJournalPayload, admin_journals, journal, journalId, journal_task_types, manuscript_manager_templates, phase_templates, roles, task_templates;
      app = startApp['default']();
      server = setupMockServer['default']();
      journal = Factory['default'].createRecord('AdminJournal');
      journalId = journal.id;
      manuscript_manager_templates = Factory['default'].createMMT(journal);
      phase_templates = Factory['default'].createPhaseTemplate(manuscript_manager_templates);
      task_templates = Factory['default'].createJournalTaskType(journal, {});
      journal_task_types = Factory['default'].createTaskTemplate(journal, phase_templates, task_templates);
      roles = Factory['default'].createJournalRole(journal);
      admin_journals = journal;
      adminJournalPayload = Factory['default'].createPayload('adminJournal');
      adminJournalPayload.addRecords([manuscript_manager_templates, phase_templates, task_templates, journal_task_types, roles, admin_journals]);
      server.respondWith('GET', '/admin/journals/authorization', [
        204, {
          'Content-Type': 'application/html'
        }, ""
      ]);
      server.respondWith('GET', '/admin/journals', [
        200, {
          'Content-Type': 'application/json'
        }, JSON.stringify(adminJournalPayload)
      ]);
      return server.respondWith('GET', "/admin/journals/" + journalId, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(adminJournalPayload)
      ]);
    }
  });

  ember_qunit.test('site admin can see the Add New Journal button', function() {
    Ember['default'].run((function(_this) {
      return function() {
        return getCurrentUser().set('siteAdmin', true);
      };
    })(this));
    visit("/admin/");
    return andThen(function() {
      ok(find('.journal').length);
      return ok(find('a.add-new-journal').length);
    });
  });

  ember_qunit.test('journal admin can not see the Add New Journal button', function() {
    visit("/admin/");
    return andThen(function() {
      ok(find('.journal').length);
      return ok(!find('a.add-new-journal').length);
    });
  });

});