define('tahi/models/task-template', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var TaskTemplate, a;

  a = DS['default'].attr;

  TaskTemplate = DS['default'].Model.extend({
    phaseTemplate: DS['default'].belongsTo('phaseTemplate'),
    journalTaskType: DS['default'].belongsTo('journalTaskType'),
    title: a('string'),
    template: a()
  });

  exports['default'] = TaskTemplate;

});