define('tahi/pods/components/flow-column/component', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var FlowColumnComponent;

  FlowColumnComponent = Ember['default'].Component.extend({
    tagName: 'li',
    classNames: ['column'],
    editing: false,
    editable: false,
    emptyText: 'There are no matches.',
    sortBy: 'asc',
    tasksSortBy: (function() {
      return ["createdAt:" + (this.get('sortBy'))];
    }).property('sortBy'),
    tasks: Ember['default'].computed.sort('flow.tasks', 'tasksSortBy'),
    flowTitleDidChange: (function() {
      return Ember['default'].run.schedule('afterRender', this, Utils['default'].resizeColumnHeaders);
    }).observes('flow.title', 'editing'),
    selectableTaskTypes: Ember['default'].computed(function() {
      return this.selectableQueries(this.get('journalTaskTypes'), 'kind', 'title');
    }),
    selectedTaskType: Ember['default'].computed(function() {
      return this.selectedQuery(this.get('selectableTaskTypes').findBy('id', this.get('flow.query').type));
    }),
    selectableTaskStates: Ember['default'].computed(function() {
      return [
        {
          id: "completed",
          text: "Completed"
        }, {
          id: "incomplete",
          text: "Incomplete"
        }
      ];
    }),
    selectedTaskState: Ember['default'].computed(function() {
      return this.selectedQuery(this.get('selectableTaskStates').findBy('id', this.get('flow.query').state));
    }),
    selectableTaskAssignments: Ember['default'].computed(function() {
      return [
        {
          id: "true",
          text: "Me"
        }, {
          id: "false",
          text: "None"
        }
      ];
    }),
    selectedTaskAssignment: Ember['default'].computed(function() {
      return this.selectedQuery(this.get('selectableTaskAssignments').findBy('id', this.get('flow.query').assigned));
    }),
    selectableTaskRoles: Ember['default'].computed(function() {
      return this.get('flow.taskRoles').map(function(role) {
        return {
          id: role,
          text: role.capitalize()
        };
      });
    }),
    selectedTaskRole: Ember['default'].computed(function() {
      return this.selectedQuery(this.get('selectableTaskRoles').findBy('id', this.get('flow.query').role));
    }),
    selectableQueries: function(options, idKey, textKey) {
      return options.map(function(option) {
        return {
          id: option.get(idKey),
          text: option.get(textKey)
        };
      });
    },
    selectedQuery: function(option) {
      if (option) {
        return {
          id: option.id,
          text: option.text
        };
      }
    },
    actions: {
      viewCard: function(card) {
        return this.sendAction('viewCard', card);
      },
      updateTypeQuery: function(query) {
        this.get('flow.query').type = query.id;
        return this.send('save');
      },
      updateStateQuery: function(query) {
        this.get('flow.query').state = query.id;
        return this.send('save');
      },
      updateAssignmentQuery: function(query) {
        this.get('flow.query').assigned = query.id;
        return this.send('save');
      },
      updateRoleQuery: function(query) {
        this.get('flow.query').role = query.id;
        return this.send('save');
      },
      removeTypeQuery: function() {
        delete this.get('flow.query').type;
        return this.send('save');
      },
      removeStateQuery: function() {
        delete this.get('flow.query').state;
        return this.send('save');
      },
      removeAssignmentQuery: function() {
        delete this.get('flow.query').assigned;
        return this.send('save');
      },
      removeRoleQuery: function() {
        delete this.get('flow.query').role;
        return this.send('save');
      },
      save: function() {
        this.sendAction('saveFlow', this.get('flow'));
        return Ember['default'].run.schedule('afterRender', this, Utils['default'].resizeColumnHeaders);
      },
      cancel: function() {
        this.get('flow').rollback();
        this.send('toggleEdit');
        return Ember['default'].run.schedule('afterRender', this, Utils['default'].resizeColumnHeaders);
      },
      toggleEdit: function() {
        if (!this.get('editable')) {
          return;
        }
        return this.toggleProperty('editing');
      },
      removeFlow: function() {
        return this.sendAction('removeFlow', this.get('flow'));
      },
      setSortOrder: function(value) {
        return this.set('sortBy', value);
      }
    }
  });

  exports['default'] = FlowColumnComponent;

});