define('tahi/pods/task/controller', ['exports', 'ember', 'tahi/mixins/controllers/saves-delayed', 'tahi/mixins/controllers/controller-participants', 'tahi/mixins/validation-errors'], function (exports, Ember, SavesDelayed, ControllerParticipants, ValidationErrorsMixin) {

  'use strict';

  var TaskController;

  TaskController = Ember['default'].ObjectController.extend(SavesDelayed['default'], ControllerParticipants['default'], ValidationErrorsMixin['default'], Ember['default'].Evented, {
    queryParams: ['isNewTask'],
    isNewTask: false,
    needs: ['application'],
    onClose: 'closeOverlay',
    isLoading: false,
    isMetadata: Ember['default'].computed.alias('isMetadataTask'),
    isUserEditable: Ember['default'].computed('paper.editable', 'isMetadata', function() {
      return this.get('paper.editable') || !this.get('isMetadata');
    }),
    isCurrentUserAdmin: Ember['default'].computed.alias('controllers.application.currentUser.siteAdmin'),
    isEditable: Ember['default'].computed.or('isUserEditable', 'isCurrentUserAdmin'),
    comments: [],
    redirectStack: Ember['default'].computed.alias('controllers.application.overlayRedirect'),
    clearCachedModel: function(transition) {
      var redirectRoute, redirectStack;
      redirectStack = this.get('redirectStack');
      if (!Ember['default'].isEmpty(redirectStack)) {
        redirectRoute = redirectStack.popObject();
        if (transition.targetName !== redirectRoute.get('firstObject')) {
          return this.get('controllers.application').set('cachedModel', null);
        }
      }
    },
    saveModel: function() {
      return this._super().then((function(_this) {
        return function() {
          return _this.clearValidationErrors();
        };
      })(this))["catch"]((function(_this) {
        return function(response) {
          _this.displayValidationErrorsFromResponse(response);
          return _this.set('model.completed', false);
        };
      })(this));
    },
    actions: {
      closeAction: function() {
        return this.send(this.get('onClose'));
      },
      redirect: function() {
        return this.transitionToRoute.apply(this, this.get('controllers.application.overlayRedirect.lastObject'));
      },
      redirectToDashboard: function() {
        return this.transitionToRoute('dashboard');
      },
      postComment: function(body) {
        var commentFields, newComment;
        if (!body) {
          return;
        }
        commentFields = {
          commenter: this.currentUser,
          task: this.get('model'),
          body: body,
          createdAt: new Date()
        };
        newComment = this.store.createRecord('comment', commentFields);
        return newComment.save();
      },
      routeWillTransition: function(transition) {
        if (this.get('isUploading')) {
          if (confirm('You are uploading, are you sure you want to abort uploading?')) {
            this.send('cancelUploads');
          } else {
            transition.abort();
            return;
          }
        }
        return this.clearCachedModel(transition);
      }
    }
  });

  exports['default'] = TaskController;

});