define('tahi/pods/components/inline-edit-email/component', ['exports', 'ember', 'tahi/mixins/components/adhoc-inline-edit-item'], function (exports, Ember, AdhocInlineEditItem) {

  'use strict';

  var InlineEditEmailComponent;

  InlineEditEmailComponent = Ember['default'].Component.extend(AdhocInlineEditItem['default'], {
    isSendable: true,
    showChooseReceivers: false,
    mailRecipients: [],
    recipients: [],
    allUsers: null,
    overlayParticipants: null,
    emailSentStates: Ember['default'].computed.alias('parentView.emailSentStates'),
    lastSentAt: null,
    initRecipients: (function() {
      if (this.get('showChooseReceivers')) {
        return this.set('recipients', this.get('overlayParticipants').copy());
      }
    }).observes('showChooseReceivers'),
    keyForStates: Ember['default'].computed.alias('bodyPart.subject'),
    showSentMessage: (function() {
      var key;
      if (this.get('isSendable')) {
        key = this.get('keyForStates');
        return this.get('emailSentStates').contains(key);
      } else {
        return false;
      }
    }).property('keyForStates', 'emailSentStates.@each'),
    setSentState: function() {
      var key;
      key = this.get('keyForStates');
      return this.get('emailSentStates').addObject(key);
    },
    actions: {
      toggleChooseReceivers: function() {
        return this.toggleProperty('showChooseReceivers');
      },
      clearEmailSent: function() {
        return this.get('emailSentStates').removeObject(this.get('keyForStates'));
      },
      sendEmail: function() {
        var bodyPart, recipientIds;
        recipientIds = this.get('recipients').mapBy('id');
        bodyPart = this.get('bodyPart');
        bodyPart.sent = moment().format('MMMM Do YYYY');
        this.set('lastSentAt', bodyPart.sent);
        this.sendAction("sendEmail", {
          body: bodyPart.value,
          subject: bodyPart.subject,
          recipients: recipientIds
        });
        this.set('showChooseReceivers', false);
        this.setSentState();
        return this.get('parentView').send('save');
      },
      removeRecipient: function(recipient) {
        return this.get('recipients').removeObject(recipient);
      },
      addRecipientById: function(recipientId) {
        var store;
        store = this.container.lookup('store:main');
        return store.find('user', recipientId).then((function(_this) {
          return function(recipient) {
            return _this.get('recipients').addObject(recipient);
          };
        })(this));
      }
    }
  });

  exports['default'] = InlineEditEmailComponent;

});