define('tahi/tests/helpers/async-helpers', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var asyncHelpers;

  asyncHelpers = (function() {
    return Ember['default'].Test.registerAsyncHelper("waitForElement", function(app, element) {
      return Ember['default'].Test.promise(function(resolve) {
        var interval;
        Ember['default'].Test.adapter.asyncStart();
        interval = setInterval(function() {
          if ($(element).length > 0) {
            clearInterval(interval);
            Ember['default'].Test.adapter.asyncEnd();
            Ember['default'].run(null, resolve, true);
          }
        }, 10);
      });
    });
  })();

  exports['default'] = asyncHelpers;

});