define('tahi/models/dashboard', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Dashboard, a;

  a = DS['default'].attr;

  Dashboard = DS['default'].Model.extend({
    papers: DS['default'].hasMany('lite-paper'),
    user: DS['default'].belongsTo('user'),
    totalPaperCount: a('number'),
    totalPageCount: a('number')
  });

  exports['default'] = Dashboard;

});