define('tahi/pods/admin/role/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var RoleController;

  RoleController = Ember['default'].ObjectController.extend({
    isEditing: false,
    notEditing: Ember['default'].computed.not('isEditing'),
    setIsEditing: (function() {
      if (this.get('model.isNew')) {
        return this.set('isEditing', true);
      }
    }).on('init'),
    actions: {
      edit: function() {
        return this.set('isEditing', true);
      },
      save: function() {
        return this.get('model').save().then((function(_this) {
          return function() {
            return _this.set('isEditing', false);
          };
        })(this))["catch"](function() {});
      },
      cancel: function() {
        this.set('isEditing', false);
        if (this.get('model.isNew')) {
          return this.get('model').deleteRecord();
        } else {
          return this.get('model').rollback();
        }
      },
      "delete": function() {
        return this.send('deleteRole', this.get('model'));
      }
    }
  });

  exports['default'] = RoleController;

});