define('tahi/pods/components/participant-selector/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ParticipantSelectorComponent;

  ParticipantSelectorComponent = Ember['default'].Component.extend({
    classNames: ['participant-selector', 'select2-multiple'],
    setupTooltips: (function() {
      return Ember['default'].run.schedule('afterRender', this, function() {
        this.$('.select2-search-choice img').tooltip({
          placement: "bottom"
        });
        return this.$('.add-participant-button').tooltip({
          placement: "bottom"
        });
      });
    }).on('didInsertElement').observes('currentParticipants.@each'),
    resultsTemplate: function(user) {
      var userInfo;
      userInfo = user.roles.length ? "" + user.username + ", " + (user.roles.join(', ')) : user.username;
      return '<strong>' + user.full_name + '</strong><br><div class="suggestion-sub-value">' + userInfo + '</div>';
    },
    selectedTemplate: function(user) {
      var name, url;
      name = user.full_name || user.get('fullName');
      url = user.avatar_url || user.get('avatarUrl');
      return new Handlebars.SafeString("<img alt='" + name + "' class='user-thumbnail-small' src='" + url + "' data-toggle='tooltip' title='" + name + "'/>");
    },
    sortByCollaboration: function(a, b) {
      if (a.roles.length && !b.roles.length) {
        return -1;
      } else if (!a.roles.length && b.roles.length) {
        return 1;
      } else {
        if (a.full_name < b.full_name) {
          return -1;
        } else if (a.full_name > b.full_name) {
          return 1;
        } else {
          return 0;
        }
      }
    },
    remoteSource: (function() {
      return {
        url: "/filtered_users/users/" + (this.get('paperId')) + "/",
        dataType: "json",
        quietMillis: 500,
        data: function(term) {
          return {
            query: term
          };
        },
        results: (function(_this) {
          return function(data) {
            data.filtered_users.sort(_this.sortByCollaboration);
            return {
              results: data.filtered_users
            };
          };
        })(this)
      };
    }).property(),
    actions: {
      addParticipant: function(newParticipant) {
        return this.sendAction("onSelect", newParticipant.id);
      },
      removeParticipant: function(participant) {
        return this.sendAction("onRemove", participant.id);
      },
      dropdownClosed: function() {
        this.$('.select2-search-field input').removeClass('active');
        return this.$('.add-participant-button').removeClass('searching');
      },
      activateDropdown: function() {
        this.$('.select2-search-field input').addClass('active').trigger('click');
        return this.$('.add-participant-button').addClass('searching');
      }
    }
  });

  exports['default'] = ParticipantSelectorComponent;

});