define('tahi/initializers/reopen-ds-model', ['exports'], function (exports) {

  'use strict';

  var ReopenDSModel;

  ReopenDSModel = {
    name: 'reopenDSModel',
    after: 'eventStream',
    initialize: function(container, application) {
      return DS.Model.reopen({
        path: function() {
          var adapter, resourceType, resourceURL;
          adapter = this.get('store').adapterFor(this);
          resourceType = this.constructor.typeKey;
          return resourceURL = adapter.buildURL(resourceType, this.get('id'));
        },
        adapterWillCommit: function() {
          var es;
          es = container.lookup('eventstream:main');
          es.pause();
          return this.send('willCommit');
        }
      });
    }
  };

  exports['default'] = ReopenDSModel;

});