define('tahi/tests/components/inline-edit-email-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleForComponent('inline-edit-email', 'Unit: components/inline-edit-email');

  ember_qunit.test('#sendEmail', function() {
    var component, mockParent, targetObject;
    targetObject = {
      emailMock: function(data) {
        ok(true, 'sends the sendEmail action to its target');
        ok(data.subject === "Greetings!", "contains the subject");
        return ok(data.body === "Welcome to Vulcan!", "contains the body");
      }
    };
    mockParent = {
      send: function(arg) {
        return ok(arg === 'save', "sends save to the parentView");
      },
      emailSentStates: Ember['default'].ArrayProxy.create({
        content: []
      })
    };
    component = this.subject();
    component.setProperties({
      sendEmail: 'emailMock',
      bodyPart: {
        subject: "Greetings!",
        value: "Welcome to Vulcan!"
      },
      overlayParticipants: [
        Ember['default'].Object.create({
          id: 5
        })
      ],
      recipients: [
        Ember['default'].Object.create({
          id: 5
        })
      ],
      targetObject: targetObject,
      parentView: mockParent,
      showChooseReceivers: true
    });
    component.send('sendEmail');
    ok(!component.get('showChooseReceivers'), 'turns off the receivers selector');
    return ok(component.get('emailSentStates').contains('Greetings!'), 'records itself as sent to the controller');
  });

  ember_qunit.test('shows itelf as sent based on emailSentStates', function() {
    var component, mockParent;
    mockParent = {
      send: function(arg) {
        return ok(arg === 'save', "sends save to the parentView");
      },
      emailSentStates: Ember['default'].ArrayProxy.create({
        content: ['Greetings!']
      })
    };
    component = this.subject();
    component.setProperties({
      parentView: mockParent,
      bodyPart: {
        subject: "Greetings!",
        value: "Welcome to Vulcan!"
      },
      overlayParticipants: [
        Ember['default'].Object.create({
          id: 5
        })
      ],
      recipients: [
        Ember['default'].Object.create({
          id: 5
        })
      ],
      showChooseReceivers: true
    });
    ok(component.get('showSentMessage'), 'It shows up when its key is in emailSentStates');
    component.send('clearEmailSent');
    return ok(!component.get('showSentMessage'), "It doesn't show up after clearing");
  });

});