define('tahi/pods/application/route', ['exports', 'ember', 'tahi/mixins/routes/animate-element', 'tahi/services/rest-less'], function (exports, Ember, AnimateElement, RESTless) {

  'use strict';

  var ApplicationRoute;

  ApplicationRoute = Ember['default'].Route.extend(AnimateElement['default'], {
    setupController: function(controller, model) {
      controller.set('model', model);
      if (this.currentUser) {
        RESTless['default'].authorize(controller, '/admin/journals/authorization', 'canViewAdminLinks');
        return RESTless['default'].authorize(controller, '/user_flows/authorization', 'canViewFlowManagerLink');
      }
    },
    actions: {
      willTransition: function(transition) {
        var appController, currentRouteController;
        appController = this.controllerFor('application');
        currentRouteController = this.controllerFor(appController.get('currentRouteName'));
        if (currentRouteController.get('isUploading')) {
          if (confirm('You are uploading. Are you sure you want abort uploading?')) {
            currentRouteController.send('cancelUploads');
          } else {
            transition.abort();
            return;
          }
        }
        appController.send('hideNavigation');
        return appController.set('isLoading', true);
      },
      didTransition: function() {
        return this.controllerFor('application').set('isLoading', false);
      },
      error: function(response, transition, originRoute) {
        var lastRoute, oldState, transitionMsg;
        oldState = transition.router.oldState;
        transitionMsg = oldState ? (lastRoute = oldState.handlerInfos.get('lastObject.name'), "Error in transition from " + lastRoute + " to " + transition.targetName) : "Error in transition into " + transition.targetName;
        this.logError(transitionMsg + "\n" + response.message + "\n" + response.stack + "\n");
        transition.abort();
        return this.controllerFor('application').set('isLoading', false);
      },
      closeOverlay: function() {
        this.flash.clearMessages();
        return this.animateOverlayOut().then((function(_this) {
          return function() {
            return _this.disconnectOutlet({
              outlet: 'overlay',
              parentView: 'application'
            });
          };
        })(this));
      },
      closeAction: function() {
        return this.send('closeOverlay');
      },
      addPaperToEventStream: function(paper) {
        return this.eventStream.addEventListener(paper.get('eventName'));
      },
      editableDidChange: function() {
        return null;
      },
      feedback: function() {
        return this.render('overlays/feedback', {
          into: 'application',
          outlet: 'overlay',
          controller: 'overlays/feedback'
        });
      }
    }
  });

  exports['default'] = ApplicationRoute;

});