define('tahi/views/overlay', ['exports', 'ember', 'tahi/mixins/routes/animate-element'], function (exports, Ember, AnimateElement) {

  'use strict';

  var OverlayView;

  OverlayView = Ember['default'].View.extend(AnimateElement['default'], {
    animateIn: (function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, this.animateOverlayIn);
    }).on('didInsertElement'),
    setupKeyup: (function() {
      return $('body').on('keyup.overlay', (function(_this) {
        return function(e) {
          if (e.keyCode === 27 || e.which === 27) {
            if ($(e.target).is(':not(input, textarea)')) {
              return _this.get('controller').send('closeAction');
            }
          }
        };
      })(this));
    }).on('didInsertElement'),
    tearDownKeyup: (function() {
      return $('body').off('keyup.overlay');
    }).on('willDestroyElement')
  });

  exports['default'] = OverlayView;

});