define('tahi/models/admin-journal-user', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var AdminJournalUser, a;

  a = DS['default'].attr;

  AdminJournalUser = DS['default'].Model.extend({
    userRoles: DS['default'].hasMany('userRole'),
    firstName: a('string'),
    lastName: a('string'),
    username: a('string')
  });

  exports['default'] = AdminJournalUser;

});