define('tahi/pods/components/inline-edit-body-part/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var InlineEditBodyPartComponent;

  InlineEditBodyPartComponent = Ember['default'].Component.extend({
    editing: false,
    snapshot: null,
    confirmDelete: false,
    _init: (function() {
      return this.set('snapshot', []);
    }).on('init'),
    createSnapshot: (function() {
      return this.set('snapshot', Ember['default'].copy(this.get('block'), true));
    }).observes('editing'),
    hasContent: (function() {
      return this.get('block').any(this._isNotEmpty);
    }).property('block.@each.value'),
    hasNoContent: Ember['default'].computed.not('hasContent'),
    bodyPartType: (function() {
      return this.get('block.firstObject.type');
    }).property('block.@each.type'),
    _isNotEmpty: function(item) {
      return item && !Ember['default'].isEmpty(item.value);
    },
    actions: {
      toggleEdit: function() {
        if (this.get('editing')) {
          this.sendAction('cancel', this.get('block'), this.get('snapshot'));
        }
        return this.toggleProperty('editing');
      },
      deleteBlock: function() {
        return this.sendAction('delete', this.get('block'));
      },
      save: function() {
        if (this.get('hasContent')) {
          this.sendAction('save', this.get('block'));
          return this.toggleProperty('editing');
        }
      },
      confirmDeletion: function() {
        return this.set('confirmDelete', true);
      },
      cancelDeletion: function() {
        return this.set('confirmDelete', false);
      },
      addItem: function() {
        return this.sendAction('addItem', this.get('block'));
      }
    }
  });

  exports['default'] = InlineEditBodyPartComponent;

});