define('tahi/models/role', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Role, a;

  a = DS['default'].attr;

  Role = DS['default'].Model.extend({
    journal: DS['default'].belongsTo('adminJournal'),
    userRoles: DS['default'].hasMany('userRole'),
    flows: DS['default'].hasMany('flow', {
      async: true
    }),
    name: a('string'),
    kind: a('string'),
    canAdministerJournal: a('boolean'),
    required: a('boolean'),
    canViewAssignedManuscriptManagers: a('boolean'),
    canViewAllManuscriptManagers: a('boolean'),
    canViewFlowManager: a('boolean'),
    destroyRecord: function() {
      this.get('userRoles').invoke('unloadRecord');
      return this._super();
    }
  });

  exports['default'] = Role;

});