define('tahi/pods/components/radio-button/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var RadioButtonComponent;

  RadioButtonComponent = Ember['default'].Component.extend({
    tagName: 'input',
    type: 'radio',
    attributeBindings: ['name', 'type', 'value', 'checked:checked:'],
    checked: (function() {
      return this.get('selection') === this.get('value');
    }).property('selection'),
    change: function() {
      return this.set('selection', this.$().val());
    }
  });

  exports['default'] = RadioButtonComponent;

});