define('tahi/tests/integration/dashboard-paper-collaboration-test', ['ember', '../helpers/start-app', 'ember-qunit', '../helpers/setups', '../helpers/mock-server', '../helpers/factory', 'tahi/services/event-stream'], function (Ember, startApp, ember_qunit, setups, setupMockServer, Factory, EventStream) {

  'use strict';

  var app, createDashboardDataWithLitePaper, createDashboardDataWithLitePaperRoles, fakeUser, server, setupEventStream;

  app = null;

  server = null;

  fakeUser = null;

  setupEventStream = function() {
    var es, store;
    store = getStore();
    es = EventStream['default'].create({
      store: store,
      init: function() {}
    });
    return [es, store];
  };

  createDashboardDataWithLitePaper = function(paperCount, litePaper) {
    var i, litePapers, lp, paperIds, _i;
    litePapers = [];
    for (i = _i = 1; _i <= paperCount; i = _i += 1) {
      lp = Factory['default'].createLitePaper({
        id: i,
        title: "Fake Paper Long Title " + i,
        short_title: "Fake Paper Short Title " + i,
        submitted: false
      });
      lp.roles = ['Collaborator'];
      lp.related_at_date = "2014-09-28T13:54:58.028Z";
      litePapers.pushObject(lp);
    }
    if (litePaper) {
      litePapers.pushObject(litePaper);
    }
    paperIds = litePapers.map(function(lp) {
      return lp.id;
    });
    return [
      litePapers, [
        {
          id: 1,
          user_id: 1,
          paper_ids: paperIds,
          total_paper_count: litePapers.length,
          total_page_count: 1
        }
      ]
    ];
  };

  createDashboardDataWithLitePaperRoles = function(roleArray) {
    var litePapers;
    litePapers = roleArray.map(function(role, index) {
      var lp;
      lp = Factory['default'].createLitePaper({
        id: index + 1,
        title: "Fake Paper Long Title " + index,
        short_title: "Fake Paper Short Title " + index,
        submitted: false
      });
      lp.roles = [role];
      lp.related_at_date = "2014-09-28T13:54:58.028Z";
      return lp;
    });
    return [
      litePapers, [
        {
          id: 1,
          user_id: 1,
          paper_ids: litePapers.mapBy('id'),
          total_paper_count: litePapers.length,
          total_page_count: 1
        }
      ]
    ];
  };

  module('Integration: Dashboard', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var adminJournalsResponse, dashboardResponse, dashboards, litePapers, _ref;
      app = startApp['default']();
      server = setupMockServer['default']();
      fakeUser = window.currentUser.user;
      _ref = createDashboardDataWithLitePaper(2), litePapers = _ref[0], dashboards = _ref[1];
      dashboardResponse = {
        users: [fakeUser],
        affiliations: [],
        lite_papers: litePapers,
        dashboards: dashboards
      };
      adminJournalsResponse = {};
      server.respondWith('GET', '/dashboards', [
        200, {
          'Content-Type': 'application/json'
        }, JSON.stringify(dashboardResponse)
      ]);
      server.respondWith('GET', '/admin/journals', [
        200, {
          'Content-Type': 'application/json'
        }, JSON.stringify(adminJournalsResponse)
      ]);
      return server.respondWith('GET', "/admin/journals/authorization", [
        204, {
          "Content-Type": "application/html"
        }, ""
      ]);
    }
  });

  ember_qunit.test('The dashboard shows papers for a user if they have any role on the paper', function() {
    var dashboardResponse, dashboards, litePapers, roles, _ref;
    roles = ['Collaborator', 'Reviewer', 'Editor', 'Admin', 'My Paper', 'Circus Clown'];
    _ref = createDashboardDataWithLitePaperRoles(roles), litePapers = _ref[0], dashboards = _ref[1];
    dashboardResponse = {
      users: [fakeUser],
      affiliations: [],
      lite_papers: litePapers,
      dashboards: dashboards
    };
    server.respondWith('GET', '/dashboards', [
      200, {
        'Content-Type': 'application/json'
      }, JSON.stringify(dashboardResponse)
    ]);
    return visit('/').then(function() {
      return equal(find('.dashboard-submitted-papers .dashboard-paper-title').length, 6, 'All papers with roles should be visible');
    });
  });

  ember_qunit.test('When paper is added, only shows if user is allowed to see the paper', function() {
    visit('/').then(function() {
      return equal(find('.dashboard-submitted-papers .dashboard-paper-title').length, 2);
    });
    andThen(function() {
      var data, es, paperPayload, records, store, _ref;
      _ref = setupEventStream(), es = _ref[0], store = _ref[1];
      paperPayload = Factory['default'].createPayload('paper');
      records = setups.paperWithRoles(200, []);
      paperPayload.addRecords(records.concat([fakeUser]));
      data = Ember['default'].merge(paperPayload.toJSON(), {
        action: "updated"
      });
      return Ember['default'].run((function(_this) {
        return function() {
          return es.msgResponse(data);
        };
      })(this));
    });
    return andThen(function() {
      return equal(find('.dashboard-submitted-papers .dashboard-paper-title').length, 2, "paper with no roles does not show on dashboard");
    });
  });

  ember_qunit.test('When user is removed from collaborating on paper', function() {
    visit('/').then(function() {
      return equal(find('.dashboard-submitted-papers .dashboard-paper-title').length, 2);
    });
    andThen(function() {
      var data, es, paperPayload, records, store, _ref;
      _ref = setupEventStream(), es = _ref[0], store = _ref[1];
      paperPayload = Factory['default'].createPayload('paper');
      records = setups.paperWithRoles(1, []);
      paperPayload.addRecords(records.concat([fakeUser]));
      data = Ember['default'].merge(paperPayload.toJSON(), {
        action: "updated"
      });
      return Ember['default'].run((function(_this) {
        return function() {
          return es.msgResponse(data);
        };
      })(this));
    });
    return andThen(function() {
      return equal(find('.dashboard-submitted-papers .dashboard-paper-title').length, 1);
    });
  });

  ember_qunit.test('User can show the feedback form', function() {
    visit('/');
    click('.navigation-toggle');
    click('.navigation-item-feedback');
    return andThen(function() {
      return ok(find(".overlay-footer .button-primary:contains('Send Feedback')").length);
    });
  });

  ember_qunit.test('Hitting escape closes the feedback form', function() {
    visit('/');
    click('.navigation-toggle');
    click('.navigation-item-feedback');
    keyEvent('.overlay', 'keyup', 27);
    return andThen(function() {
      return ok(!find(".overlay-footer .button-primary:contains('Send Feedback')").length);
    });
  });

  ember_qunit.test('User can show the feedback form', function() {
    server.respondWith('POST', "/feedback", [
      200, {
        "Content-Type": "application/json"
      }, JSON.stringify({})
    ]);
    visit('/');
    click('.navigation-toggle');
    click('.navigation-item-feedback');
    fillIn('.feedback-overlay-remarks', 'all my feedback');
    click('.overlay-footer .button-primary');
    return andThen(function() {
      ok(find(".feedback-overlay-thanks").length);
      return ok(server.requests.findBy('url', '/feedback'));
    });
  });

});