define('tahi/pods/components/select-2/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var Select2Component;

  Select2Component = Ember['default'].TextField.extend({
    tagName: 'div',
    classNames: ['select2'],
    autoFocus: false,
    source: [],
    closeOnSelect: false,
    multiSelect: false,
    selectedData: [],
    placeholder: "",
    setupSelectedListener: function() {
      this.$().off('select2-selecting');
      return this.$().on('select2-selecting', (function(_this) {
        return function(e) {
          return _this.sendAction('selectionSelected', e.choice);
        };
      })(this));
    },
    setupRemovedListener: function() {
      this.$().off('select2-removing');
      return this.$().on('select2-removing', (function(_this) {
        return function(e) {
          return _this.sendAction('selectionRemoved', e.choice);
        };
      })(this));
    },
    setupClearingListener: function() {
      this.$().off('select2-clearing');
      return this.$().on('select2-clearing', (function(_this) {
        return function(e) {
          return _this.sendAction('selectionCleared', e.choice);
        };
      })(this));
    },
    setupClosedListener: function() {
      this.$().off('select2-close');
      return this.$().on('select2-close', (function(_this) {
        return function() {
          return _this.sendAction('dropdownClosed');
        };
      })(this));
    },
    setSelectedData: (function() {
      return this.$().select2('val', this.get('selectedData').mapProperty('id'));
    }).observes('selectedData'),
    initSelection: function(el, callback) {
      var selectedData;
      selectedData = this.get('selectedData') || [];
      return callback(selectedData.compact());
    },
    repaint: function() {
      this.teardown();
      return this.setup();
    },
    setup: (function() {
      var options;
      options = {};
      options.placeholder = this.get('placeholder');
      if (this.get('minimumInputLength')) {
        options.minimumInputLength = this.get('minimumInputLength');
      }
      if (this.get('selectedTemplate')) {
        options.formatSelection = this.get('selectedTemplate');
      }
      if (this.get('resultsTemplate')) {
        options.formatResult = this.get('resultsTemplate');
      }
      options.allowClear = this.get('allowClear');
      options.multiple = this.get('multiSelect');
      options.data = this.get('source');
      options.closeOnSelect = this.get('closeOnSelect');
      if (this.get('remoteSource')) {
        options.ajax = this.get('remoteSource');
      }
      if (this.get('dropdownClass')) {
        options.dropdownCssClass = this.get('dropdownClass');
      }
      options.initSelection = Ember['default'].run.bind(this, this.initSelection);
      this.$().select2(options);
      this.setupSelectedListener();
      this.setupRemovedListener();
      this.setupClosedListener();
      this.setupClearingListener();
      this.setSelectedData();
      return this.addObserver('source', this, this.repaint);
    }).on('didInsertElement'),
    teardown: (function() {
      this.$().off('select2-selecting');
      this.$().off('select2-removing');
      this.$().off('select2-close');
      return this.removeObserver('source', this, this.repaint);
    }).on('willDestroyElement')
  });

  exports['default'] = Select2Component;

});