define('tahi/tests/helpers/store-helpers', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var storeHelpers;

  storeHelpers = (function() {
    Ember['default'].Test.registerHelper('pushModel', function(app, type, data) {
      var store;
      store = app.__container__.lookup('store:main');
      return Ember['default'].run(function() {
        store.push(type, data);
        return store.getById(type, data.id);
      });
    });
    Ember['default'].Test.registerHelper('pushPayload', function(app, type, data) {
      var store;
      store = app.__container__.lookup('store:main');
      return Ember['default'].run(function() {
        return store.pushPayload(type, data);
      });
    });
    Ember['default'].Test.registerHelper('getStore', function(app) {
      return app.__container__.lookup('store:main');
    });
    return Ember['default'].Test.registerHelper('getCurrentUser', function(app) {
      return app.__container__.lookup('user:current');
    });
  })();

  exports['default'] = storeHelpers;

});