define('tahi/tests/integration/manuscript-manager-template-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/mock-server', '../helpers/factory'], function (Ember, ember_qunit, startApp, setupMockServer, Factory) {

  'use strict';

  var app, createCard, createJournalWithTaskTemplate, server;

  app = null;

  server = null;

  createJournalWithTaskTemplate = function(taskType) {
    var journal, jtt, mmt, pt;
    journal = Factory['default'].createRecord('Journal', {
      id: 1,
      _rootKey: 'admin_journal'
    });
    mmt = Factory['default'].createMMT(journal, {
      id: 1
    });
    pt = Factory['default'].createPhaseTemplate(mmt, {
      id: 1
    });
    jtt = Factory['default'].createJournalTaskType(journal, taskType);
    return [journal, mmt, pt, jtt];
  };

  module('Integration: Manuscript Manager Templates', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var admin, adminJournalPayload, adminJournalResponse, records, response;
      app = startApp['default']();
      server = setupMockServer['default']();
      records = createJournalWithTaskTemplate({
        kind: "Task",
        title: "Ad Hoc",
        id: 1
      });
      adminJournalPayload = Factory['default'].createPayload('admin_journal');
      adminJournalPayload.addRecords(records);
      adminJournalResponse = adminJournalPayload.toJSON();
      admin = Factory['default'].createRecord('User', {
        siteAdmin: true
      });
      server.respondWith('GET', /\/flows\/authorization/, [
        204, {
          'Tahi-Authorization-Check': 'true'
        }, ""
      ]);
      server.respondWith('GET', "/admin/journals/1", [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(adminJournalResponse)
      ]);
      server.respondWith('GET', "/admin/journals/authorization", [
        204, {
          "Content-Type": "application/html"
        }, ""
      ]);
      server.respondWith('GET', "/users/" + admin.id, [
        200, {
          'Content-Type': 'application/json'
        }, JSON.stringify({
          user: admin
        })
      ]);
      server.respondWith('DELETE', "/task_templates/1", [
        204, {
          "Content-Type": "application/json"
        }, JSON.stringify({})
      ]);
      server.respondWith('PUT', "/manuscript_manager_templates/1", [
        200, {
          "Content-Type": "application/json"
        }, '{}'
      ]);
      server.respondWith('PUT', "/phase_templates/1", [
        200, {
          "Content-Type": "application/json"
        }, '{}'
      ]);
      response = {
        "journal_task_types": [
          {
            "id": 1,
            "title": "Ad-hoc",
            "role": "user",
            "kind": "Task",
            "journal_id": 1
          }
        ],
        "task_template": {
          "id": 1,
          "template": [],
          "title": "Ad-hoc",
          "phase_template_id": 1,
          "journal_task_type_id": 1
        }
      };
      return server.respondWith('POST', "/task_templates", [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(response)
      ]);
    }
  });

  ember_qunit.test('Changing phase name', function() {
    var columnTitleSelect;
    columnTitleSelect = 'h2.column-title:contains("Phase 1")';
    visit("/admin/journals/1/manuscript_manager_templates/1/edit").then(function() {
      return ok(find(columnTitleSelect).length);
    });
    click(columnTitleSelect).then(function() {
      return Em.$(columnTitleSelect).html('Shazam!');
    });
    return andThen(function() {
      return ok(find('h2.column-title:contains("Shazam!")').length);
    });
  });

  ember_qunit.test('Adding an Ad-Hoc card', function() {
    visit("/admin/journals/1/manuscript_manager_templates/1/edit");
    click('a.button--green:contains("Add New Card")');
    pickFromChosenSingle('.task-type-select', 'Ad Hoc');
    click('.button--green:contains("Add")').then(function() {
      return ok(find('h1.inline-edit:contains("Ad Hoc")').length);
    });
    click('.adhoc-content-toolbar .glyphicon-plus');
    click('.adhoc-content-toolbar .adhoc-toolbar-item--text');
    andThen(function() {
      ok(find('h1.inline-edit').hasClass('editing'), "The title should be editable to start");
      Em.$('.inline-edit-form div[contenteditable]').html("New contenteditable, yahoo!").trigger('keyup');
      return click('.task-body .inline-edit-body-part .button--green:contains("Save")');
    });
    andThen(function() {
      assertText('.inline-edit', 'yahoo');
      return click('.inline-edit-body-part .glyphicon-trash');
    });
    andThen(function() {
      assertText('.inline-edit-body-part', 'Are you sure?');
      return click('.inline-edit-body-part .delete-button');
    });
    return andThen(function() {
      assertNoText('.inline-edit', 'yahoo');
      return click('.overlay-close-button:first');
    });
  });

  createCard = function() {
    visit("/admin/journals/1/manuscript_manager_templates/1/edit");
    click('a.button--green:contains("Add New Card")');
    pickFromChosenSingle('.task-type-select', 'Ad Hoc');
    click('.button--green:contains("Add")').then(function() {
      return ok(find('h1.inline-edit:contains("Ad Hoc")').length, 'It finds teh ad hocs');
    });
    return andThen(function() {
      return click('.overlay-close-button:first');
    });
  };

  ember_qunit.test('show delete confirmation overlay on deletion of a card', function() {
    createCard();
    andThen(function() {
      return click(".card-remove");
    });
    andThen(function() {
      equal(find('.overlay button:contains("Yes, Delete this Card")').length, 1);
      return click(find('.overlay button:contains("Yes, Delete this Card")'));
    });
    return andThen(function() {
      return equal(0, find('.card-content').length);
    });
  });

  ember_qunit.test('click delete confirmation overlay cancel button', function() {
    createCard();
    return andThen(function() {
      equal(find(".card-content").length, 1);
      $("div.card .card-remove").show();
      click("div.card .card-remove");
      click('.overlay button:contains("cancel")');
      return equal(find(".card-content").length, 1);
    });
  });

  ember_qunit.test('click delete confirmation overlay submit button', function() {
    createCard();
    andThen(function() {
      return click('.paper-type-save-button:contains("Save Template")');
    });
    andThen(function() {
      equal(find(".card-content").length, 1, "It finds the card content");
      $("div.card .card-remove").show();
      click("div.card .card-remove");
      return click('.overlay button:contains("Yes, Delete this Card")');
    });
    andThen(function() {
      return equal(find(".card-content").length, 0, "The card is gone");
    });
    return andThen(function() {
      var search;
      search = {
        method: "DELETE",
        url: "/task_templates/1"
      };
      return ok(_.findWhere(server.requests, search), "It sends DELETE request to the server");
    });
  });

});