define('tahi/helpers/user-thumbnail', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var UserThumbnail;

  UserThumbnail = Ember['default'].Handlebars.makeBoundHelper(function(user) {
    var name, url;
    url = user.get('avatarUrl');
    name = user.get('name');
    return new Handlebars.SafeString("<img alt='" + name + "' class='user-thumbnail' src='" + url + "' data-toggle='tooltip' title='" + name + "' />");
  });

  exports['default'] = UserThumbnail;

});