define('tahi/pods/paper/manage/controller', ['exports', 'ember', 'tahi/services/rest-less'], function (exports, Ember, RESTless) {

  'use strict';

  var PaperManageController;

  PaperManageController = Ember['default'].ObjectController.extend({
    positionSort: ["position:asc"],
    sortedPhases: Ember['default'].computed.sort('model.phases', 'positionSort'),
    updatePositions: function(phase) {
      var relevantPhases;
      relevantPhases = this.get('model.phases').filter(function(p) {
        return p !== phase && p.get('position') >= phase.get('position');
      });
      return relevantPhases.invoke('incrementProperty', 'position');
    },
    paper: Ember['default'].computed.alias('model'),
    canRemoveCard: true,
    actions: {
      addPhase: function(position) {
        var paper, phase;
        paper = this.get('model');
        phase = this.store.createRecord('phase', {
          position: position,
          name: "New Phase",
          paper: paper
        });
        this.updatePositions(phase);
        return phase.save().then(function() {
          return paper.reload();
        });
      },
      changeTaskPhase: function(task, targetPhase) {
        task.set('phase', targetPhase);
        return task.save();
      },
      removePhase: function(phase) {
        var paper;
        paper = phase.get('paper');
        return phase.destroyRecord().then(function() {
          return paper.reload();
        });
      },
      savePhase: function(phase) {
        return phase.save();
      },
      rollbackPhase: function(phase) {
        return phase.rollback();
      },
      toggleEditable: function() {
        return RESTless['default'].putUpdate(this.get('model'), '/toggle_editable')["catch"]((function(_this) {
          return function(_arg) {
            var message, model, status;
            status = _arg.status, model = _arg.model;
            message = (function() {
              switch (status) {
                case 422:
                  return model.get('errors.messages') + " You should probably reload.";
                case 403:
                  return "You weren't authorized to do that";
                default:
                  return "There was a problem saving.  Please reload.";
              }
            })();
            return _this.flash.displayMessage('error', message);
          };
        })(this));
      }
    }
  });

  exports['default'] = PaperManageController;

});