define('tahi/views/funder', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var FunderView;

  FunderView = Ember['default'].View.extend({
    templateName: 'standard_tasks/funder',
    change: function(e) {
      return this.get('controller').send('funderDidChange');
    }
  });

  exports['default'] = FunderView;

});