define('tahi/pods/components/dashboard-link/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DashboardLinkComponent;

  DashboardLinkComponent = Ember['default'].Component.extend({
    paperId: Ember['default'].computed.alias('model.id'),
    linkIdentifier: (function() {
      return this.get('model.doi') || this.get('model.id');
    }).property('model.id', 'model.doi'),
    unreadCommentsList: Ember['default'].computed('unreadComments.@each.readAt', 'unreadComments.@each.paperId', function() {
      var paperId;
      paperId = this.get('paperId');
      return this.get('unreadComments').filter(function(c) {
        return c.get('paperId') === paperId && !c.get('readAt');
      });
    }),
    unreadCommentsCount: (function() {
      return this.get('unreadCommentsList.length');
    }).property('unreadCommentsList.length'),
    refreshTooltips: function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, (function(_this) {
        return function() {
          if (_this.$()) {
            return _this.$('.link-tooltip').tooltip('destroy').tooltip({
              placement: 'bottom'
            });
          }
        };
      })(this));
    },
    setupTooltips: (function() {
      this.addObserver('model.unreadCommentsCount', this, this.refreshTooltips);
      return this.refreshTooltips();
    }).on('didInsertElement'),
    teardownTooltips: (function() {
      return this.removeObserver('model.unreadCommentsCount', this, this.refreshTooltips);
    }).on('willDestroyElement'),
    badgeTitle: (function() {
      return "" + (this.get('unreadCommentsCount')) + " new posts";
    }).property('unreadCommentsCount')
  });

  exports['default'] = DashboardLinkComponent;

});