define('tahi/services/utils', ['exports', 'ember', 'tahi/config/environment'], function (exports, Ember, ENV) {

  'use strict';

  var Utils;

  Utils = Ember['default'].Namespace.create({
    camelizeKeys: function(object) {
      var camelized;
      camelized = {};
      Ember['default'].keys(object).forEach(function(key) {
        return camelized[Ember['default'].String.camelize(key)] = object[key];
      });
      return camelized;
    },
    deepCamelizeKeys: function(hash) {
      var spelunk;
      spelunk = function(thing) {
        if (!thing || typeof thing !== 'object') {
          return thing;
        }
        if (Ember['default'].isArray(thing)) {
          return thing.map(spelunk);
        }
        return Ember['default'].keys(thing).reduce(function(previousValue, key) {
          previousValue[Ember['default'].String.camelize(key)] = spelunk(thing[key]);
          return previousValue;
        }, {});
      };
      return spelunk(hash);
    },
    deepJoinArrays: function(hash) {
      var spelunk;
      spelunk = function(thing) {
        Ember['default'].keys(thing).forEach(function(key) {
          if (!thing || typeof thing !== 'object') {
            return thing;
          }
          if (Ember['default'].isArray(thing[key])) {
            return thing[key] = thing[key].join(', ');
          } else {
            return spelunk(thing[key]);
          }
        });
        return thing;
      };
      return spelunk(hash);
    },
    generateUUID: function() {
      var d;
      d = new Date().getTime();
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r;
        r = (d + Math.random() * 16) % 16 | 0;
        d = Math.floor(d / 16);
        if (c === 'x') {
          return r.toString(16);
        } else {
          return (r & 0x7 | 0x8).toString(16);
        }
      });
    },
    windowLocation: function(url) {
      return window.location = url;
    },
    windowHistory: function() {
      return window.history;
    },
    resizeColumnHeaders: function() {
      var error, headers, max, wrappers;
      headers = $('.column-header');
      if (!headers.length) {
        return;
      }
      wrappers = headers.find('.column-title-wrapper');
      wrappers.css('height', '');
      max = null;
      try {
        max = Math.max.apply(Math, wrappers.map(function() {
          return $(this).outerHeight();
        }));
      } catch (_error) {
        error = _error;
        max = 20;
      }
      wrappers.css('height', max);
      return $('.column-content').css('top', headers.first().outerHeight());
    },
    togglePropertyAfterDelay: function(obj, prop, startVal, endVal, ms) {
      obj.set(prop, startVal);
      return setTimeout(function() {
        return Ember['default'].run.schedule('actions', obj, 'set', prop, endVal);
      }, ms);
    },
    debug: function(description, obj) {
      if (ENV['default'].environment === 'development' || ENV['default'].environment === 'test' || Ember['default'].testing) {
        console.groupCollapsed(description);
        console.log(Ember['default'].copy(obj, true));
        return console.groupEnd();
      }
    },
    deNamespaceTaskType: function(typeString) {
      var taskTypeNames;
      taskTypeNames = typeString.split('::');
      if (taskTypeNames.length === 1) {
        return typeString;
      }
      if (taskTypeNames[0] !== 'Task') {
        return taskTypeNames[1];
      }
      throw new Error("The task type: '" + typeString + "' is not qualified.");
    }
  });

  exports['default'] = Utils;

});