define('tahi/models/phase-template', ['exports', 'ember', 'ember-data'], function (exports, Ember, DS) {

  'use strict';

  var PhaseTemplate, a;

  a = DS['default'].attr;

  PhaseTemplate = DS['default'].Model.extend({
    manuscriptManagerTemplate: DS['default'].belongsTo('manuscriptManagerTemplate'),
    taskTemplates: DS['default'].hasMany('taskTemplate'),
    name: a('string'),
    position: a('number'),
    noTasks: Ember['default'].computed.empty('taskTemplates')
  });

  exports['default'] = PhaseTemplate;

});