define('tahi/models/affiliation', ['exports', 'ember', 'ember-data'], function (exports, Ember, DS) {

  'use strict';

  var Affiliation, a;

  a = DS['default'].attr;

  Affiliation = DS['default'].Model.extend({
    user: DS['default'].belongsTo('user'),
    name: a('string'),
    endDate: a('string'),
    startDate: a('string'),
    email: a('string'),
    isCurrent: (function() {
      return Ember['default'].isBlank(this.get('endDate'));
    }).property('endDate'),
    displayEndDate: (function() {
      return this.get('endDate') || 'Current';
    }).property('endDate')
  });

  exports['default'] = Affiliation;

});