define('tahi/pods/components/direct-download-link/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DirectDownloadLinkComponent;

  DirectDownloadLinkComponent = Ember['default'].Component.extend({
    downloadLink: (function() {
      return this.get('link') + (this.get('extension') || '');
    }).property('link')
  });

  exports['default'] = DirectDownloadLinkComponent;

});