define('tahi/tests/controllers/base-paper-test', ['ember', '../helpers/start-app', 'ember-qunit'], function (Ember, startApp, ember_qunit) {

  'use strict';

  ember_qunit.moduleFor('controller:base-paper', 'BasePaperController', {
    needs: ['controller:application'],
    setup: function() {
      startApp['default']();
      this.phase1 = Ember['default'].Object.create({
        position: 1
      });
      this.phase2 = Ember['default'].Object.create({
        position: 2
      });
      this.phase3 = Ember['default'].Object.create({
        position: 3
      });
      this.phase4 = Ember['default'].Object.create({
        position: 4
      });
      this.paper = Ember['default'].Object.create({
        title: 'test paper',
        phases: []
      });
      return sinon.stub(jQuery, "ajax");
    },
    teardown: function() {
      return jQuery.ajax.restore();
    }
  });

  ember_qunit.test('#export: calls the export url in Tahi', function() {
    var basePaperController, downloadType;
    basePaperController = this.subject();
    basePaperController.set('model', this.paper);
    downloadType = {
      url: "http://example.com",
      format: "docx"
    };
    basePaperController.send('export', downloadType);
    return ok(jQuery.ajax.calledWithMatch({
      url: "/papers/" + this.paper.id + "/export",
      data: {
        format: downloadType.format
      }
    }));
  });

});