define('tahi/tests/helpers/setups', ['exports', '../helpers/factory'], function (exports, Factory) {

  'use strict';

  var addUserAsParticipant, paperWithParticipant, paperWithRoles, paperWithTask;

  exports.paperWithParticipant = paperWithParticipant = function() {
    var journal, litePaper, paper, participation, phase, task, user;
    journal = Factory['default'].createRecord('Journal', {
      id: 1
    });
    paper = Factory['default'].createRecord('Paper', {
      journal_id: journal.id,
      id: 1
    });
    litePaper = Factory['default'].createLitePaper(paper);
    phase = Factory['default'].createPhase(paper);
    task = Factory['default'].createTask('Task', paper, phase);
    user = Factory['default'].createRecord('User', {
      full_name: 'Some Guy'
    });
    participation = addUserAsParticipant(task, user);
    return Factory['default'].createPayload('paper').addRecords([journal, paper, litePaper, phase, task, user, participation]);
  };

  exports.paperWithTask = paperWithTask = function(taskType, taskAttrs) {
    var journal, litePaper, paper, phase, task;
    journal = Factory['default'].createRecord('Journal', {
      id: 1
    });
    paper = Factory['default'].createRecord('Paper', {
      journal_id: journal.id,
      editable: true
    }, Factory['default'].getNewId('paper'));
    litePaper = Factory['default'].createLitePaper(paper);
    phase = Factory['default'].createPhase(paper);
    task = Factory['default'].createTask(taskType, paper, phase, taskAttrs);
    return [paper, task, journal, litePaper, phase];
  };

  addUserAsParticipant = function(task, user) {
    var participation;
    participation = Factory['default'].createRecord('Participation', {
      task: {
        id: task.id,
        type: task.type
      },
      participant_id: user.id
    });
    Factory['default'].mergeArrays(task, 'participant_ids', user.id);
    return participation;
  };

  exports.paperWithRoles = paperWithRoles = function(id, roles) {
    var journal, litePaper, paper;
    journal = Factory['default'].createRecord('Journal', {
      id: 1
    });
    paper = Factory['default'].createRecord('Paper', {
      journal_id: journal.id,
      id: id
    });
    litePaper = Factory['default'].createLitePaperWithRoles(paper, roles);
    return [paper, journal, litePaper];
  };

});