define('tahi/pods/components/question-check/component', ['exports', 'tahi/pods/components/question/component'], function (exports, QuestionComponent) {

  'use strict';

  var QuestionCheckComponent;

  QuestionCheckComponent = QuestionComponent['default'].extend({
    displayContent: Em.computed.oneWay('checked'),
    checked: (function(key, value, oldValue) {
      var answer;
      if (arguments.length > 1) {
        return this.set('model.answer', value);
      } else {
        answer = this.get('model.answer');
        return answer === 'true' || answer === true;
      }
    }).property('model.answer'),
    actions: {
      additionalDataAction: function() {
        return this.get('additionalData').pushObject({});
      }
    }
  });

  exports['default'] = QuestionCheckComponent;

});