define('tahi/controllers/overlays/paper-submit', ['exports', 'ember', 'tahi/services/rest-less'], function (exports, Ember, RESTless) {

  'use strict';

  var PaperSubmitOverlayController;

  PaperSubmitOverlayController = Ember['default'].Controller.extend({
    overlayClass: 'overlay--fullscreen paper-submit-overlay',
    actions: {
      submit: function() {
        return RESTless['default'].putUpdate(this.get('model'), "/submit").then((function(_this) {
          return function() {
            return _this.transitionToRoute('application');
          };
        })(this))["catch"]((function(_this) {
          return function(_arg) {
            var message, model, status;
            status = _arg.status, model = _arg.model;
            message = (function() {
              switch (status) {
                case 422:
                  return model.get('errors.messages') + " You should probably reload.";
                case 403:
                  return "You weren't authorized to do that";
                default:
                  return "There was a problem saving. Please reload.";
              }
            })();
            return _this.flash.displayMessage('error', message);
          };
        })(this));
      }
    }
  });

  exports['default'] = PaperSubmitOverlayController;

});