define('tahi/components/figure-thumbnail', ['exports', 'ember', 'tahi/pods/components/attachment-thumbnail/component'], function (exports, Ember, AttachmentThumbnailComponent) {

  'use strict';

  var FigureThumbnailComponent;

  FigureThumbnailComponent = AttachmentThumbnailComponent['default'].extend({
    attachmentType: 'figure',
    actions: {
      toggleStrikingImageFromCheckbox: function(checkbox) {
        var newValue;
        newValue = checkbox.get('checked') ? checkbox.get('attachment.id') : null;
        return this.sendAction('action', newValue);
      },
      saveAttachment: function() {
        this.get('attachment').save().then((function(_this) {
          return function() {
            return _this.sendAction('saveAttachment');
          };
        })(this));
        return this.set('editState', false);
      }
    }
  });

  exports['default'] = FigureThumbnailComponent;

});