define('tahi/pods/admin/journal/thumbnail/view', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var JournalThumbnailView;

  JournalThumbnailView = Ember['default'].View.extend({
    togglePreview: (function() {
      return Ember['default'].run.schedule('afterRender', (function(_this) {
        return function() {
          if (_this.get('controller.logoPreview')) {
            return _this.$('.journal-logo-preview').empty().append(_this.get('controller.logoPreview'));
          } else {
            return _this.$('.journal-logo-preview').html('');
          }
        };
      })(this));
    }).observes('controller.logoPreview')
  });

  exports['default'] = JournalThumbnailView;

});