define('tahi/pods/paper/index/view', ['exports', 'ember', 'tahi/mixins/views/redirects-if-editable'], function (exports, Ember, RedirectsIfEditable) {

  'use strict';

  var PaperIndexView;

  PaperIndexView = Ember['default'].View.extend(RedirectsIfEditable['default'], {
    subNavVisible: false,
    downloadsVisible: false,
    contributorsVisible: false,
    applyManuscriptCss: (function() {
      return $('#paper-body').attr('style', this.get('controller.model.journal.manuscriptCss'));
    }).on('didInsertElement'),
    setBackgroundColor: (function() {
      return $('html').addClass('matte paper-submitted');
    }).on('didInsertElement'),
    resetBackgroundColor: (function() {
      return $('html').removeClass('matte paper-submitted');
    }).on('willDestroyElement'),
    subNavVisibleDidChange: (function() {
      if (this.get('subNavVisible')) {
        $('.oo-ui-toolbar').css('top', '103px');
        $('#tahi-container').addClass('sub-nav-visible');
        return $('html').addClass('control-bar-sub-nav-active');
      } else {
        $('.oo-ui-toolbar').css('top', '60px');
        $('#tahi-container').removeClass('sub-nav-visible');
        return $('html').removeClass('control-bar-sub-nav-active');
      }
    }).observes('subNavVisible'),
    teardownControlBarSubNav: (function() {
      return $('html').removeClass('control-bar-sub-nav-active');
    }).on('willDestroyElement'),
    actions: {
      showSubNav: function(sectionName) {
        if (this.get('subNavVisible') && this.get("" + sectionName + "Visible")) {
          return this.send('hideSubNav');
        } else {
          this.set('subNavVisible', true);
          return this.send("show" + (sectionName.capitalize()));
        }
      },
      hideSubNav: function() {
        return this.setProperties({
          subNavVisible: false,
          contributorsVisible: false,
          downloadsVisible: false
        });
      },
      showContributors: function() {
        this.set('contributorsVisible', true);
        return this.set('downloadsVisible', false);
      },
      showDownloads: function() {
        this.set('contributorsVisible', false);
        return this.set('downloadsVisible', true);
      }
    }
  });

  exports['default'] = PaperIndexView;

});