define('tahi/helpers/make-plural', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var Pluralize;

  Pluralize = Ember['default'].Handlebars.makeBoundHelper(function(number, single, plural) {
    if (number === 1) {
      return single;
    } else {
      return plural;
    }
  });

  exports['default'] = Pluralize;

});