define('tahi/services/rest-less', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var RESTless;

  RESTless = Ember['default'].Namespace.create({
    ajaxPromise: function(method, path, data) {
      return new Ember['default'].RSVP.Promise(function(resolve, reject) {
        return Ember['default'].$.ajax({
          url: path,
          type: method,
          data: data,
          success: resolve,
          error: reject,
          dataType: 'json'
        });
      });
    },
    "delete": function(path, data) {
      return this.ajaxPromise('DELETE', path, data);
    },
    put: function(path, data) {
      return this.ajaxPromise('PUT', path, data);
    },
    post: function(path, data) {
      return this.ajaxPromise('POST', path, data);
    },
    get: function(path, data) {
      return this.ajaxPromise('GET', path, data);
    },
    putModel: function(model, path, data) {
      return this.put("" + (model.path()) + path, data);
    },
    putUpdate: function(model, path, data) {
      return this.putModel(model, path).then(function(response) {
        return model.get('store').pushPayload(response);
      }, function(xhr) {
        var errors, modelErrors;
        if (errors = xhr.responseJSON.errors) {
          errors = Utils['default'].camelizeKeys(errors);
          modelErrors = model.get('errors');
          Ember['default'].keys(errors).forEach(function(key) {
            return modelErrors.add(key, errors[key]);
          });
        }
        throw {
          status: xhr.status,
          model: model
        };
      });
    },
    authorize: function(controller, url, property) {
      var authorize;
      authorize = function(value) {
        return function(result) {
          return Ember['default'].run(function() {
            return controller.set(property, value);
          });
        };
      };
      return Ember['default'].$.ajax(url, {
        success: authorize(true),
        error: authorize(false)
      });
    }
  });

  exports['default'] = RESTless;

});