define('tahi/pods/admin/roles/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var RolesIndexController;

  RolesIndexController = Ember['default'].ArrayController.extend({
    needs: ['admin/journal'],
    journal: Ember['default'].computed.alias('controllers.journal.model'),
    actions: {
      addRole: function() {
        var role;
        role = this.store.createRecord('role');
        return this.get('content').unshiftObject(role);
      },
      deleteRole: function(role) {
        return role.destroyRecord();
      }
    }
  });

  exports['default'] = RolesIndexController;

});