define('tahi/models/journal-task-type', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var JournalTaskType, a;

  a = DS['default'].attr;

  JournalTaskType = DS['default'].Model.extend({
    journal: DS['default'].belongsTo('journal'),
    title: a('string'),
    role: a('string'),
    kind: a('string')
  });

  exports['default'] = JournalTaskType;

});