define('tahi/initializers/reopen-link-to', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ReopenLinkTo;

  ReopenLinkTo = {
    name: 'reopenLinkTo',
    initialize: function(container, application) {
      return Ember['default'].LinkView.reopen({
        attributeBindings: ['data-toggle', 'data-placement', 'title']
      });
    }
  };

  exports['default'] = ReopenLinkTo;

});