define('tahi/models/user', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var User, a;

  a = DS['default'].attr;

  User = DS['default'].Model.extend({
    siteAdmin: a('boolean'),
    affiliations: DS['default'].hasMany('affiliation'),
    email: a('string'),
    fullName: a('string'),
    firstName: a('string'),
    avatarUrl: a('string'),
    username: a('string'),
    name: Ember.computed.alias('fullName'),
    affiliationSort: ['isCurrent:desc', 'endDate:desc', 'startDate:asc'],
    affiliationsByDate: Ember.computed.sort('affiliations', 'affiliationSort')
  });

  exports['default'] = User;

});