define('tahi/controllers/overlays/feedback', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var FeedbackController;

  FeedbackController = Ember['default'].Controller.extend({
    overlayClass: 'overlay--fullscreen feedback-overlay',
    feedbackSubmitted: false,
    isUploading: false,
    setupModel: (function() {
      this.resetModel();
      this.set('model.referrer', window.location);
      return this.set('model.screenshots', []);
    }).on('init'),
    resetModel: function() {
      return this.set('model', this.store.createRecord('feedback'));
    },
    actions: {
      submit: function() {
        return this.get('model').save().then((function(_this) {
          return function(feedback) {
            _this.set('feedbackSubmitted', true);
            return _this.resetModel();
          };
        })(this));
      },
      closeAction: function() {
        this.send('closeOverlay');
        return this.set('feedbackSubmitted', false);
      },
      uploadFinished: function(data, filename) {
        this.set('isUploading', false);
        return this.get('model.screenshots').pushObject({
          url: data,
          filename: filename
        });
      },
      uploadStarted: function(data, filename) {
        return this.set('isUploading', true);
      },
      removeScreenshot: function(screenshot) {
        return this.get('model.screenshots').removeObject(screenshot);
      }
    }
  });

  exports['default'] = FeedbackController;

});