define('tahi/pods/profile/controller', ['exports', 'ember', 'tahi/mixins/file-upload', 'tahi/mixins/validation-errors', 'tahi/services/utils'], function (exports, Ember, FileUploadMixin, ValidationErrorsMixin, Utils) {

  'use strict';

  var ProfileController;

  ProfileController = Ember['default'].ObjectController.extend(FileUploadMixin['default'], ValidationErrorsMixin['default'], {
    showAffiliationForm: false,
    errorText: "",
    affiliations: Ember['default'].computed.alias("model.affiliationsByDate"),
    avatarUploadUrl: (function() {
      return "/users/" + (this.get('model.id')) + "/update_avatar";
    }).property('id'),
    selectableInstitutions: (function() {
      return this.get('model.institutions').map(function(institution) {
        return {
          id: institution,
          text: institution
        };
      });
    }).property('model.institutions'),
    actions: {
      uploadFinished: function(data, filename) {
        this.uploadFinished(data, filename);
        return this.set('model.avatarUrl', data.avatar_url);
      },
      hideNewAffiliationForm: function() {
        this.clearValidationErrors();
        this.set('showAffiliationForm', false);
        if (this.get('newAffiliation.isNew')) {
          return this.get('newAffiliation').deleteRecord();
        }
      },
      showNewAffiliationForm: function() {
        this.set('newAffiliation', this.store.createRecord('affiliation'));
        return this.set('showAffiliationForm', true);
      },
      removeAffiliation: function(affiliation) {
        if (confirm('Are you sure you want to destroy this affiliation?')) {
          return affiliation.destroyRecord();
        }
      },
      commitAffiliation: function(affiliation) {
        affiliation.set('user', this.get('model'));
        this.clearValidationErrors();
        return affiliation.save().then((function(_this) {
          return function(affiliation) {
            affiliation.get('user.affiliations').addObject(affiliation);
            return _this.send('hideNewAffiliationForm');
          };
        })(this), (function(_this) {
          return function(response) {
            affiliation.set('user', null);
            return _this.displayValidationErrorsFromResponse(response);
          };
        })(this));
      }
    }
  });

  exports['default'] = ProfileController;

});