define('tahi/controllers/overlays/financial-disclosure', ['exports', 'tahi/pods/task/controller'], function (exports, TaskController) {

  'use strict';

  var FinancialDisclosureOverlayController;

  FinancialDisclosureOverlayController = TaskController['default'].extend({
    task: Em.computed.alias("model"),
    funders: Em.computed.alias("task.funders"),
    paper: Em.computed.alias("task.paper"),
    authors: Em.computed.alias('task.authors'),
    receivedFunding: (function() {
      return this.get('funders.length') > 0;
    }).property('funders.@each', 'funders.[]'),
    receivedNoFunding: Em.computed.not('receivedFunding'),
    actions: {
      choseFundingReceived: function() {
        if (this.get('funders.length') < 1) {
          return this.send('addFunder');
        }
      },
      choseFundingNotReceived: function() {
        return this.get('funders').toArray().forEach(function(funder) {
          if (funder.get('isNew')) {
            return funder.deleteRecord();
          } else {
            return funder.destroyRecord();
          }
        });
      },
      addFunder: function() {
        return this.get('funders').pushObject(this.store.createRecord('funder', {
          task: this.get('task')
        }));
      }
    }
  });

  exports['default'] = FinancialDisclosureOverlayController;

});