define('tahi/services/event-stream', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var EventStream, interval;

  interval = 500;

  EventStream = Ember['default'].Object.extend({
    eventSource: null,
    channels: null,
    messageQueue: null,
    wait: false,
    init: function() {
      this.set('messageQueue', []);
      this.resetChannels();
      return this.processMessages();
    },
    addEventListener: function(eventName) {
      return this.get('eventSource').addEventListener(eventName, this.msgEnqueue.bind(this));
    },
    msgEnqueue: function(msg) {
      return this.get('messageQueue').unshiftObject(msg);
    },
    processMessages: function() {
      var description, msg;
      if (!this.get('wait')) {
        if (msg = this.messageQueue.popObject()) {
          msg.parsedData = JSON.parse(msg.data);
          if (this.shouldProcessMessage(msg)) {
            description = "Event Stream (" + msg.type + "): " + msg.parsedData.subscription_name;
            Utils['default'].debug(description, msg);
            this.msgResponse(msg.parsedData);
          }
        }
      }
      return Ember['default'].run.later(this, 'processMessages', [], interval);
    },
    shouldProcessMessage: function(msg) {
      return this.get('channels').contains(msg.type) || msg.parsedData.action === 'destroyed';
    },
    pause: function() {
      return this.set('wait', true);
    },
    play: function() {
      return this.set('wait', false);
    },
    stop: function() {
      if (this.get('eventSource')) {
        return this.get('eventSource').close();
      }
    },
    resetChannels: function() {
      var params;
      this.pause();
      this.stop();
      params = {
        url: '/event_stream',
        method: 'GET',
        success: (function(_this) {
          return function(data) {
            if (data.enabled === 'false') {
              return;
            }
            _this.set('eventSource', new EventSource(data.url));
            Ember['default'].$(window).unload(function() {
              return _this.stop();
            });
            _this.set('channels', data.channels);
            Utils['default'].debug("Event Stream: updated channels", data.channels);
            data.channels.forEach(function(eventName) {
              return _this.addEventListener(eventName);
            });
            return _this.play();
          };
        })(this)
      };
      return Ember['default'].$.ajax(params);
    },
    msgResponse: function(esData) {
      var action;
      action = esData.action;
      delete esData.action;
      delete esData.subscription_name;
      return (this.eventStreamActions[action] || function() {
        return null;
      }).call(this, esData);
    },
    applicationSerializer: (function() {
      return this.store.container.lookup("serializer:application");
    }).property(),
    eventStreamActions: {
      created: function(esData) {
        return Ember['default'].run((function(_this) {
          return function() {
            return _this.store.pushPayload(esData);
          };
        })(this));
      },
      updated: function(esData) {
        return Ember['default'].run((function(_this) {
          return function() {
            return _this.store.pushPayload(esData);
          };
        })(this));
      },
      destroyed: function(esData) {
        var type;
        type = this.get('applicationSerializer').typeForRoot(esData.type);
        return esData.ids.forEach((function(_this) {
          return function(id) {
            var record;
            if (type === "task") {
              record = _this.store.findTask(id);
            } else {
              record = _this.store.getById(type, id);
            }
            if (record) {
              return record.unloadRecord();
            }
          };
        })(this));
      }
    }
  });

  exports['default'] = EventStream;

});