define('tahi/mixins/controllers/builds-task-template', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var BuildsTaskTemplate;

  BuildsTaskTemplate = Ember['default'].Mixin.create({
    newBlocks: [],
    blocks: null,
    emailSentStates: null,
    _init: (function() {
      return this.set('newBlocks', []);
    }).on('init'),
    setEmailStates: (function() {
      return this.set('emailSentStates', Ember['default'].ArrayProxy.create({
        content: []
      }));
    }).on('init'),
    isNew: function(block) {
      return this.get('newBlocks').contains(block);
    },
    replaceBlock: function(block, otherBlock) {
      var blocks, position;
      blocks = this.get('blocks');
      position = blocks.indexOf(block);
      if (position !== -1) {
        return Ember['default'].EnumerableUtils.replace(blocks, position, 1, [otherBlock]);
      }
    },
    _pruneEmptyItems: function(block) {
      return block.reject(function(item) {
        return Em.isEmpty(item.value);
      });
    },
    actions: {
      setTitle: function(title) {
        return this.set('title', title);
      },
      addTextBlock: function() {
        return this.get('newBlocks').pushObject([
          {
            type: "text",
            value: ""
          }
        ]);
      },
      addChecklist: function() {
        return this.get('newBlocks').pushObject([
          {
            type: "checkbox",
            value: "",
            answer: false
          }
        ]);
      },
      addEmail: function() {
        return this.get('newBlocks').pushObject([
          {
            type: "email",
            subject: "",
            value: "",
            sent: ""
          }
        ]);
      },
      saveBlock: function(block) {
        if (this.isNew(block)) {
          this.get('blocks').pushObject(block);
          this.get('newBlocks').removeObject(block);
        }
        return this.replaceBlock(block, this._pruneEmptyItems(block));
      },
      resetBlock: function(block, snapshot) {
        if (this.isNew(block)) {
          return this.get('newBlocks').removeObject(block);
        } else {
          return this.replaceBlock(block, snapshot);
        }
      },
      addCheckboxItem: function(block) {
        return block.pushObject({
          type: "checkbox",
          value: "",
          answer: false
        });
      },
      deleteItem: function(item, block) {
        block.removeObject(item);
        if (Ember['default'].isEmpty(block)) {
          return this.send('deleteBlock', block);
        }
      },
      deleteBlock: function(block) {
        if (this.isNew(block)) {
          return this.get('newBlocks').removeObject(block);
        } else {
          return this.get('blocks').removeObject(block);
        }
      }
    }
  });

  exports['default'] = BuildsTaskTemplate;

});