define('tahi/pods/flow-manager/view', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var FlowManagerView;

  FlowManagerView = Ember['default'].View.extend({
    columnCountDidChange: (function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, Utils['default'].resizeColumnHeaders);
    }).on('didInsertElement').observes('controller.model.@each')
  });

  exports['default'] = FlowManagerView;

});