define('tahi/pods/admin/journal/manuscript-manager-template/edit/route', ['exports', 'ember', 'tahi/mixins/routes/alert-unsaved-changes'], function (exports, Ember, AlertUnsavedChanges) {

  'use strict';

  var ManuscriptManagerTemplateEditRoute;

  ManuscriptManagerTemplateEditRoute = Ember['default'].Route.extend(AlertUnsavedChanges['default'], {
    model: function(params) {
      return this.store.find('manuscriptManagerTemplate', params.manuscript_manager_template_id);
    },
    afterModel: function(model) {
      if (!model.get('phaseTemplates.length')) {
        return model.reload();
      }
    },
    actions: {
      saveChanges: function() {
        return this.controller.send('saveTemplate', this.get('attemptingTransition'));
      },
      didRollBack: function() {}
    }
  });

  exports['default'] = ManuscriptManagerTemplateEditRoute;

});