define('tahi/helpers/format-number', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var FormatNumber;

  FormatNumber = Ember['default'].Handlebars.makeBoundHelper(function(number) {
    if (number) {
      return number.toString().replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1,");
    } else {
      return 0;
    }
  });

  exports['default'] = FormatNumber;

});