define('tahi/pods/admin/journal/index/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var JournalIndexController;

  JournalIndexController = Ember['default'].ObjectController.extend({
    epubCssSaveStatus: '',
    pdfCssSaveStatus: '',
    manuscriptCssSaveStatus: '',
    doiEditState: false,
    doiStartNumberEditable: true,
    epubCoverUploadUrl: (function() {
      return "/admin/journals/" + (this.get('model.id')) + "/upload_epub_cover";
    }).property(),
    adminJournalUsers: null,
    epubCoverUploading: false,
    resetSearch: function() {
      this.set('adminJournalUsers', null);
      return this.set('placeholderText', null);
    },
    logo: (function() {
      var logoUrl;
      logoUrl = this.get("logoUrl");
      if (/no-journal-image/.test(logoUrl)) {
        return false;
      } else {
        return logoUrl;
      }
    }).property('logoUrl'),
    journalUrl: (function() {
      return "/admin/journals/" + (this.get('model.id'));
    }).property('model.id'),
    epubCoverUploadedAgo: (function() {
      var uploadTime;
      uploadTime = this.get('epubCoverUploadedAt');
      if (uploadTime) {
        return $.timeago(this.get('epubCoverUploadedAt'));
      } else {
        return null;
      }
    }).property('epubCoverUploadedAt'),
    formattedDOI: (function() {
      var journal, publisher, start;
      if (this.get('doiInvalid')) {
        return '';
      } else {
        publisher = this.get('doiPublisherPrefix');
        journal = this.get('doiJournalPrefix');
        start = this.get('lastDoiIssued');
        return "" + publisher + "/" + journal + (Ember['default'].isEmpty(journal) ? '' : '.') + start;
      }
    }).property('doiPublisherPrefix', 'doiJournalPrefix', 'lastDoiIssued'),
    doiInvalid: (function() {
      return Ember['default'].isEmpty(this.get('doiPublisherPrefix')) || Ember['default'].isEmpty(this.get('lastDoiIssued')) || this.get('doiStartNumberInvalid');
    }).property('doiPublisherPrefix', 'lastDoiIssued'),
    doiStartNumberInvalid: (function() {
      return !$.isNumeric(this.get('lastDoiIssued')) && !Ember['default'].isEmpty(this.get('doiStartNumber'));
    }).property('lastDoiIssued'),
    actions: {
      searchUsers: function() {
        this.resetSearch();
        return this.store.find('AdminJournalUser', {
          query: this.get('searchQuery'),
          journal_id: this.get('model.id')
        }).then((function(_this) {
          return function(users) {
            _this.set('adminJournalUsers', users);
            if (Ember['default'].isEmpty(_this.get('adminJournalUsers'))) {
              return _this.set('placeholderText', "No matching users found");
            }
          };
        })(this));
      },
      epubCoverUploading: function() {
        return this.set('epubCoverUploading', true);
      },
      epubCoverUploaded: function(data) {
        var journal;
        this.set('epubCoverUploading', false);
        journal = data.admin_journal;
        return this.setProperties({
          epubCoverUrl: journal.epub_cover_url,
          epubCoverFileName: journal.epub_cover_file_name,
          epubCoverUploadedAt: journal.epub_cover_uploaded_at
        });
      },
      resetSaveStatuses: function() {
        return this.setProperties({
          epubCssSaveStatus: '',
          pdfCssSaveStatus: '',
          manuscriptCssSaveStatus: ''
        });
      },
      editDOI: function() {
        return this.set('doiEditState', true);
      },
      cancelDOI: function() {
        this.get('model').rollback();
        return this.set('doiEditState', false);
      },
      saveDOI: function() {
        if (this.get('doiInvalid')) {
          return;
        }
        this.set('doiStartNumberEditable', false);
        this.get('model').save();
        return this.set('doiEditState', false);
      },
      assignRole: function(roleId, user) {
        var userRole;
        userRole = this.store.createRecord('userRole', {
          user: user,
          role: this.store.getById('role', roleId)
        });
        return userRole.save()["catch"](function(res) {
          userRole.transitionTo('created.uncommitted');
          return userRole.deleteRecord();
        });
      },
      removeRole: function(userRoleId) {
        return this.store.getById('userRole', userRoleId).destroyRecord();
      }
    }
  });

  exports['default'] = JournalIndexController;

});