define('tahi/mixins/file-upload', ['exports', 'ember', 'tahi/models/file-upload'], function (exports, Ember, FileUpload) {

  'use strict';

  var FileUploadMixin;

  FileUploadMixin = Ember['default'].Mixin.create({
    _initFileUpload: (function() {
      return this.set('uploads', []);
    }).on('init'),
    uploads: null,
    isUploading: Ember['default'].computed.notEmpty('uploads'),
    uploadStarted: function(data, fileUploadXHR) {
      var file, filename;
      file = data.files[0];
      filename = file.name;
      this.get('uploads').pushObject(FileUpload['default'].create({
        file: file,
        xhr: fileUploadXHR
      }));
      return $(window).on("beforeunload.cancelUploads." + filename, function() {
        return 'You are uploading, are you sure you want to abort uploading?';
      });
    },
    uploadProgress: function(data) {
      var currentUpload;
      currentUpload = this.get('uploads').findBy('file', data.files[0]);
      if (!currentUpload) {
        return;
      }
      return currentUpload.setProperties({
        dataLoaded: data.loaded,
        dataTotal: data.total
      });
    },
    uploadFinished: function(data, filename) {
      var newUpload, uploads;
      uploads = this.get('uploads');
      newUpload = uploads.findBy('file.name', filename);
      uploads.removeObject(newUpload);
      return $(window).off("beforeunload.cancelUploads." + filename);
    },
    cancelUploads: function() {
      this.get('uploads').invoke('abort');
      this.set('uploads', []);
      return $(window).off('beforeunload.cancelUploads');
    },
    actions: {
      uploadStarted: function(data, fileUploadXHR) {
        return this.uploadStarted(data, fileUploadXHR);
      },
      uploadProgress: function(data) {
        return this.uploadProgress(data);
      },
      uploadFinished: function(data, filename) {
        return this.uploadFinished(data, filename);
      },
      cancelUploads: function() {
        return this.cancelUploads();
      }
    }
  });

  exports['default'] = FileUploadMixin;

});