define('tahi/pods/components/inline-edit-h1/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var InlineEditH1Component;

  InlineEditH1Component = Ember['default'].Component.extend({
    editing: Ember['default'].computed.oneWay('editOnOpen'),
    snapshot: null,
    createSnapshot: (function() {
      return this.set('snapshot', Em.copy(this.get('title')));
    }).observes('editing'),
    hasContent: Ember['default'].computed.notEmpty('title'),
    focusOnEdit: (function() {
      if (this.get('editing')) {
        return Ember['default'].run.schedule('afterRender', this, function() {
          return this.$('input[type=text]').focus().select();
        });
      }
    }).observes('editing').on('didInsertElement'),
    actions: {
      toggleEdit: function() {
        if (this.get('editing')) {
          this.sendAction('setTitle', this.get('snapshot'));
        }
        return this.toggleProperty('editing');
      },
      save: function() {
        if (this.get('hasContent')) {
          this.sendAction('setTitle', this.get('title'));
          return this.toggleProperty('editing');
        }
      }
    }
  });

  exports['default'] = InlineEditH1Component;

});