define('tahi/models/question-attachment', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var QuestionAttachment, a;

  a = DS['default'].attr;

  QuestionAttachment = DS['default'].Model.extend({
    question: DS['default'].belongsTo('question'),
    title: a('string'),
    status: a('string'),
    filename: a('string'),
    src: a('string')
  });

  exports['default'] = QuestionAttachment;

});