define('tahi/pods/components/check-box/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var CheckBoxComponent;

  CheckBoxComponent = Ember['default'].Component.extend({
    classNames: ["ember-checkbox"],
    tagName: "input",
    attributeBindings: "type checked indeterminate disabled tabindex name autofocus form".w(),
    type: "checkbox",
    checked: false,
    disabled: false,
    indeterminate: false,
    init: function() {
      this._super();
      return this.on("change", this, this._updateElementValue);
    },
    didInsertElement: function() {
      this._super();
      return this.get("element").indeterminate = !!this.get("indeterminate");
    },
    _updateElementValue: function() {
      return this.set("checked", this.$().prop("checked"));
    },
    change: function() {
      return this.sendAction('action', this);
    }
  });

  exports['default'] = CheckBoxComponent;

});