define('tahi/tests/handlebars/display-line-breaks-test', ['ember-qunit', '../../helpers/display-line-breaks'], function (ember_qunit, DisplayLineBreaks) {

  'use strict';

  module('helper:display-line-breaks', 'Unit: Display line breaks');

  ember_qunit.test("#displayLineBreaks", function() {
    equal(DisplayLineBreaks['default']._rawFunction("Tahi\nis\ncool").string, "Tahi<br>is<br>cool");
    return equal(DisplayLineBreaks['default']._rawFunction("").string, "");
  });

});