define('tahi/views/author-drop-target', ['exports', 'ember', 'tahi/services/drag-n-drop'], function (exports, Ember, DragNDrop) {

  'use strict';

  exports['default'] = Ember['default'].View.extend(DragNDrop['default'].DroppableMixin, {
    tagName: "div",
    classNameBindings: [":author-drop-target", "isEditable::hidden"],

    isEditable: Ember['default'].computed.alias("controller.isEditable"),

    position: (function () {
      return this.get("index") + 1;
    }).property("index"),

    notAdjacent: function (thisPosition, dragItemPosition) {
      return thisPosition <= dragItemPosition - 1 || thisPosition > dragItemPosition + 1;
    },

    removeDragStyles: function () {
      this.$().removeClass("current-drop-target");
    },

    dragEnter: function (e) {
      if (this.notAdjacent(this.get("position"), DragNDrop['default'].dragItem.get("position"))) {
        this.$().addClass("current-drop-target");
        DragNDrop['default'].cancel(e);
      }
    },

    dragLeave: function (e) {
      this.removeDragStyles();
    },

    dragEnd: function (e) {
      this.removeDragStyles();
    },

    drop: function (e) {
      this.removeDragStyles();
      this.get("controller").shiftAuthorPositions(DragNDrop['default'].dragItem, this.get("position"));
      DragNDrop['default'].dragItem = null;
      return DragNDrop['default'].cancel(e);
    }
  });

});