define('tahi/controllers/overlays/paper_admin', ['exports', 'tahi/pods/task/controller', 'tahi/mixins/controllers/select-2-assignees'], function (exports, TaskController, Select2Assignees) {

  'use strict';

  var PaperAdminOverlayController;

  PaperAdminOverlayController = TaskController['default'].extend(Select2Assignees['default'], {
    select2RemoteUrl: Ember.computed('paper.journal.id', function() {
      return "/filtered_users/admins/" + (this.get('model.paper.journal.id')) + "/";
    }),
    actions: {
      assignAdmin: function(select2User) {
        return this.store.find('user', select2User.id).then((function(_this) {
          return function(user) {
            _this.set('admin', user);
            return _this.send('saveModel');
          };
        })(this));
      }
    }
  });

  exports['default'] = PaperAdminOverlayController;

});