define('tahi/tests/controllers/task-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleFor('controller:task', 'TaskController', {
    needs: ['controller:application'],
    setup: function() {
      var currentUser;
      this.paper = Ember['default'].Object.create({
        editable: true
      });
      this.currentUser = Ember['default'].Object.create({
        siteAdmin: false
      });
      currentUser = this.currentUser;
      this.task = Ember['default'].Object.create({
        isMetadataTask: true,
        paper: this.paper
      });
      return Ember['default'].run((function(_this) {
        return function() {
          _this.subject().set('model', _this.task);
          return _this.subject().set('controllers.application.currentUser', currentUser);
        };
      })(this));
    }
  });

  ember_qunit.test('#isEditable: true when the task is not a metadata task', function() {
    return Ember['default'].run((function(_this) {
      return function() {
        _this.task.set('isMetadataTask', false);
        return equal(_this.subject().get('isEditable'), true);
      };
    })(this));
  });

  ember_qunit.test('#isEditable: always true when the user is an admin', function() {
    return Ember['default'].run((function(_this) {
      return function() {
        _this.currentUser.set('siteAdmin', true);
        _this.task.set('isMetadataTask', true);
        _this.paper.set('editable', false);
        return equal(_this.subject().get('isEditable'), true);
      };
    })(this));
  });

  ember_qunit.test('#isEditable: true when paper is editable and task is a metadata task', function() {
    return Ember['default'].run((function(_this) {
      return function() {
        _this.paper.set('editable', true);
        return equal(_this.subject().get('isEditable'), true);
      };
    })(this));
  });

  ember_qunit.test('#isEditable: false when the paper is not editable and the task is a metadata task', function() {
    return Ember['default'].run((function(_this) {
      return function() {
        _this.paper.set('editable', false);
        _this.task.set('isMetadataTask', true);
        return equal(_this.subject().get('isEditable'), false);
      };
    })(this));
  });

});