define('tahi/views/overlay-background', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var OverlayBackgroundView;

  OverlayBackgroundView = Ember['default'].View.extend({
    willDestroyElement: function() {
      return $(this.get('element')).empty();
    }
  });

  exports['default'] = OverlayBackgroundView;

});