define('tahi/pods/components/radio-button-action/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var RadioButtonActionComponent;

  RadioButtonActionComponent = Ember['default'].Component.extend({
    tagName: 'input',
    type: 'radio',
    attributeBindings: ['name', 'type', 'value', 'checked:checked'],
    checked: (function() {
      return this.get('selection') === this.get('value');
    }).property('selection'),
    change: function() {
      this.set('selection', this.get('value'));
      return this.sendAction();
    }
  });

  exports['default'] = RadioButtonActionComponent;

});