define('tahi/services/drag-n-drop', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var cancelDragEvent = function (e) {
    e.preventDefault();
    e.stopPropagation();
    return false;
  };

  exports['default'] = {
    dragItem: null,

    cancel: function (e) {
      return cancelDragEvent(e);
    },

    DraggableMixin: Ember['default'].Mixin.create({
      attributeBindings: ["draggable"],
      draggable: true,
      dragStart: function () {
        throw new Error("Implement dragStart");
      }
    }),

    DroppableMixin: Ember['default'].Mixin.create({
      _dragEnter: (function (e) {
        return cancelDragEvent(e);
      }).on("dragEnter"),

      _dragOver: (function (e) {
        return cancelDragEvent(e);
      }).on("dragOver"),

      drop: function () {
        throw new Error("Implement drop");
      }
    })
  };

});