define('tahi/controllers/overlays/show-collaborators', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ShowCollaboratorsOverlayController;

  ShowCollaboratorsOverlayController = Ember['default'].ObjectController.extend({
    overlayClass: 'overlay--fullscreen',
    availableCollaborators: Ember['default'].computed.setDiff('allUsers', 'collaborators'),
    formattedCollaborators: (function() {
      return this.get('availableCollaborators').map(function(collab) {
        return {
          id: collab.get('id'),
          text: collab.get('fullName')
        };
      });
    }).property('availableCollaborators.@each'),
    addedCollaborations: Ember['default'].computed.setDiff('collaborations', 'initialCollaborations'),
    removedCollaborations: Ember['default'].computed.setDiff('initialCollaborations', 'collaborations'),
    allUsers: null,
    selectedCollaborator: null,
    paper: null,
    initialCollaborations: null,
    collaborations: null,
    collaborators: (function() {
      return this.get('collaborations').filter(function(collaboration) {
        return !collaboration.get('isDeleted');
      }).mapBy('user');
    }).property('collaborations.@each'),
    actions: {
      addNewCollaborator: function(formattedOption) {
        var existingRecord, newCollaboration, newCollaborator, paper;
        newCollaborator = this.get('availableCollaborators').findBy('id', formattedOption.id);
        paper = this.get('paper');
        existingRecord = this.store.all('collaboration').find(function(c) {
          return c.get('oldPaper') === paper && c.get('user') === newCollaborator;
        });
        newCollaboration = existingRecord || this.store.createRecord('collaboration', {
          paper: paper,
          user: newCollaborator
        });
        return this.get('collaborations').addObject(newCollaboration);
      },
      removeCollaborator: function(collaborator) {
        var collaboration;
        collaboration = this.get('collaborations').findBy('user', collaborator);
        collaboration.set('oldPaper', collaboration.get('paper'));
        this.get('collaborations').removeObject(collaboration);
        return this.set('selectedCollaborator', null);
      },
      cancel: function() {
        var collaborations;
        collaborations = this.get('collaborations');
        this.get('removedCollaborations').forEach(function(c) {
          return collaborations.addObject(c);
        });
        this.get('addedCollaborations').forEach(function(c) {
          return collaborations.removeObject(c);
        });
        return this.send('closeOverlay');
      },
      save: function() {
        var addPromises, deletePromises;
        addPromises = this.get('addedCollaborations').map((function(_this) {
          return function(collaboration) {
            return collaboration.save();
          };
        })(this));
        deletePromises = this.get('removedCollaborations').map(function(collaboration) {
          return collaboration.destroyRecord();
        });
        return Ember['default'].RSVP.all(_.union(addPromises, deletePromises)).then((function(_this) {
          return function() {
            return _this.send('closeOverlay');
          };
        })(this));
      }
    }
  });

  exports['default'] = ShowCollaboratorsOverlayController;

});