define('tahi/pods/components/comment-board/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var CommentBoardComponent;

  CommentBoardComponent = Ember['default'].Component.extend({
    classNames: ['comment-board'],
    editing: false,
    comments: [],
    commentBody: '',
    commentsToShow: 5,
    commentSort: ['createdAt:desc'],
    sortedComments: Ember['default'].computed.sort('comments', 'commentSort'),
    firstComments: Ember['default'].computed.filter('sortedComments', function(comment, index) {
      return index < 5;
    }),
    setupFocus: (function() {
      return this.$('.new-comment-field').on('focus', (function(_this) {
        return function(e) {
          return _this.set('editing', true);
        };
      })(this));
    }).on('didInsertElement'),
    showingAllComments: (function() {
      return this.get('comments.length') <= this.get('commentsToShow');
    }).property('comments.length'),
    omittedCommentsCount: (function() {
      return this.get('comments.length') - this.get('commentsToShow');
    }).property('comments.length'),
    actions: {
      showAllComments: function() {
        return this.set('showingAllComments', true);
      },
      postComment: function() {
        this.sendAction('postComment', this.get('commentBody'));
        return this.send('clearComment');
      },
      clearComment: function() {
        this.set('commentBody', '');
        return this.set('editing', false);
      }
    }
  });

  exports['default'] = CommentBoardComponent;

});