define('tahi/tests/integration/super-ad-hoc-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/mock-server', '../helpers/setups', '../helpers/factory'], function (Ember, ember_qunit, startApp, setupMockServer, setups, Factory) {

  'use strict';

  var app, currentPaper, fakeUser, server;

  app = null;

  server = null;

  fakeUser = null;

  currentPaper = null;

  module('Integration: Super AdHoc Card', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      var collaborators, paperPayload, paperResponse, records;
      app = startApp['default']();
      server = setupMockServer['default']();
      fakeUser = window.currentUser.user;
      records = setups.paperWithTask('Task', {
        id: 1,
        title: "Super Ad-Hoc"
      });
      currentPaper = records[0];
      paperPayload = Factory['default'].createPayload('paper');
      paperPayload.addRecords(records.concat([fakeUser]));
      paperResponse = paperPayload.toJSON();
      collaborators = [
        {
          id: "35",
          full_name: "Aaron Baker",
          info: "testroles2, collaborator"
        }
      ];
      server.respondWith('GET', "/dashboards", [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify({
          dashboards: []
        })
      ]);
      server.respondWith('GET', "/papers/" + currentPaper.id, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(paperResponse)
      ]);
      server.respondWith('PUT', /\/tasks\/\d+/, [
        204, {
          "Content-Type": "application/json"
        }, JSON.stringify({})
      ]);
      server.respondWith('GET', "/filtered_users/collaborators/" + currentPaper.id, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(collaborators)
      ]);
      return server.respondWith('GET', /\/filtered_users\/non_participants\/\d+\/\w+/, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify([])
      ]);
    }
  });

  ember_qunit.test("Changing the title on an AdHoc Task", function() {
    visit("/papers/" + currentPaper.id + "/tasks/1");
    click('h1.inline-edit .glyphicon-pencil');
    fillIn('.large-edit input[name=title]', 'Shazam!');
    click('.large-edit .button--green:contains("Save")');
    return andThen(function() {
      return equal(find('h1.inline-edit:contains("Shazam!")').length, 1);
    });
  });

  ember_qunit.test("Adding a text block to an AdHoc Task", function() {
    visit("/papers/" + currentPaper.id + "/tasks/1");
    click('.adhoc-content-toolbar .glyphicon-plus');
    click('.adhoc-content-toolbar .adhoc-toolbar-item--text');
    andThen(function() {
      Em.$('.inline-edit-form div[contenteditable]').html("New contenteditable, yahoo!").trigger('keyup');
      return click('.task-body .inline-edit-body-part .button--green:contains("Save")');
    });
    andThen(function() {
      assertText('.inline-edit', 'yahoo');
      return click('.inline-edit-body-part .glyphicon-trash');
    });
    andThen(function() {
      assertText('.inline-edit-body-part', 'Are you sure?');
      return click('.inline-edit-body-part .delete-button');
    });
    return andThen(function() {
      return assertNoText('.inline-edit', 'yahoo');
    });
  });

  ember_qunit.test("Adding and removing a checkbox item to an AdHoc Task", function() {
    visit("/papers/" + currentPaper.id + "/tasks/1");
    click('.adhoc-content-toolbar .glyphicon-plus');
    click('.adhoc-content-toolbar .adhoc-toolbar-item--list');
    andThen(function() {
      equal(find('.inline-edit-form .item-remove').length, 1);
      Em.$('.inline-edit-form label[contenteditable]').html("Here is a checkbox list item").trigger('keyup');
      return click('.task-body .inline-edit-body-part .button--green:contains("Save")');
    });
    andThen(function() {
      assertText('.inline-edit', 'checkbox list item');
      equal(find('.inline-edit input[type=checkbox]').length, 1);
      return click('.inline-edit-body-part .glyphicon-trash');
    });
    andThen(function() {
      assertText('.inline-edit-body-part', 'Are you sure?');
      return click('.inline-edit-body-part .delete-button');
    });
    return andThen(function() {
      return assertNoText('.inline-edit', 'checkbox list item');
    });
  });

  ember_qunit.test("Adding an email block to an AdHoc Task", function() {
    visit("/papers/" + currentPaper.id + "/tasks/1");
    click('.adhoc-content-toolbar .glyphicon-plus');
    click('.adhoc-content-toolbar .adhoc-toolbar-item--email');
    fillIn('.inline-edit-form input[placeholder="Enter a subject"]', "Deep subject");
    andThen(function() {
      Em.$('.inline-edit-form div[contenteditable]').html("Awesome email body!").trigger('keyup');
      return click('.task-body .inline-edit-body-part .button--green:contains("Save")');
    });
    return andThen(function() {
      assertText('.inline-edit .item-subject', 'Deep');
      return assertText('.inline-edit .item-text', 'Awesome');
    });
  });

  ember_qunit.test("User can send an email from an adhoc card", function() {
    server.respondWith('PUT', /\/tasks\/\d+\/send_message/, [
      204, {
        "Content-Type": "application/json"
      }, JSON.stringify({})
    ]);
    visit("/papers/" + currentPaper.id + "/tasks/1");
    click('.adhoc-content-toolbar .glyphicon-plus');
    click('.adhoc-content-toolbar .adhoc-toolbar-item--email');
    fillIn('.inline-edit-form input[placeholder="Enter a subject"]', "Deep subject");
    andThen(function() {
      Em.$('.inline-edit-form div[contenteditable]').html("Awesome email body!").trigger('keyup');
      return click('.task-body .inline-edit-body-part .button--green:contains("Save")');
    });
    click('.task-body .email-send-participants');
    click('.send-email-action');
    return andThen(function() {
      ok(find('.bodypart-last-sent').length, 'The sent at time should appear');
      ok(find('.bodypart-email-sent-overlay').length, 'The sent confirmation should appear');
      return ok(_.findWhere(server.requests, {
        method: "PUT",
        url: "/tasks/1/send_message"
      }), "It posts to the server");
    });
  });

});