define('tahi/pods/components/attachment-thumbnail/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var AttachmentThumbnailComponent;

  AttachmentThumbnailComponent = Ember['default'].Component.extend({
    classNameBindings: ['destroyState:_destroy', 'editState:_edit'],
    destroyState: false,
    previewState: false,
    editState: false,
    attachmentType: 'attachment',
    attachmentUrl: (function() {
      return "/figures/" + (this.get('attachment.id')) + "/update_attachment";
    }).property('attachment.id'),
    focusOnFirstInput: (function() {
      if (this.get('editState')) {
        return Em.run.schedule('afterRender', this, (function() {
          return this.$('input[type=text]:first').focus();
        }));
      }
    }).observes('editState'),
    scrollToView: function() {
      return $('.overlay').animate({
        scrollTop: this.$().offset().top + $('.overlay').scrollTop()
      }, 500);
    },
    isProcessing: Ember['default'].computed.equal('attachment.status', 'processing'),
    showSpinner: Ember['default'].computed.or('isProcessing', 'isUploading'),
    actions: {
      cancelEditing: function() {
        this.set('editState', false);
        return this.get('attachment').rollback();
      },
      toggleEditState: function(focusSelector) {
        return this.toggleProperty('editState');
      },
      saveAttachment: function() {
        this.get('attachment').save();
        return this.set('editState', false);
      },
      cancelDestroyAttachment: function() {
        return this.set('destroyState', false);
      },
      confirmDestroyAttachment: function() {
        return this.set('destroyState', true);
      },
      destroyAttachment: function() {
        return this.$().fadeOut(250, (function(_this) {
          return function() {
            return _this.sendAction('destroyAttachment', _this.get('attachment'));
          };
        })(this));
      },
      uploadStarted: function(data, fileUploadXHR) {
        return this.sendAction('uploadStarted', data, fileUploadXHR);
      },
      uploadProgress: function(data) {
        return this.sendAction('uploadProgress', data);
      },
      uploadFinished: function(data, filename) {
        return this.sendAction('uploadFinished', data, filename);
      },
      togglePreview: function() {
        this.toggleProperty('previewState');
        if (this.get('previewState')) {
          return this.scrollToView();
        }
      }
    }
  });

  exports['default'] = AttachmentThumbnailComponent;

});