define('tahi/mixins/controllers/saves-delayed', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var SavesDelayed;

  SavesDelayed = Ember['default'].Mixin.create({
    needs: ['application'],
    delayedSave: Ember['default'].computed.alias('controllers.application.delayedSave'),
    saveDelayed: function() {
      this.set('delayedSave', true);
      return Ember['default'].run.debounce(this, this.saveModel, 500);
    },
    saveModel: function() {
      if (!this.get('saveInFlight')) {
        this.set('saveInFlight', true);
        return this.get('model').save()["finally"]((function(_this) {
          return function() {
            _this.set('saveInFlight', false);
            return _this.set('delayedSave', false);
          };
        })(this));
      }
    },
    actions: {
      saveModel: function() {
        return this.saveDelayed();
      }
    }
  });

  exports['default'] = SavesDelayed;

});