define('tahi/tests/models/paper-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleForModel('paper', 'Unit: Paper Model', {
    needs: ['model:author', 'model:user', 'model:figure', 'model:journal', 'model:question-attachment', 'model:comment-look', 'model:attachable', 'model:affiliation', 'model:phase', 'model:task', 'model:comment', 'model:participation', 'model:lite-paper', 'model:card-thumbnail', 'model:question', 'model:collaboration', 'model:attachment', 'model:supporting-information-file']
  });

  ember_qunit.test('displayTitle displays short title if title is missing', function() {
    var paper, shortTitle;
    shortTitle = 'test short title';
    paper = Ember['default'].run((function(_this) {
      return function() {
        return paper = _this.store().createRecord('paper', {
          title: '',
          shortTitle: shortTitle
        });
      };
    })(this));
    return equal(paper.get('displayTitle'), shortTitle);
  });

  ember_qunit.test('displayTitle displays title if present', function() {
    var paper, title;
    title = 'Test Title';
    paper = Ember['default'].run((function(_this) {
      return function() {
        return _this.store().createRecord('paper', {
          title: title,
          shortTitle: 'test short title'
        });
      };
    })(this));
    return equal(paper.get('displayTitle'), title);
  });

  ember_qunit.test('Paper hasMany tasks (async)', function() {
    var paperPromise;
    stop();
    paperPromise = Ember['default'].run((function(_this) {
      return function() {
        var paper, task1, task2;
        task1 = _this.store().createRecord('task', {
          type: 'Task',
          title: 'A message',
          isMetadataTask: false
        });
        task2 = _this.store().createRecord('task', {
          type: 'TechCheckTask',
          title: 'some task',
          isMetadataTask: true
        });
        paper = _this.store().createRecord('paper', {
          title: 'some really long title',
          shortTitle: 'test short title'
        });
        return paper.get('tasks').then(function(tasks) {
          tasks.pushObjects([task1, task2]);
          return paper;
        });
      };
    })(this));
    return paperPromise.then(function(paper) {
      return deepEqual(paper.get('tasks').mapBy('type'), ['Task', 'TechCheckTask']);
    }).then(start, start);
  });

  ember_qunit.test('allMetadata tasks filters tasks by isMetaData', function() {
    var paperPromise;
    stop();
    paperPromise = Ember['default'].run((function(_this) {
      return function() {
        var paper, task1, task2;
        task1 = _this.store().createRecord('task', {
          type: 'Task',
          title: 'A message',
          isMetadataTask: false
        });
        task2 = _this.store().createRecord('task', {
          type: 'TechCheckTask',
          title: 'some task',
          isMetadataTask: true
        });
        paper = _this.store().createRecord('paper', {
          title: 'some really long title',
          shortTitle: 'test short title'
        });
        return paper.get('tasks').then(function(tasks) {
          tasks.pushObjects([task1, task2]);
          return paper;
        });
      };
    })(this));
    return paperPromise.then(function(paper) {
      return deepEqual(paper.get('allMetadataTasks').mapBy('type'), ['TechCheckTask']);
    }).then(start, start);
  });

});