define('tahi/tests/controllers/paper-manage-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleFor('controller:paper/manage', 'PaperManageController', {
    needs: ['controller:application'],
    setup: function() {
      this.phase1 = Ember['default'].Object.create({
        position: 1
      });
      this.phase2 = Ember['default'].Object.create({
        position: 2
      });
      this.phase3 = Ember['default'].Object.create({
        position: 3
      });
      this.phase4 = Ember['default'].Object.create({
        position: 4
      });
      return this.paper = Ember['default'].Object.create({
        title: 'test paper',
        phases: []
      });
    }
  });

  ember_qunit.test('#sortedPhases: phases are sorted by position', function() {
    var paperManageController, sortedPositionArray;
    paperManageController = this.subject();
    paperManageController.set('model', this.paper);
    paperManageController.set('model.phases', [this.phase3, this.phase2, this.phase4]);
    sortedPositionArray = paperManageController.get('sortedPhases').mapBy('position').toArray();
    deepEqual(sortedPositionArray, [2, 3, 4]);
    paperManageController.get('model.phases').pushObject(this.phase1);
    sortedPositionArray = paperManageController.get('sortedPhases').mapBy('position').toArray();
    return deepEqual(sortedPositionArray, [1, 2, 3, 4]);
  });

  ember_qunit.test('#updatePositions: phase positions are updated accordingly', function() {
    var paperManageController;
    equal(this.phase3.get('position'), 3);
    equal(this.phase4.get('position'), 4);
    paperManageController = this.subject();
    paperManageController.set('model', this.paper);
    paperManageController.set('model.phases', [this.phase1, this.phase2, this.phase3, this.phase4]);
    this.phase1.setProperties({
      position: 3
    });
    paperManageController.updatePositions(this.phase1);
    equal(this.phase3.get('position'), 4);
    return equal(this.phase4.get('position'), 5);
  });

});