define('tahi/views/chosen', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ChosenView;

  ChosenView = Ember['default'].Select.extend({
    multiple: false,
    width: '200px',
    disableSearchThreshold: 0,
    disabled: false,
    searchContains: true,
    attributeBindings: ['multiple', 'width', 'disableSearchThreshold', 'searchContains', 'data-placeholder'],
    changeAction: null,
    change: function() {
      var action;
      action = this.get('changeAction');
      if (action) {
        return this.get('controller').send(action, this.get('value'));
      }
    },
    disabledDidChange: (function() {
      return this.$().attr('disabled', this.get('disabled')).trigger('chosen:updated');
    }).observes('disabled'),
    setup: (function() {
      var options;
      options = {
        multiple: this.get('multiple'),
        width: this.get('width'),
        disable_search_threshold: this.get('disableSearchThreshold'),
        search_contains: this.get('searchContains'),
        no_results_text: this.get('noResultsText'),
        max_selected_options: this.get('maxSelectedOptions'),
        allow_single_deselect: this.get('allowSingleDeselect'),
        inherit_select_classes: true
      };
      options.clean_search_text = this.cleanSearchText;
      options.calling_context = this;
      this.$().chosen(options);
      this.addObserver(this.get("optionLabelPath").replace(/^content/, "content.@each"), (function(_this) {
        return function() {
          return _this.rerenderChosen();
        };
      })(this));
      return this.addObserver("value", (function(_this) {
        return function() {
          return _this.rerenderChosen();
        };
      })(this));
    }).on('didInsertElement'),
    teardown: (function() {
      return this.$().chosen('destroy');
    }).on('willDestroyElement'),
    cleanSearchText: function(option, context) {
      return option.text;
    },
    warnValue: (function() {
      if (this.get('multiple') && !this.get('selection')) {
        throw new Error("You should use the selection option with a multiple select rather than value");
      }
    }).on('init'),
    observeSelection: (function() {
      return this.rerenderChosen();
    }).observes('selection.@each'),
    rerenderChosen: function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, function() {
        if (this.$()) {
          return this.$().trigger('chosen:updated');
        }
      });
    }
  });

  exports['default'] = ChosenView;

});