define('tahi/helpers/display-line-breaks', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DisplayLineBreaks;

  DisplayLineBreaks = Ember['default'].Handlebars.makeBoundHelper(function(text) {
    return new Em.Handlebars.SafeString(text.replace(/\n/g, "<br>"));
  });

  exports['default'] = DisplayLineBreaks;

});