define('tahi/controllers/overlays/ethics', ['exports', 'tahi/pods/task/controller', 'tahi/mixins/saves-questions-on-close'], function (exports, TaskController, SavesQuestionsOnClose) {

  'use strict';

  var EthicsOverlayController;

  EthicsOverlayController = TaskController['default'].extend(SavesQuestionsOnClose['default'], {
    actions: {
      destroyAttachment: function(attachment) {
        return attachment.destroyRecord();
      }
    }
  });

  exports['default'] = EthicsOverlayController;

});