define('tahi/pods/task/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var TaskRoute;

  TaskRoute = Ember['default'].Route.extend({
    model: function(params) {
      return this.store.find('paper', params.paper_id).then((function(_this) {
        return function() {
          return _this.store.findTask(params.task_id) || _this.store.find('task', params.task_id);
        };
      })(this));
    },
    setupController: function(controller, model) {
      var baseObjectName, currentType, taskComments, taskController, taskParticipations;
      currentType = model.get('type');
      if (currentType === 'Task') {
        currentType = 'AdHocTask';
      }
      baseObjectName = (currentType || 'AdHocTask').replace('Task', '');
      this.set('baseObjectName', baseObjectName);
      taskController = this.controllerFor("overlays/" + baseObjectName);
      taskController.set('model', model);
      this.set('taskController', taskController);
      taskComments = this.store.filter('comment', function(part) {
        return part.get('task') === model;
      });
      taskController.set('comments', taskComments);
      taskParticipations = this.store.filter('participation', function(part) {
        return part.get('task') === model;
      });
      taskController.set('participations', taskParticipations);
      if (!Ember['default'].isEmpty(this.controllerFor('application').get('overlayRedirect'))) {
        taskController.set('onClose', 'redirect');
      } else {
        taskController.set('onClose', 'redirectToDashboard');
      }
      return taskController.trigger('didSetupController');
    },
    resetController: function(controller, isExiting, _transition) {
      if (isExiting) {
        return controller.set('isNewTask', false);
      }
    },
    renderTemplate: function() {
      this.render("overlays/" + (this.get('baseObjectName')), {
        into: 'application',
        outlet: 'overlay',
        controller: this.get('taskController')
      });
      return this.render(this.controllerFor('application').get('overlayBackground'));
    },
    deactivate: function() {
      this.send('closeOverlay');
      return this.controllerFor('application').setProperties({
        overlayRedirect: [],
        overlayBackground: null
      });
    },
    actions: {
      willTransition: function(transition) {
        return this.get('taskController').send('routeWillTransition', transition);
      }
    }
  });

  exports['default'] = TaskRoute;

});