define('tahi/models/paper-reviewer-task', ['exports', 'ember-data', 'tahi/models/task'], function (exports, DS, Task) {

  'use strict';

  var PaperReviewerTask;

  PaperReviewerTask = Task['default'].extend({
    reviewers: DS['default'].hasMany('user'),
    relationshipsToSerialize: ['reviewers', 'participants']
  });

  exports['default'] = PaperReviewerTask;

});