define('tahi/models/question', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Question, a;

  a = DS['default'].attr;

  Question = DS['default'].Model.extend({
    questionAttachment: DS['default'].belongsTo('question-attachment'),
    task: DS['default'].belongsTo('task', {
      polymorphic: true,
      inverse: 'questions'
    }),
    ident: a('string'),
    question: a('string'),
    answer: a('string'),
    additionalData: a(),
    url: a('string')
  });

  exports['default'] = Question;

});