define('tahi/helpers/display-value', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DisplayValue;

  DisplayValue = Ember['default'].Handlebars.makeBoundHelper(function(value, options) {
    if (Em.isEmpty(value)) {
      return options.hash.or;
    } else {
      return value.htmlSafe();
    }
  });

  exports['default'] = DisplayValue;

});