define('tahi/helpers/errors-on', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ErrorsOn;

  ErrorsOn = Ember['default'].Handlebars.makeBoundHelper(function(model, field) {
    var errors, msg;
    errors = model.get("errors." + field);
    if (Ember['default'].isEmpty(errors)) {
      return new Handlebars.SafeString("");
    } else {
      msg = "" + (field.capitalize()) + " " + (errors.mapProperty('message').join("; "));
      return new Handlebars.SafeString("<span class='error-message'>" + msg + "</span>");
    }
  }, 'errors.length');

  exports['default'] = ErrorsOn;

});