define('tahi/controllers/overlays/figure', ['exports', 'tahi/pods/task/controller', 'tahi/mixins/file-upload'], function (exports, TaskController, FileUploadMixin) {

  'use strict';

  var FigureOverlayController;

  FigureOverlayController = TaskController['default'].extend(FileUploadMixin['default'], {
    figureUploadUrl: (function() {
      return "/papers/" + (this.get('litePaper.id')) + "/figures";
    }).property('litePaper.id'),
    figures: (function() {
      var figures;
      figures = this.get('paper.figures') || [];
      figures = figures.filter(function(figure) {
        return !figure.get('isDeleted');
      });
      return figures.sortBy('createdAt').reverse();
    }).property('paper.figures.[]', 'paper.figures.@each.createdAt'),
    actions: {
      uploadFinished: function(data, filename) {
        var figure;
        this.uploadFinished(data, filename);
        this.store.pushPayload('figure', data);
        figure = this.store.getById('figure', data.figure.id);
        return this.get('paper.figures').pushObject(figure);
      },
      changeStrikingImage: function(newValue) {
        return this.get('paper').set('strikingImageId', newValue);
      },
      updateStrikingImage: function() {
        return this.get('paper').save();
      },
      destroyAttachment: function(attachment) {
        this.get('paper.figures').removeObject(attachment);
        return attachment.destroyRecord();
      }
    }
  });

  exports['default'] = FigureOverlayController;

});