define('tahi/models/manuscript-manager-template', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var ManuscriptManagerTemplate, a;

  a = DS['default'].attr;

  ManuscriptManagerTemplate = DS['default'].Model.extend({
    journal: DS['default'].belongsTo('adminJournal'),
    phaseTemplates: DS['default'].hasMany('phaseTemplate'),
    paperType: a('string')
  });

  exports['default'] = ManuscriptManagerTemplate;

});