define('tahi/pods/admin/admin-journal-user/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var AdminJournalUserController;

  AdminJournalUserController = Ember['default'].ObjectController.extend({
    needs: ['admin/journal/index'],
    journalRoles: Ember['default'].computed.alias('controllers.admin/journal/index.model.roles'),
    userJournalRoles: Ember['default'].computed.mapBy('model.userRoles', 'role'),
    selectableJournalRoles: (function() {
      return this.get('journalRoles').map(function(jr) {
        return {
          id: jr.get('id'),
          text: jr.get('name')
        };
      });
    }).property('journalRoles'),
    selectableUserJournalRoles: (function() {
      return this.get('userJournalRoles').map(function(jr) {
        return {
          id: jr.get('id'),
          text: jr.get('name')
        };
      });
    }).property('userJournalRoles'),
    actions: {
      removeRole: function(roleObj) {
        return this.get('model.userRoles').findBy('role.id', roleObj.id).destroyRecord();
      },
      assignRole: function(roleObj) {
        var userRole;
        userRole = this.store.createRecord('userRole', {
          user: this.get('model'),
          role: this.store.getById('role', roleObj.id)
        });
        return userRole.save();
      }
    }
  });

  exports['default'] = AdminJournalUserController;

});