define('tahi/pods/paper/manage/view', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var PaperManageView;

  PaperManageView = Ember['default'].View.extend({
    setupColumnHeights: (function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, Utils['default'].resizeColumnHeaders);
    }).on('didInsertElement').observes('controller.phases.@each')
  });

  exports['default'] = PaperManageView;

});