define('tahi/pods/components/date-picker/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DatePickerComponent;

  DatePickerComponent = Ember['default'].TextField.extend({
    tagName: 'input',
    classNames: ['datepicker', 'form-control', 'datepicker-field'],
    elementInserted: false,
    date: null,
    didInsertElement: function() {
      var $picker;
      Ember['default'].tryInvoke(this.get('parentView'), 'registerPicker', [this]);
      this.set('value', this.get('date'));
      $picker = this.$().datepicker({
        autoclose: true
      });
      $picker.on('changeDate', (function(_this) {
        return function(event) {
          _this.set('date', event.format());
          return Ember['default'].tryInvoke(_this.get('parentView'), 'dateChanged', [_this]);
        };
      })(this));
      $picker.on('clearDate', (function(_this) {
        return function(event) {
          _this.set('date', null);
          return Ember['default'].tryInvoke(_this.get('parentView'), 'dateChanged', [_this]);
        };
      })(this));
      this.set('$picker', $picker);
      return this.set('elementInserted', true);
    },
    setStartDate: function(dateString) {
      return this.get('$picker').datepicker('setStartDate', dateString);
    },
    setEndDate: function(dateString) {
      return this.get('$picker').datepicker('setEndDate', dateString);
    }
  });

  exports['default'] = DatePickerComponent;

});