define('tahi/initializers/current-user', ['exports'], function (exports) {

  'use strict';

  var CurrentUser;

  CurrentUser = {
    name: 'currentUser',
    after: 'store',
    initialize: function(container, application) {
      var store, user, userInfo;
      if (userInfo = window.currentUser) {
        store = container.lookup('store:main');
        store.pushPayload(userInfo);
        user = store.getById('user', userInfo.user.id);
        container.register('user:current', user, {
          instantiate: false
        });
        application.inject('controller', 'currentUser', 'user:current');
        return application.inject('route', 'currentUser', 'user:current');
      }
    }
  };

  exports['default'] = CurrentUser;

});