define('tahi/pods/admin/role/view', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var RoleView;

  RoleView = Ember['default'].View.extend({
    classNameBindings: [':admin-role', 'isEditing:is-editing:not-editing'],
    isNew: Ember['default'].computed.alias('controller.content.isNew'),
    isEditing: Ember['default'].computed.alias('controller.isEditing'),
    _animateInIfNewRole: (function() {
      if (this.get('isNew')) {
        return this.$().hide().fadeIn(250);
      }
    }).on('didInsertElement'),
    focusObserver: (function() {
      if (this.get('controller.isEditing')) {
        return Ember['default'].run.schedule('afterRender', (function(_this) {
          return function() {
            return _this.$('input:first').focus();
          };
        })(this));
      }
    }).observes('controller.isEditing'),
    click: function(e) {
      if (!this.get('isEditing')) {
        this.set('isEditing', true);
        return e.stopPropagation();
      }
    },
    actions: {
      "delete": function() {
        return this.$().fadeOut(250, (function(_this) {
          return function() {
            return _this.get('controller').send('delete');
          };
        })(this));
      },
      cancel: function() {
        var sendCancel;
        sendCancel = (function(_this) {
          return function() {
            return _this.get('controller').send('cancel');
          };
        })(this);
        if (this.get('isNew')) {
          return this.$().fadeOut(250, function() {
            return sendCancel();
          });
        } else {
          return sendCancel();
        }
      }
    }
  });

  exports['default'] = RoleView;

});