define('tahi/controllers/overlays/admin-journal', ['exports', 'tahi/pods/task/controller'], function (exports, TaskController) {

  'use strict';

  var AdminJournalOverlayController;

  AdminJournalOverlayController = TaskController['default'].extend({
    needs: ['admin/journal/index'],
    overlayClass: 'overlay--fullscreen journal-edit-overlay',
    propertyName: '',
    actions: {
      save: function() {
        var controller;
        controller = this.get('controllers.admin/journal/index');
        controller.set("" + (this.get('propertyName')) + "SaveStatus", 'Saved');
        controller.get('model').save();
        return this.send('closeAction');
      }
    }
  });

  exports['default'] = AdminJournalOverlayController;

});