define('tahi/pods/admin/journal/flow-manager/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var Route;

  Route = Ember['default'].Route.extend({
    model: function(params) {
      return this.store.find('role', params.role_id);
    },
    afterModel: function(model) {
      return model.get('flows');
    },
    renderTemplate: function() {
      this._super();
      return this.render('flow-manager-buttons', {
        outlet: 'controlBarButtons',
        template: 'journal'
      });
    },
    setupController: function(controller, model) {
      return controller.setProperties({
        model: model,
        commentLooks: this.store.all('commentLook'),
        journal: this.modelFor('admin.journal'),
        journalTaskTypes: this.store.all('journalTaskType')
      });
    },
    actions: {
      viewCard: function(task) {
        var paperId, redirectParams;
        paperId = task.get('litePaper.id');
        redirectParams = ['journal.flow_manager', this.modelFor('journal'), this.modelFor('journal.flow_manager')];
        this.controllerFor('application').get('overlayRedirect').pushObject(redirectParams);
        this.controllerFor('application').set('overlayBackground', 'journal.flow_manager');
        return this.transitionTo('task', paperId, task.get('id'));
      }
    }
  });

  exports['default'] = Route;

});