define('tahi/pods/dashboard/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DashboardRoute;

  DashboardRoute = Ember['default'].Route.extend({
    model: function() {
      return this.store.find('dashboard').then(function(dashboardArray) {
        return dashboardArray.get('firstObject');
      });
    },
    setupController: function(controller, model) {
      var papers;
      this.store.find('commentLook');
      controller.set('model', model);
      papers = this.store.filter('litePaper', function(p) {
        return !Ember['default'].isEmpty(p.get('roles'));
      });
      controller.set('papers', papers);
      return controller.set('unreadComments', this.store.all('commentLook'));
    },
    actions: {
      didTransition: function() {
        this.controllerFor('dashboard').set('pageNumber', 1);
        return true;
      },
      viewCard: function(task) {
        var redirectParams;
        redirectParams = ['index'];
        this.controllerFor('application').get('overlayRedirect').pushObject(redirectParams);
        this.controllerFor('application').set('cachedModel', this.modelFor('index'));
        this.controllerFor('application').set('overlayBackground', 'index');
        return this.transitionTo('task', task.get('litePaper.id'), task.get('id'));
      },
      showNewPaperOverlay: function() {
        return this.store.find('journal').then((function(_this) {
          return function(journals) {
            var model;
            model = _this.store.createRecord('paper', {
              journal: journals.get('content.firstObject'),
              paperType: journals.get('content.firstObject.paperTypes.firstObject'),
              editable: true,
              body: ''
            });
            _this.controllerFor('overlays/paperNew').setProperties({
              model: model,
              journals: journals
            });
            return _this.render('overlays/paperNew', {
              into: 'application',
              outlet: 'overlay',
              controller: 'overlays/paperNew'
            });
          };
        })(this));
      },
      closeAction: function() {
        this.controllerFor('overlays/paperNew').get('model').set('journal', null).deleteRecord();
        return this.send('closeOverlay');
      }
    }
  });

  exports['default'] = DashboardRoute;

});