define('tahi/controllers/overlays/choose-new-flow-manager-column', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ChooseNewFlowManagerColumnOverlayController;

  ChooseNewFlowManagerColumnOverlayController = Ember['default'].Controller.extend({
    overlayClass: 'flow-manager-column-overlay overlay--fullscreen',
    isLoading: false,
    flows: [],
    groupedFlows: (function() {
      var result;
      result = [];
      this.get('flows').forEach(function(flow) {
        var flowJournalName;
        flowJournalName = flow.journal_name || 'Default Flows';
        if (!result.findBy('title', flowJournalName)) {
          result.pushObject(Ember['default'].Object.create({
            title: flowJournalName,
            logo: flow.journal_logo,
            flows: []
          }));
        }
        return result.findBy('title', flowJournalName).get('flows').pushObject(flow);
      });
      return result;
    }).property('flows.[]'),
    actions: {
      createFlow: function(flow) {
        flow = this.store.createRecord('userFlow', {
          title: flow.title,
          flowId: flow.id,
          journalName: flow.journal_name,
          journalLogo: flow.journal_logo
        });
        flow.save();
        return this.send('closeOverlay');
      }
    }
  });

  exports['default'] = ChooseNewFlowManagerColumnOverlayController;

});