define('tahi/pods/components/datepicker-group/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DatePickerGroupComponent;

  DatePickerGroupComponent = Ember['default'].Component.extend({
    startPicker: null,
    endPicker: null,
    registerPicker: function(datePicker) {
      return this.set(datePicker.get('role'), datePicker);
    },
    dateChanged: function(datePicker) {
      return this.enforceConsistency();
    },
    enforceConsistency: function() {
      return Ember['default'].run((function(_this) {
        return function() {
          if (_this.get('startPicker.elementInserted') && _this.get('endPicker.elementInserted')) {
            _this.get('endPicker').setStartDate(_this.get('startPicker.date'));
            return _this.get('startPicker').setEndDate(_this.get('endPicker.date'));
          }
        };
      })(this));
    }
  });

  exports['default'] = DatePickerGroupComponent;

});