define('tahi/pods/paper/edit/controller', ['exports', 'ember', 'tahi/controllers/base-paper', 'tahi/services/visual-editor'], function (exports, Ember, BasePaperController, VisualEditor) {

  'use strict';

  var PaperEditController;

  PaperEditController = BasePaperController['default'].extend({
    needs: ['overlays/paperSubmit'],
    visualEditor: null,
    saveState: false,
    setupVisualEditor: (function() {
      return this.set('visualEditor', VisualEditor['default'].create());
    }).on("init"),
    errorText: "",
    isBodyEmpty: Ember['default'].computed('model.body', function() {
      return Ember['default'].isBlank($(this.get('model.body')).text());
    }),
    showPlaceholder: Ember['default'].computed('isBodyEmpty', 'visualEditor.isCurrentlyEditing', function() {
      return this.get('isBodyEmpty') && !this.get('visualEditor.isCurrentlyEditing');
    }),
    statusMessage: Ember['default'].computed.any('processingMessage', 'userEditingMessage', 'saveStateMessage'),
    processingMessage: (function() {
      if (this.get('status') === "processing") {
        return "Processing Manuscript";
      } else {
        return null;
      }
    }).property('status'),
    userEditingMessage: (function() {
      var lockedBy;
      lockedBy = this.get('lockedBy');
      if (lockedBy && lockedBy !== this.currentUser) {
        return "<span class='edit-paper-locked-by'>" + (lockedBy.get('fullName')) + "</span> <span>is editing</span>";
      } else {
        return null;
      }
    }).property('lockedBy'),
    locked: (function() {
      return !Ember['default'].isBlank(this.get('processingMessage') || this.get('userEditingMessage'));
    }).property('processingMessage', 'userEditingMessage'),
    isEditing: (function() {
      var lockedBy;
      lockedBy = this.get('lockedBy');
      return lockedBy && lockedBy === this.currentUser;
    }).property('lockedBy'),
    canEdit: (function() {
      return !this.get('locked');
    }).property('locked'),
    defaultBody: 'Type your manuscript here',
    saveStateDidChange: (function() {
      if (this.get('saveState')) {
        return this.setProperties({
          saveStateMessage: "Saved",
          savedAt: new Date()
        });
      } else {
        return this.setProperties({
          saveStateMessage: null,
          savedAt: null
        });
      }
    }).observes('saveState'),
    actions: {
      tryHidingPlaceholder: function() {
        return this.get('visualEditor').startEditing();
      },
      toggleEditing: function() {
        if (this.get('lockedBy')) {
          this.set('body', this.get('visualEditor.bodyHtml'));
          this.set('lockedBy', null);
          this.send('stopEditing');
          return this.get('model').save().then((function(_this) {
            return function(paper) {
              return _this.set('saveState', true);
            };
          })(this));
        } else {
          this.set('lockedBy', this.currentUser);
          return this.get('model').save().then((function(_this) {
            return function(paper) {
              _this.send('startEditing');
              return _this.set('saveState', false);
            };
          })(this));
        }
      },
      savePaper: function() {
        if (!this.get('model.editable')) {
          return;
        }
        return this.get('model').save().then((function(_this) {
          return function(paper) {
            return _this.set('saveState', true);
          };
        })(this));
      },
      updateDocumentBody: function(content) {
        this.set('body', content);
        return false;
      },
      confirmSubmitPaper: function() {
        if (!this.get('allMetadataTasksCompleted')) {
          return;
        }
        this.get('model').save();
        this.get('controllers.overlays/paperSubmit').set('model', this.get('model'));
        return this.send('showConfirmSubmitOverlay');
      }
    }
  });

  exports['default'] = PaperEditController;

});