define('tahi/views/phase-header', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var PhaseHeaderView;

  PhaseHeaderView = Ember['default'].View.extend({
    templateName: 'phase_header',
    classNames: ['column-header'],
    classNameBindings: ['active'],
    active: false,
    previousContent: null,
    focusIn: function(e) {
      this.set('active', true);
      if ($(e.target).attr('contentEditable')) {
        return this.set('oldPhaseName', this.get('phase.name'));
      }
    },
    phaseNameDidChange: (function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, Utils['default'].resizeColumnHeaders);
    }).observes('phase.name'),
    currentHeaderHeight: Ember['default'].computed('phase.name', function() {
      return this.$().find('.column-title').height();
    }),
    input: function(e) {
      if (this.get('currentHeaderHeight') <= 58) {
        return this.set('previousContent', this.get('phase.name'));
      } else {
        return this.set('phase.name', this.get('previousContent'));
      }
    },
    actions: {
      save: function() {
        this.set('active', false);
        return this.get('controller').send('savePhase', this.get('phase'));
      },
      cancel: function() {
        this.set('active', false);
        this.get('controller').send('rollbackPhase', this.get('phase'), this.get('oldPhaseName'));
        return Ember['default'].run.scheduleOnce('afterRender', this, Utils['default'].resizeColumnHeaders);
      }
    }
  });

  exports['default'] = PhaseHeaderView;

});