define('tahi/controllers/overlays/paper-reviewer', ['exports', 'tahi/pods/task/controller', 'tahi/mixins/controllers/select-2-assignees'], function (exports, TaskController, Select2Assignees) {

  'use strict';

  var PaperReviewerOverlayController;

  PaperReviewerOverlayController = TaskController['default'].extend(Select2Assignees['default'], {
    select2RemoteUrl: Ember.computed('paper.journal.id', function() {
      return "/filtered_users/reviewers/" + (this.get('model.paper.journal.id')) + "/";
    }),
    actions: {
      addReviewer: function(select2User) {
        return this.store.find('user', select2User.id).then((function(_this) {
          return function(user) {
            _this.get('reviewers').addObject(user);
            return _this.send('saveModel');
          };
        })(this));
      },
      removeReviewer: function(select2User) {
        return this.store.find('user', select2User.id).then((function(_this) {
          return function(user) {
            _this.get('reviewers').removeObject(user);
            return _this.send('saveModel');
          };
        })(this));
      }
    }
  });

  exports['default'] = PaperReviewerOverlayController;

});