define('tahi/mixins/saves-questions-on-close', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var SavesQuestionsOnClose;

  SavesQuestionsOnClose = Ember['default'].Mixin.create({
    onClose: "saveAndClose",
    actions: {
      saveAndClose: function() {
        var records;
        records = this.get('model.questions').map(function(q) {
          if (q.get('isNew') || q.get('isDirty')) {
            return q.save();
          }
        });
        return Ember['default'].RSVP.all(records).then((function(_this) {
          return function() {
            return _this.get('model').save().then(function() {
              return _this.send('closeOverlay');
            });
          };
        })(this));
      }
    }
  });

  exports['default'] = SavesQuestionsOnClose;

});