define('tahi/tests/components/card-preview-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleForComponent('card-preview', 'Unit: components/card-preview', {
    needs: ['helper:badge-count']
  });

  ember_qunit.test("#unreadCommentsCount returns unread comments count", function() {
    var component, readCommentLook, task, unreadCommentLook;
    task = Ember['default'].Object.create({
      id: 99
    });
    readCommentLook = Ember['default'].Object.create({
      taskId: 99,
      readAt: "2015-09-01"
    });
    unreadCommentLook = Ember['default'].Object.create({
      taskId: 99,
      readAt: null
    });
    component = this.subject({
      task: task,
      commentLooks: [unreadCommentLook, readCommentLook]
    });
    return equal(component.get('unreadCommentsCount'), 1);
  });

  ember_qunit.test("#unreadCommentsCount gets updated when commentLook is read", function() {
    var component, readCommentLook, task, unreadCommentLook;
    task = Ember['default'].Object.create({
      id: 99
    });
    readCommentLook = Ember['default'].Object.create({
      taskId: 99,
      readAt: "2015-09-01"
    });
    unreadCommentLook = Ember['default'].Object.create({
      taskId: 99,
      readAt: null
    });
    component = this.subject({
      task: task,
      commentLooks: [unreadCommentLook, readCommentLook]
    });
    unreadCommentLook.set('readAt', '2015-09-01');
    return equal(component.get('unreadCommentsCount'), 0);
  });

});