define('tahi/pods/components/timed-message/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var TimedMessageComponent;

  TimedMessageComponent = Ember['default'].Component.extend({
    duration: 5000,
    messageDidChange: (function() {
      this.$().html(this.get('message'));
      return Ember['default'].run.later(this, function() {
        return this.set('message', '');
      }, this.get('duration'));
    }).observes('message')
  });

  exports['default'] = TimedMessageComponent;

});