define('tahi/pods/components/select-2-multiple/component', ['exports', 'tahi/pods/components/select-2/component'], function (exports, Select2Component) {

  'use strict';

  var Select2MultipleComponent;

  Select2MultipleComponent = Select2Component['default'].extend({
    multiSelect: true,
    setSelectedData: (function() {
      return this.$().select2('val', (this.get('selectedData') || []).mapProperty('id'));
    }).observes('selectedData')
  });

  exports['default'] = Select2MultipleComponent;

});