define('tahi/tests/addons/reporting-guidelines', function () {

  'use strict';

  module('Integration: Reporting Guidelines Card', {
    teardown: function() {
      ETahi.reset();
      return ETahi.paperEditActionStub.restore();
    },
    setup: function() {
      var ef, litePaper, paper, paperPayload, questionModifiedResponse, questionResponse, records, task, taskPayload, _;
      setupApp({
        integration: true
      });
      TahiTest.questionId = 553;
      questionResponse = {
        question: {
          _rootKey: "question",
          id: TahiTest.questionId,
          ident: "reporting_guidelines.systematic_reviews",
          question: "Systematic Reviews",
          answer: "false",
          additional_data: [{}],
          task_id: TahiTest.reportingGuidelinesId,
          question_attachment_id: null
        },
        question_attachments: []
      };
      questionModifiedResponse = {
        question: {
          id: TahiTest.questionId,
          ident: "reporting_guidelines.systematic_reviews",
          question: "Systematic Reviews",
          answer: "true",
          additional_data: [{}],
          task_id: TahiTest.reportingGuidelinesId,
          question_attachment_id: null
        },
        question_attachments: []
      };
      ef = ETahi.Factory;
      records = ETahi.Setups.paperWithTask("ReportingGuidelinesTask", {
        id: TahiTest.reportingGuidelinesId,
        question_ids: [TahiTest.questionId]
      });
      paper = records[0], task = records[1], _ = records[2], litePaper = records[3];
      paperPayload = ef.createPayload('paper');
      paperPayload.addRecords(records.concat(fakeUser, questionResponse.question));
      taskPayload = ef.createPayload('task');
      taskPayload.addRecords([task, litePaper]);
      server.respondWith('GET', "/papers/" + TahiTest.paperId, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(paperPayload.toJSON())
      ]);
      server.respondWith('GET', "/tasks/" + TahiTest.reportingGuidelinesId, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(taskPayload.toJSON())
      ]);
      return server.respondWith('PUT', /\/questions\/\d+/, [
        200, {
          "Content-Type": "application/json"
        }, JSON.stringify(questionModifiedResponse)
      ]);
    }
  });

  test('Supporting Guideline is a meta data card, contains the right questions and sub-questions', function() {
    var findQuestionLi;
    findQuestionLi = function(questionText) {
      return find('.question .item').filter(function(i, el) {
        return Em.$(el).find('label').text().trim() === questionText;
      });
    };
    visit("/papers/" + TahiTest.paperId + "/edit").then(function() {
      ok(exists(find('.card-content:contains("Reporting Guidelines")')));
      return ETahi.paperEditActionStub = sinon.stub(ETahi.__container__.lookup('controller:paperEdit')._actions, "savePaper");
    });
    click('.card-content:contains("Reporting Guidelines")').then(function() {
      var questionLi;
      equal(find('.question .item').length, 6);
      equal(find('h1').text(), 'Reporting Guidelines');
      questionLi = findQuestionLi('Systematic Reviews');
      return ok(exists(questionLi.find('.additional-data.hidden')));
    });
    return click('input[name="reporting_guidelines.systematic_reviews"]').then(function() {
      var additionalDataText, questionLi;
      questionLi = findQuestionLi('Systematic Reviews');
      ok(!(exists(questionLi.find('.additional-data.hidden'))));
      ok(exists(questionLi.find('.additional-data')));
      additionalDataText = questionLi.find('.additional-data').first().text().trim();
      ok(additionalDataText.indexOf('Select & upload') > -1);
      return ok(additionalDataText.indexOf('Provide a completed PRISMA checklist as supporting information.') > -1);
    });
  });

});