define('tahi/pods/admin/journal/manuscript-manager-template/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ManuscriptManagerTemplateRoute;

  ManuscriptManagerTemplateRoute = Ember['default'].Route.extend({
    actions: {
      chooseNewCardTypeOverlay: function(phaseTemplate) {
        var journalTaskTypes;
        journalTaskTypes = this.modelFor('admin.journal').get('journalTaskTypes');
        this.controllerFor('overlays/chooseNewCardType').setProperties({
          phaseTemplate: phaseTemplate,
          journalTaskTypes: journalTaskTypes
        });
        return this.render('overlays/add-manuscript-template-card', {
          into: 'application',
          outlet: 'overlay',
          controller: 'overlays/chooseNewCardType'
        });
      },
      addTaskType: function(phaseTemplate, taskType) {
        var newTask;
        newTask = this.store.createRecord('taskTemplate', {
          title: taskType.get('title'),
          journalTaskType: taskType,
          phaseTemplate: phaseTemplate,
          template: []
        });
        if (taskType.get('kind') === "Task") {
          this.controllerFor('overlays/adHocTemplate').setProperties({
            phaseTemplate: phaseTemplate,
            model: newTask,
            isNewTask: true
          });
          return this.render('overlays/adHocTemplate', {
            into: 'application',
            outlet: 'overlay',
            controller: 'overlays/adHocTemplate'
          });
        } else {
          return this.send('addTaskAndClose');
        }
      },
      addTaskAndClose: function() {
        this.controllerFor('admin.journal.manuscriptManagerTemplate/edit').set('dirty', true);
        return this.send('closeOverlay');
      },
      closeAction: function() {
        return this.send('closeOverlay');
      },
      viewCard: function() {},
      showDeleteConfirm: function(task) {
        return this.render('overlays/cardDelete', {
          into: 'application',
          outlet: 'overlay',
          controller: 'overlays/card-delete',
          model: task
        });
      }
    }
  });

  exports['default'] = ManuscriptManagerTemplateRoute;

});