define('tahi/routes/authorized', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var AuthorizedRoute;

  AuthorizedRoute = Ember['default'].Route.extend({
    handleUnauthorizedRequest: function(transition) {
      transition.abort();
      return this.transitionTo('dashboard');
    },
    actions: {
      error: function(response, transition) {
        console.log(response);
        switch (response.status) {
          case 403:
            this.handleUnauthorizedRequest(transition);
        }
        console.log("Error in transition to " + transition.targetName);
        return true;
      }
    }
  });

  exports['default'] = AuthorizedRoute;

});