define('tahi/tests/helpers/chosen-helpers', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var chosenHelpers;

  chosenHelpers = (function() {
    return Ember['default'].Test.registerAsyncHelper('pickFromChosenSingle', function(app, selector, choice) {
      click(".chosen-container" + selector + " a.chosen-single");
      return click("li.active-result:contains('" + choice + "')");
    });
  })();

  exports['default'] = chosenHelpers;

});