define('tahi/mixins/controllers/controller-participants', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ControllerParticipants;

  ControllerParticipants = Ember['default'].Mixin.create({
    needs: ['application'],
    currentUser: Ember['default'].computed.alias('controllers.application.currentUser'),
    participations: [],
    participants: (function() {
      return this.get('participations').mapBy('user');
    }).property('participations.@each.user'),
    createParticipant: function(newParticipant) {
      if (newParticipant && !this.get('participants').contains(newParticipant)) {
        return this.store.createRecord('participation', {
          user: newParticipant,
          task: this.get('model')
        });
      }
    },
    findParticipation: function(participantId) {
      return this.get('participations').findBy("user.id", participantId);
    },
    actions: {
      saveNewParticipant: function(newParticipantId) {
        return this.store.find('user', newParticipantId).then((function(_this) {
          return function(user) {
            var part;
            if (part = _this.createParticipant(user)) {
              if (!_this.get('model.isNew')) {
                return part.save();
              }
            }
          };
        })(this));
      },
      removeParticipant: function(participantId) {
        var part;
        if (part = this.findParticipation("" + participantId)) {
          part.deleteRecord();
          if (!this.get('model.isNew')) {
            return part.save();
          }
        }
      }
    }
  });

  exports['default'] = ControllerParticipants;

});