define('tahi/controllers/overlays/register-decision', ['exports', 'tahi/pods/task/controller'], function (exports, TaskController) {

  'use strict';

  var RegisterDecisionOverlayController;

  RegisterDecisionOverlayController = TaskController['default'].extend({
    actions: {
      setDecisionTemplate: function(decision) {
        this.setProperties({
          'model.paperDecisionLetter': this.get("model." + decision + "LetterTemplate"),
          'model.paperDecision': decision,
          'isSavingData': true
        });
        return this.get('model').save().then((function(_this) {
          return function() {
            return _this.set('isSavingData', false);
          };
        })(this));
      }
    }
  });

  exports['default'] = RegisterDecisionOverlayController;

});