define('tahi/router', ['exports', 'ember', './config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router;

  Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function() {
    this.route('dashboard', {
      path: '/'
    });
    this.route('flow_manager');
    this.resource('paper', {
      path: '/papers/*paper_id'
    }, function() {
      this.route('edit');
      return this.route('manage');
    });
    this.route('task', {
      path: '/papers/:paper_id/tasks/:task_id'
    });
    this.route('profile', {
      path: '/profile'
    });
    this.resource('affiliation');
    this.resource('author');
    this.resource('admin', function() {
      this.resource('journal_user', {
        path: '/journal_users/:journal_id'
      });
      return this.resource('admin.journal', {
        path: '/journals/:journal_id'
      }, function() {
        this.resource('admin.journal.manuscript_manager_template', {
          path: '/manuscript_manager_templates'
        }, function() {
          this.route('new');
          return this.route('edit', {
            path: '/:manuscript_manager_template_id/edit'
          });
        });
        return this.route('flow_manager', {
          path: '/roles/:role_id/flow_manager'
        });
      });
    });
    return this.route('styleguide');
  });

  exports['default'] = Router;

});