define('tahi/services/document-download', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var DocumentDownloadService;

  DocumentDownloadService = Ember['default'].Namespace.create({
    initiate: function(paperId, downloadFormat) {
      this.paperId = paperId;
      this.downloadFormat = downloadFormat;
      return Ember['default'].$.ajax({
        url: "/papers/" + this.paperId + "/export",
        data: {
          format: this.downloadFormat
        },
        success: (function(_this) {
          return function(data) {
            var jobId;
            jobId = data['job']['id'];
            return _this.checkJobState(jobId);
          };
        })(this),
        error: function(data) {
          throw new Error("Could not download " + this.downloadFormat);
        }
      });
    },
    checkJobState: function(jobId) {
      var status;
      status = "";
      this.timeout = 2000;
      return Ember['default'].$.ajax({
        url: "/papers/" + this.paperId + "/status/" + jobId,
        success: (function(_this) {
          return function(data) {
            var job;
            job = data['job'];
            if (job.state === "converted") {
              return Utils['default'].windowLocation(job.url);
            } else if (job.state === "errored") {
              return alert("The download failed");
            } else {
              return setTimeout((function() {
                return _this.checkJobState(jobId);
              }), _this.timeout);
            }
          };
        })(this)
      });
    }
  });

  exports['default'] = DocumentDownloadService;

});