define('tahi/pods/admin/journal/manuscript-manager-template/edit/view', ['exports', 'ember', 'tahi/services/utils'], function (exports, Ember, Utils) {

  'use strict';

  var ManuscriptManagerTemplateEditView;

  ManuscriptManagerTemplateEditView = Ember['default'].View.extend({
    setupColumnResizing: (function() {
      return Ember['default'].run.scheduleOnce('afterRender', this, Utils['default'].resizeColumnHeaders);
    }).on('didInsertElement').observes('controller.editMode')
  });

  exports['default'] = ManuscriptManagerTemplateEditView;

});