define('tahi/serializers/dashboard', ['exports', 'tahi/serializers/application', 'tahi/mixins/serializers/serializes-has-many'], function (exports, ApplicationSerializer, SerializesHasMany) {

  'use strict';

  var DashboardSerializer;

  DashboardSerializer = ApplicationSerializer['default'].extend(SerializesHasMany['default'], {
    normalizeHash: {
      tasks: function(hash) {
        hash.qualified_type = hash.type;
        return hash.type = hash.type.replace(/.+::/, '');
      }
    }
  });

  exports['default'] = DashboardSerializer;

});