define('tahi/pods/components/question-uploader/component', ['exports', 'tahi/pods/components/question/component', 'tahi/models/file-upload'], function (exports, QuestionComponent, FileUpload) {

  'use strict';

  var QuestionUploaderComponent;

  QuestionUploaderComponent = QuestionComponent['default'].extend({
    fileUpload: null,
    actions: {
      uploadStarted: function(data) {
        return this.set('fileUpload', FileUpload['default'].create({
          file: data.files[0]
        }));
      },
      uploadProgress: function(data) {
        return this.get('fileUpload').setProperties({
          dataLoaded: data.loaded,
          dataTotal: data.total
        });
      },
      uploadFinished: function(data) {
        this.set('model.url', data);
        return this.get('model').save().then((function(_this) {
          return function() {
            return _this.set('fileUpload', null);
          };
        })(this));
      },
      destroyAttachment: function(attachment) {
        return this.sendAction('destroyAttachment', attachment);
      }
    }
  });

  exports['default'] = QuestionUploaderComponent;

});