define('tahi/pods/admin/index/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var AdminIndexController;

  AdminIndexController = Ember['default'].ArrayController.extend(Ember['default'].PromiseProxyMixin, {
    needs: ['application'],
    sortProperties: ['createdAt'],
    sortAscending: false,
    placeholderText: "Need to find a user?<br> Search for them here.",
    isCurrentUserAdmin: Ember['default'].computed.alias('controllers.application.currentUser.siteAdmin'),
    newJournalPresent: (function() {
      return this.get('arrangedContent').any(function(a) {
        return a.get('isNew');
      });
    }).property('arrangedContent.@each.isNew'),
    resetSearch: function() {
      this.set('adminJournalUsers', null);
      return this.set('placeholderText', null);
    },
    actions: {
      addNewJournal: function() {
        if (this.get('newJournalPresent')) {
          return;
        }
        return this.store.createRecord('adminJournal', {
          createdAt: new Date
        });
      },
      searchUsers: function() {
        this.resetSearch();
        return this.store.find('AdminJournalUser', {
          query: this.get('searchQuery')
        }).then((function(_this) {
          return function(users) {
            _this.set('adminJournalUsers', users);
            if (Ember['default'].isEmpty(_this.get('adminJournalUsers'))) {
              return _this.set('placeholderText', "No matching users found");
            }
          };
        })(this));
      }
    }
  });

  exports['default'] = AdminIndexController;

});