define('tahi/models/flow', ['exports', 'ember-data', 'tahi/mixins/models/common-flow-attrs'], function (exports, DS, CommonFlowAttrs) {

  'use strict';

  var Flow;

  Flow = DS['default'].Model.extend(CommonFlowAttrs['default'], {
    role: DS['default'].belongsTo('role'),
    position: DS['default'].attr('number'),
    query: DS['default'].attr(),
    relationshipsToSerialize: ['role']
  });

  exports['default'] = Flow;

});