define('tahi/tests/controllers/ad-hoc-overlay-test', ['ember', 'ember-qunit', 'tahi/services/rest-less'], function (Ember, ember_qunit, RESTless) {

  'use strict';

  ember_qunit.moduleFor('controller:overlays/ad-hoc', 'AdHocOverlayController', {
    needs: ['controller:application', 'controller:task'],
    teardown: function() {
      return RESTless['default'].putModel.restore();
    }
  });

  ember_qunit.test('sendEmail calls send_message endpoint', function() {
    sinon.stub(RESTless['default'], 'putModel');
    this.subject().send('sendEmail', 'Foo');
    return ok(RESTless['default'].putModel.getCall(0).args[2].task === 'Foo');
  });

});