define('tahi/controllers/overlays/ad-hoc-template', ['exports', 'ember', 'tahi/mixins/controllers/builds-task-template'], function (exports, Ember, BuildsTaskTemplate) {

  'use strict';

  var AdHocTemplateOverlayController;

  AdHocTemplateOverlayController = Ember['default'].ObjectController.extend(BuildsTaskTemplate['default'], {
    isNewTask: false,
    blocks: Ember['default'].computed.alias('template'),
    phaseTemplate: null,
    actions: {
      closeAction: function() {
        return this.send('addTaskAndClose');
      }
    }
  });

  exports['default'] = AdHocTemplateOverlayController;

});