define('tahi/models/paper', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Paper, a;

  a = DS['default'].attr;

  Paper = DS['default'].Model.extend({
    authors: DS['default'].hasMany('author'),
    collaborations: DS['default'].hasMany('collaboration'),
    editors: DS['default'].hasMany('user'),
    figures: DS['default'].hasMany('figure', {
      inverse: 'paper'
    }),
    journal: DS['default'].belongsTo('journal'),
    lockedBy: DS['default'].belongsTo('user'),
    phases: DS['default'].hasMany('phase'),
    reviewers: DS['default'].hasMany('user'),
    supportingInformationFiles: DS['default'].hasMany('supporting-information-file'),
    tasks: DS['default'].hasMany('task', {
      async: true,
      polymorphic: true
    }),
    body: a('string'),
    doi: a('string'),
    shortTitle: a('string'),
    submitted: a('boolean'),
    status: a('string'),
    title: a('string'),
    paperType: a('string'),
    eventName: a('string'),
    strikingImageId: a('string'),
    editable: a('boolean'),
    relationshipsToSerialize: [],
    displayTitle: (function() {
      return this.get('title') || this.get('shortTitle');
    }).property('title', 'shortTitle'),
    allMetadataTasks: (function() {
      return this.get('tasks').filterBy('isMetadataTask');
    }).property('tasks.content.@each.isMetadataTask'),
    collaborators: (function() {
      return this.get('collaborations').mapBy('user');
    }).property('collaborations.@each'),
    allMetadataTasksCompleted: (function() {
      return this.get('allMetadataTasks').everyProperty('completed', true);
    }).property('allMetadataTasks.@each.completed'),
    unloadRecord: function() {
      var litePaper;
      litePaper = this.store.getById('lite-paper', this.get('id'));
      if (litePaper) {
        this.store.unloadRecord(litePaper);
      }
      return this._super();
    }
  });

  exports['default'] = Paper;

});