define('tahi/mixins/views/redirects-if-editable', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var RedirectsIfEditable;

  RedirectsIfEditable = Em.Mixin.create({
    editable: Ember['default'].computed.alias('controller.model.editable'),
    supportedDownloadFormats: Ember['default'].computed.alias('controller.supportedDownloadFormats'),
    toggleEditable: function() {
      if (this.get('editable') !== this.get('lastEditable')) {
        this.set('lastEditable', this.get('editable'));
        return this.get('controller').send('editableDidChange');
      }
    },
    setupEditableToggle: (function() {
      this.set('lastEditable', this.get('editable'));
      return this.addObserver('editable', this, this.toggleEditable);
    }).on('didInsertElement'),
    teardownEditableToggle: (function() {
      return this.removeObserver('editable', this, this.toggleEditable);
    }).on('willDestroyElement')
  });

  exports['default'] = RedirectsIfEditable;

});