define('tahi/tests/controllers/manuscript-manager-template-edit-test', ['ember', '../helpers/start-app', 'ember-qunit'], function (Ember, startApp, ember_qunit) {

  'use strict';

  ember_qunit.moduleFor('controller:admin/journal/manuscript-manager-template/edit', 'ManuscriptManagerTemplateEditController', {
    setup: function() {
      startApp['default']();
      return Ember['default'].run((function(_this) {
        return function() {
          _this.ctrl = _this.subject();
          _this.store = getStore();
          _this.phase = _this.store.createRecord('phaseTemplate', {
            name: 'First Phase'
          });
          _this.task1 = _this.store.createRecord('taskTemplate', {
            title: 'ATask',
            phaseTemplate: _this.phase
          });
          _this.task2 = _this.store.createRecord('taskTemplate', {
            title: 'BTask',
            phaseTemplate: _this.phase
          });
          _this.template = _this.store.createRecord('manuscriptManagerTemplate', {
            name: 'A name',
            paper_type: 'A type',
            phases: [_this.phase]
          });
          return _this.ctrl.setProperties({
            model: _this.template,
            store: _this.store
          });
        };
      })(this));
    }
  });

  ember_qunit.test('#rollbackPhase sets the given old name on the given phase', function() {
    var phase;
    phase = Ember['default'].Object.create({
      name: "Captain Picard"
    });
    this.ctrl.send('rollbackPhase', phase, "Captain Kirk");
    return equal(phase.get('name'), "Captain Kirk");
  });

  ember_qunit.test('#addPhase adds a phase at a specified index', function() {
    return Em.run((function(_this) {
      return function() {
        _this.ctrl.send('addPhase', 0);
        return equal(_this.ctrl.get('sortedPhaseTemplates.firstObject.name'), 'New Phase');
      };
    })(this));
  });

  ember_qunit.test("#removeTask removes the given task from the template's phase", function() {
    return Ember['default'].run((function(_this) {
      return function() {
        var tasks;
        _this.ctrl.send('removeTask', _this.task1);
        tasks = _this.phase.get('taskTemplates');
        return deepEqual(tasks.mapBy('title'), ['BTask']);
      };
    })(this));
  });

});