define('tahi/tests/integration/download-docx-test', ['ember', 'ember-qunit', '../helpers/start-app', '../helpers/setups', '../helpers/mock-server', '../helpers/factory', 'tahi/services/utils'], function (Ember, ember_qunit, startApp, setups, setupMockServer, Factory, Utils) {

  'use strict';

  var app, currentPaper, exportUrl, fakeUser, server;

  app = null;

  server = null;

  currentPaper = null;

  fakeUser = null;

  exportUrl = null;

  module('Integration: Paper Docx Download', {
    teardown: function() {
      server.restore();
      return Ember['default'].run(app, app.destroy);
    },
    setup: function() {
      app = startApp['default']();
      server = setupMockServer['default']();
      return fakeUser = window.currentUser.user;
    }
  });

  ember_qunit.test('show download links on control bar', function() {
    var jobId, mock, paperPayload, paperResponse, records;
    records = setups.paperWithTask('Task', {
      id: 1,
      title: "Metadata",
      isMetadataTask: true,
      completed: true
    });
    currentPaper = records[0];
    paperPayload = Factory['default'].createPayload('paper');
    paperPayload.addRecords(records.concat([fakeUser]));
    paperResponse = paperPayload.toJSON();
    jobId = '232134-324-1234-1234';
    exportUrl = "/papers/" + currentPaper.id + "/export?format=docx";
    server.respondWith('GET', "/papers/" + currentPaper.id, [
      200, {
        "Content-Type": "application/json"
      }, JSON.stringify(paperResponse)
    ]);
    server.respondWith('GET', exportUrl, [
      200, {
        "Content-Type": "application/json"
      }, JSON.stringify({
        job: {
          id: "" + jobId
        }
      })
    ]);
    server.respondWith('GET', "/papers/" + currentPaper.id + "/status/" + jobId, [
      200, {
        "Content-Type": "application/json"
      }, JSON.stringify({
        "job": {
          "state": "converted",
          "id": "" + jobId,
          "url": 'https://www.google.com'
        }
      })
    ]);
    mock = void 0;
    visit("/papers/" + currentPaper.id + "/edit");
    andThen(function() {
      mock = sinon.mock(Utils['default']);
      mock.expects("windowLocation").withArgs("https://www.google.com").returns(true);
      equal(find("div.downloads-link div.control-bar-link-icon").length, 1);
      ok(click("div.downloads-link div.control-bar-link-icon"));
      equal(find("div.manuscript-download-links.active").length, 1);
      equal(find('a.docx').length, 1);
      equal(find('a.docx').attr('title'), 'Download docx');
      return click('a.docx');
    });
    return andThen(function() {
      ok(_.findWhere(server.requests, {
        method: 'GET',
        url: exportUrl
      }));
      return mock.restore();
    });
  });

});