define('tahi/pods/flow-manager/route', ['exports', 'tahi/routes/authorized', 'tahi/services/rest-less'], function (exports, AuthorizedRoute, RESTless) {

  'use strict';

  var FlowManagerRoute;

  FlowManagerRoute = AuthorizedRoute['default'].extend({
    beforeModel: function(transition) {
      if (!this.currentUser) {
        return this.handleUnauthorizedRequest(transition);
      }
    },
    model: function() {
      var cachedModel;
      if (cachedModel = this.controllerFor('application').get('cachedModel')) {
        this.controllerFor('application').set('cachedModel', null);
        return cachedModel;
      } else {
        return this.store.find('userFlow');
      }
    },
    afterModel: function() {
      return this.store.find('commentLook');
    },
    setupController: function(controller, model) {
      controller.set('model', model);
      controller.set('commentLooks', this.store.all('commentLook'));
      return controller.set('journalTaskTypes', this.store.all('journalTaskType'));
    },
    actions: {
      chooseNewFlowManagerColumn: function() {
        var controller;
        controller = this.controllerFor('overlays/chooseNewFlowManagerColumn');
        controller.set('isLoading', true);
        RESTless['default'].get('/user_flows/potential_flows').then(function(data) {
          controller.set('isLoading', false);
          return controller.set('flows', data.flows);
        });
        return this.render('overlays/chooseNewFlowManagerColumn', {
          into: 'application',
          outlet: 'overlay',
          controller: controller
        });
      },
      removeFlow: function(flow) {
        return flow.destroyRecord();
      },
      saveFlow: function(flow) {
        return flow.save();
      },
      viewCard: function(task) {
        var paperId, redirectParams;
        paperId = task.get('litePaper.id');
        redirectParams = ['flow_manager'];
        this.controllerFor('application').get('overlayRedirect').pushObject(redirectParams);
        this.controllerFor('application').set('cachedModel', this.modelFor('flow_manager'));
        this.controllerFor('application').set('overlayBackground', 'flow_manager');
        return this.transitionTo('task', paperId, task.get('id'));
      }
    }
  });

  exports['default'] = FlowManagerRoute;

});